<template>
  <div ref="popUpRef" class="search-popup" @click="clicked">
    {{ item.computedResult.sortedPrices[0] | currency }}
  </div>
</template>
<script>
export default {
  props: {
    google: {
      type: Object,
      required: true
    },
    map: {
      type: Object,
      required: true
    },
    item: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      popup: '',
      Popup: ''
    };
  },
  computed: {},
  methods: {
    clicked() {
      this.$emit('click');
    },
    definePopupClass() {
      // /**
      //  * A customized popup on the map.
      //  * @param {!this.google.maps.LatLng} position
      //  * @param {!Element} content
      //  * @constructor
      //  * @extends {this.google.maps.OverlayView}
      //  */
      this.Popup = function(position, content) {
        this.position = position;

        content.classList.add('popup-bubble-content');

        var pixelOffset = document.createElement('div');
        pixelOffset.classList.add('popup-bubble-anchor');
        pixelOffset.appendChild(content);

        this.anchor = document.createElement('div');
        this.anchor.classList.add('popup-tip-anchor');
        this.anchor.appendChild(pixelOffset);

        // Optionally stop clicks, etc., from bubbling up to the map.
        // this.stopEventPropagation();
      };
      // NOTE: google.maps.OverlayView is only defined once the Maps API has
      // loaded. That is why Popup is defined inside initMap().
      this.Popup.prototype = Object.create(this.google.maps.OverlayView.prototype);

      /** Called when the popup is added to the map. */
      this.Popup.prototype.onAdd = function() {
        this.getPanes().floatPane.appendChild(this.anchor);
      };

      /** Called when the popup is removed from the map. */
      this.Popup.prototype.onRemove = function() {
        if (this.anchor.parentElement) {
          this.anchor.parentElement.removeChild(this.anchor);
        }
      };

      /** Called when the popup needs to draw itself. */
      this.Popup.prototype.draw = function() {
        var divPosition = this.getProjection().fromLatLngToDivPixel(this.position);
        // Hide the popup when it is far out of view.
        var display =
          Math.abs(divPosition.x) < 4000 && Math.abs(divPosition.y) < 4000 ? 'block' : 'none';

        if (display === 'block') {
          this.anchor.style.left = divPosition.x + 'px';
          this.anchor.style.top = divPosition.y + 'px';
        }
        if (this.anchor.style.display !== display) {
          this.anchor.style.display = display;
        }
      };

      /** Stops clicks/drags from bubbling up to the map. */
      // this.Popup.prototype.stopEventPropagation = function() {
      //   var anchor = this.anchor;
      //   anchor.style.cursor = 'auto';

      //   [
      //     'click',
      //     'dblclick',
      //     'contextmenu',
      //     'wheel',
      //     'mousedown',
      //     'touchstart',
      //     'pointerdown'
      //   ].forEach(function(event) {
      //     anchor.addEventListener(event, function(e) {
      //       e.stopPropagation();
      //     });
      //   });
      // };
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.definePopupClass();
      this.popup = new this.Popup(
        new this.google.maps.LatLng(this.item.info.LatLng.lat, this.item.info.LatLng.lng),
        this.$refs.popUpRef
      );
      // this.popup.addListener('click', () => {
      //   console.log('click');
      // });
      this.popup.setMap(this.map);
    });
  },
  beforeDestroy() {
    this.popup.onRemove();
  }
};
</script>

<style lang="scss">
.google-map {
  /* The location pointed to by the popup tip. */
  .popup-tip-anchor {
    height: 0;
    position: absolute;
    /* The max width of the info window. */
    width: 200px;
  }
  /* The bubble is anchored above the tip. */
  .popup-bubble-anchor {
    position: absolute;
    width: 100%;
    bottom: /* TIP_HEIGHT= */ 8px;
    left: 0;
  }
  /* Draw the tip. */
  .popup-bubble-anchor::after {
    // display: none;
    content: '';
    position: absolute;
    top: -1px;
    left: 0;
    /* Center the tip horizontally. */
    transform: translate(-50%, 0);
    /* The tip is a https://css-tricks.com/snippets/css/css-triangle/ */
    width: 0;
    height: 0;
    /* The tip is 8px high, and 12px wide. */
    // border-left: 6px solid transparent;
    // border-right: 6px solid transparent;
    // border-top: /* TIP_HEIGHT= */ 8px solid white;

    width: 1px;
    height: 8px;
    background-color: black;
    border-radius: 13px;
    box-shadow: 0px 2px 7px 0px rgba(0, 0, 0, 0.35);
  }
  /* The popup bubble itself. */
  .popup-bubble-content {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-50%, -100%);
    /* Style the info window. */
    background-color: white;
    padding: 5px 10px;
    border-radius: 15px;
    font-size: 14px;
    color: rgb(34, 34, 34);
    font-weight: 800;
    white-space: nowrap;
    justify-content: center;
    overflow-y: auto;
    max-height: 60px;
    box-shadow: rgba(0, 0, 0, 0.08) 0px 0px 0px 1px, rgba(0, 0, 0, 0.18) 0px 1px 2px;
    cursor: pointer;
    transition: all cubic-bezier(0.075, 0.82, 0.165, 0.25) 0.15s;
    border: 1px solid rgba(0, 0, 0, 0);
  }
  .popup-bubble-content:hover,
  .hover {
    z-index: 3;
    transform: translate(-50%, -100%) scale(1.07);
  }
  .popup-bubble-content.hover {
    background-color: black;
    color: #fff;
  }
  .selected {
    z-index: 2;
    background-color: black;
    color: #fff;
    transform: translate(-50%, -100%) scale(1.07);
  }
}
</style>
