<template>
  <div class="search-bar-container">
    <CustomAutocomplete
      ref="searchRef"
      :placeholder="
        large
          ? _windowSize.md.larger
            ? $t('homePage.searchBar.location_long')
            : $t('homePage.searchBar.location_short')
          : _windowSize.lg.larger
          ? $t('homePage.searchBar.location_long')
          : $t('homePage.searchBar.location_short')
      "
      @blur="handleBlur"
      @select="handleSelect"
      @input="handleInput"
      @KeyEnter="handleKeyEnter"
      v-model="query"
      placement="bottom"
      :noData="noData"
      :loading.sync="suggestionLoading"
      :fetch-suggestions="querySearchAsync"
      :suggestions.sync="suggestions"
      :popper-append-to-body="false"
      class="search-bar-group"
      :class="{
        'input-active': activeInput,
        'btn-active': !activeInput,
        'large-search-bar': large
      }"
    >
      <el-button
        v-if="!activeInput"
        slot="append"
        @click="changeInput(true)"
        class="input-btn"
        :class="{ notFill: !searchByMap || $route.name == 'homePage' }"
        >{{ tempSelectLocation ? tempSelectLocation.value : searchBarTitle }}</el-button
      >
      <el-tooltip
        slot="suffix"
        class="tipBtn"
        effect="dark"
        :content="$t('homePage.searchBar.tipTooltip')"
        placement="bottom"
        :disabled="!activeEnterTip"
        :class="{ tipActive: activeEnterTip }"
      >
        <span @click="handleTipClick">
          <i v-if="suggestionLoading" class="el-icon-loading"></i>
          {{ suggestionLoading ? '' : '' }}
        </span>
      </el-tooltip>
      <!-- <span v-if="activeEnterTip" slot="suffix"></span> -->
    </CustomAutocomplete>

    <el-date-picker
      ref="datePicker"
      style="display:none"
      v-model="computedDate"
      popper-class="date-pick-full-page"
      type="daterange"
      size="small"
      align="center"
      range-separator="-"
      value-format="yyyy/M/d"
      :start-placeholder="$t('homePage.searchBar.checkInDates')"
      :end-placeholder="$t('homePage.searchBar.checkOutDates')"
      :picker-options="datePickerOptions"
    >
    </el-date-picker>
    <!-- guest number -->
    <div class="search-btn">
      <el-popover
        slot="append"
        placement="bottom"
        width="250"
        trigger="click"
        class="ppl-popover-wrapper"
        popper-class="ppl-popover"
      >
        <div class="input-wrapper">
          <span class="label">{{ $t('homePage.searchBar.peopleNum_short') }}</span>
          <el-input-number v-model="pplNum" size="mini" :min="0"></el-input-number>
        </div>
        <el-button class="ppl-popover-btn" :class="{ notFill: !pplNum }" slot="reference">{{
          pplNum
            ? pplNum +
              (pplNum > 1
                ? $t('homePage.searchBar.peopleUnits')
                : $t('homePage.searchBar.peopleUnit'))
            : $t('homePage.searchBar.peopleNum')
        }}</el-button>
      </el-popover>
    </div>
    <!-- date picker -->
    <div class="search-btn">
      <el-button
        @click="handleDatePickerClick()"
        class="ppl-popover-btn"
        :class="{ notFill: !computedDate }"
        slot="reference"
        >{{
          displayDate ? displayDate[0] + ' - ' + displayDate[1] : $t('homePage.searchBar.addDates')
        }}</el-button
      >
    </div>
    <!-- search -->
    <div class="search-btn-wrapper">
      <el-button
        slot="append"
        :class="{ ready: selectLocation.mapInfo }"
        @click="selectLocation.mapInfo ? handleSearch() : leadToInput()"
      >
        <template>
          <span
            class="search-btn-inner"
            :style="{
              backgroundPosition: `left ${btnBgPosition.x}px top ${btnBgPosition.y}px`
            }"
            @mouseenter="handleSearchBtnHover"
            @mousemove="handleSearchBtnHover"
          >
            <span> <i class="el-icon-search"></i> {{ $t('homePage.searchBar.search') }}</span>
          </span>
        </template>
      </el-button>
    </div>

    <button @click="handleDatePickerClose" v-if="datePickBtnShow" class="date-picker-close-btn">
      <i class="el-icon-close"></i>
    </button>
    <!-- <button class="date-picker-close-btn">
        <i class="el-icon-close"></i>
      </button> -->
  </div>
</template>

<script>
import CustomAutocomplete from '@/components/general/customAutocomplete/CustomAutocomplete';
import { mapState } from 'vuex';
import searchMixin from '@/mixins/searchMixin';
export default {
  components: { CustomAutocomplete },
  mixins: [searchMixin],
  props: {
    large: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      noData: false,
      suggestions: [],
      resultArray: [],
      google: null,
      fakePplNum: null,
      options: [],
      //bind input
      query: null,
      //
      searchVal: null,
      activeInput: false,
      suggestionLoading: false,
      activeEnterTip: false,
      selectLocation: {
        value: null
      },
      selectLocationInit: {
        value: null
      },
      viewportContainsCenter: true,
      datePickerOptions: {
        disabledDate(date) {
          let today = new Date();
          today.setDate(today.getDate() - 1);
          return date < today;
        }
      },
      btnBgPosition: {
        x: null,
        y: null
      },
      closeBtn: null,
      tempSelectLocation: null
    };
  },
  computed: {
    ...mapState('UIModules', ['_windowSize', 'datePickBtnShow'])
  },
  watch: {
    computedDate(newVal) {
      if (newVal) {
        this.$store.commit('UIModules/SET_DATEPICKER_CLOSE_BUTTON', false);
        window.removeEventListener('click', this.addDatePickerListener);
      }
    }
  },
  methods: {
    handleSearchBtnHover(e) {
      this.btnBgPosition.x = e.offsetX - e.target.offsetWidth * 1.5;
      this.btnBgPosition.y = e.offsetY - e.target.offsetHeight * 1.5;
    }
  },
  beforeDestroy() {
    this.$store.commit('UIModules/SET_DATEPICKER_CLOSE_BUTTON', false);
  }
};
</script>
