import Vue from 'vue';
import VueRouter from 'vue-router';
import Dashboard from '@/views/backstage/Dashboard';
import Products from '@/views/backstage/Products';
import RoomManagement from '@/views/backstage/RoomManagement';
import CreateProduct from '@/views/backstage/CreateProduct';
import ClientPage from '@/views/forestage/ClientPage';
import HomePage from '@/views/forestage/HomePage';
import MapMode from '@/views/forestage/MapMode';

Vue.use(VueRouter);

const routes = [
  {
    path: '*',
    redirect: { name: 'homePage' }
  },
  {
    path: '/admin',
    component: Dashboard,
    children: [
      {
        path: '',
        name: 'ProductsHomePage',
        component: Products,
        meta: { requiresAuth: true }
      },
      {
        path: 'products',
        name: 'Products',
        component: Products,
        meta: { requiresAuth: true }
      },
      {
        path: 'create',
        name: 'Create',
        component: CreateProduct,
        meta: { requiresAuth: true }
      },
      {
        path: 'edit',
        name: 'Edit',
        component: CreateProduct,
        meta: { requiresAuth: true }
      },
      {
        path: 'management',
        name: 'RoomManagement',
        component: RoomManagement,
        meta: { requiresAuth: true }
      }
    ]
  },
  {
    path: '/',
    component: ClientPage,
    children: [
      {
        path: '',
        name: 'homePage',
        component: HomePage
      },
      {
        path: 'search',
        name: 'search',
        component: MapMode,
        meta: {
          title: 'search'
        }
      },
      {
        path: 'wishlist',
        name: 'wishList',
        component: MapMode,
        meta: {
          title: 'wishList'
        }
      }
    ]
  }
];

const router = new VueRouter({
  routes
});

export default router;
