<template>
  <el-dialog
    class="preview-dialog"
    :lock-scroll="true"
    :fullscreen="true"
    :visible="visible"
    width="100%"
    :center="true"
    @close="closeDialog"
    :modal="false"
  >
    <VuePannellum
      :src.sync="selectedPano.data.image.url"
      :preview="selectedPano.data.display_url"
      :showFullscreen="true"
      :autoRotate="false"
      :panoList="src"
      style="height: 100vh;"
    >
      <transition :duration="1200" name="el-zoom-in-bottom">
        <div v-if="showList" class="pano-list">
          <ul ref="listRef" class="list-ul">
            <template v-for="pano in src">
              <li
                class="pano-wrapper"
                :class="{ selected: selectedPano === pano }"
                @click="handleSelect(pano)"
                :key="'pano-' + pano.id"
              >
                <img :src="pano.data.display_url" />
              </li>
            </template>
          </ul>
          <span
            v-if="listSrollLeft > 0"
            @mousedown="handleListBtn(-1, true)"
            @mouseup="handleListBtn(0, false)"
            class="list-scroll-btn left-btn"
          >
            <i class="fas fa-angle-left"></i>
          </span>
          <span
            v-if="listSrollRight < scrollWidth"
            @mousedown="handleListBtn(1, true)"
            @mouseup="handleListBtn(0, false)"
            class="list-scroll-btn right-btn"
          >
            <i class="fas fa-angle-right"></i>
          </span>
        </div>
      </transition>
      <span @click="handleCollapseList" class="show-list-btn" :class="{ 'list-show': !showList }"
        ><i class="fas fa-chevron-down"></i>
      </span>
    </VuePannellum>
  </el-dialog>
</template>

<script>
import VuePannellum from '@/components/general/vue-pannellum';
export default {
  props: {
    src: Array,
    visible: Boolean
  },
  components: { VuePannellum },
  data() {
    return {
      selectedPano: null,
      showList: false,
      listWidth: null,
      scrollWidth: null,
      listSrollLeft: 0,
      scrollTimer: null
    };
  },
  computed: {
    listSrollRight() {
      return this.listSrollLeft + this.listWidth;
    }
  },
  methods: {
    closeDialog() {
      this.$emit('closeDialog');
    },
    handleSelect(pano) {
      this.selectedPano = pano;
    },
    handleCollapseList() {
      this.showList = !this.showList;
      this.$nextTick(() => {
        this.calcListWidth();
        this.handleScrollEvent();
      });
    },
    handleScrollEvent() {
      if (this.$refs.listRef) {
        //listscroll
        this.$refs.listRef.addEventListener('scroll', () => {
          this.listSrollLeft = this.$refs.listRef.scrollLeft;
        });
      }
    },
    calcListWidth() {
      if (this.$refs.listRef) {
        this.scrollWidth = this.$refs.listRef.scrollWidth;
        this.listWidth = this.$refs.listRef.clientWidth;
        this.listSrollLeft = this.$refs.listRef.scrollLeft;
        // this.listSrollRight = this.listSrollLeft + this.listWidth;
      }
    },
    handleListBtn(value, excuteTimer) {
      if (excuteTimer) {
        this.$refs.listRef.scrollLeft += value * 50;
        this.listSrollLeft = this.$refs.listRef.scrollLeft;
        this.scrollTimer = setInterval(() => {
          this.$refs.listRef.scrollLeft += value * 50;
          this.listSrollLeft = this.$refs.listRef.scrollLeft;
          if (this.listSrollRight >= this.scrollWidth || this.listSrollLeft <= 0) {
            clearInterval(this.scrollTimer);
          }
        }, 50);
      } else {
        clearInterval(this.scrollTimer);
      }
    }
  },
  created() {
    this.selectedPano = this.src[0];
  },
  mounted() {
    //resizeul
    this.$bus.$on('resize', this.calcListWidth);
    //html element
    document.documentElement.style.overflow = 'hidden';
  },
  beforeDestroy() {
    this.$bus.$off('resize', this.calcListWidth);
    document.documentElement.removeAttribute('style');
  }
};
</script>

