<template>
  <div id="app">
    <router-view />
    <LoginModal v-if="loginModalVisible"></LoginModal>
    <LoadingOverlay :active="isLoading"></LoadingOverlay>
  </div>
</template>
<script>
import LoginModal from '@/components/general/Login.vue';
import { mapState, mapGetters } from 'vuex';
import LoadingOverlay from '@/components/general/LoadingOverlay';
// import axios from 'axios';
export default {
  name: 'app',
  components: { LoginModal, LoadingOverlay },
  watch: {
    // $route(to) {
    //   document.title = to.meta.title || 'EC Project';
    // }
    '$route.query.lang': {
      handler(newVal) {
        // localStorage.setItem(newVal);
        if (newVal in this.$i18n.messages) {
          this._i18n.locale = newVal;
          localStorage.setItem('language', newVal);
        } else {
          return;
        }
      }
    }
  },
  computed: {
    ...mapState('UIModules', ['loginModalVisible']),
    ...mapGetters(['isLoading'])
  },
  created() {
    document.title = this.$t('pageTitle');
    if (this.$route.query.lang) {
      this._i18n.locale = this.$route.query.lang;
      localStorage.setItem('language', this.$route.query.lang);
    }

    const hexToken = this.getCookie('hexToken');
    const authToken = document.cookie.replace(/(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/, '$1');

    if (hexToken) {
      this.$http.defaults.headers.common.Authorization = authToken;
    } else {
      this.$store.commit('AuthModules/UPDATE_USER_AUTH', false);
    }

    const api = `${process.env.VUE_APP_APIPATH}/api/user/check`;
    this.$http.post(api).then(response => {
      if (response.data.success) {
        console.log('check login success');
        this.$store.commit('AuthModules/UPDATE_USER_AUTH', true);
      } else {
        document.cookie = 'hexToken=; Max-Age=0'
        document.cookie = 'hexTokenExpireAt=; Max-Age=0'
        this.$store.commit('AuthModules/UPDATE_USER_AUTH', false);
        if (this.$route.meta.requiresAuth) {
          this.$router.push('/');
        }
      }
    });
    // if (this.$route.meta.requiresAuth) {
    //   const api = `${process.env.VUE_APP_APIPATH}/api/user/check`;
    //   axios.post(api).then(response => {
    //     if (response.data.success) {
    //       console.log('api/user/check>success');
    //       return;
    //     } else {
    //       console.log('api/user/check', response.data);
    //       this.$router.push({
    //         name: 'homePage'
    //       });
    //     }
    //   });
    // }
  },
  methods: {
    onResize() {
      this.$bus.$emit('resize');
      this.windowWidth = window.innerWidth;
      this.$store.commit('UIModules/SET_WINDOW_WIDTH', window.innerWidth);
    },
    getCookie(cookieName) {
    let cookie = {};
    document.cookie.split(';').forEach(function(el) {
      let [key,value] = el.split('=');
      cookie[key.trim()] = value;
    })
    return cookie[cookieName];
    }
  },
  mounted() {
    this.$store.commit('UIModules/SET_WINDOW_WIDTH', window.innerWidth);
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize);
    });
  }
};
</script>
<style lang="scss">
@import './assets/all';
</style>
