<template>
  <GoogleMapLoader
    class="map"
    :initGeolocation="false"
    ref="mapLoader"
    v-if="isMapAlive"
    :mapConfig="mapConfig"
    @update:center="updateCenter"
    @update:bounds="updateBounds"
    @update:zoom="updateZoom"
    @idle="handleIdle"
    @mapLoaded="handleMapLoaded"
  >
    <template slot-scope="{ google, map }">
      <GoogleMapPopup
        v-for="product in paginatedStaySource[currentPage - 1]"
        :google="google"
        :map="map"
        :key="
          $route.name == 'search'
            ? `searchPopup-${product.id + searchKey}`
            : $route.name == 'wishList'
            ? `wishListPopup-${product.id + searchKey}`
            : `popup-${product.id + searchKey}`
        "
        :item="product"
        :class="{
          selected: product == $store.state.MapModules.mapProduct.select,
          hover:
            product == $store.state.MapModules.mapProduct.hover &&
            $store.state.MapModules.mapProduct.hoverClass
        }"
        class="popup-bubble-content"
        @click.native="popupClicked(product)"
        @mouseenter.native="hoverProduct(product)"
        @mouseleave.native="hoverProduct(product)"
      />

      <GoogleMapControl
        ref="searchControlRef"
        @click.native="SearchControlEvent"
        :google="google"
        :map="map"
        :wait="!SearchControlTimer"
        :class="{
          'fade-out': !SearchControlTimer,
          hide: !showSearchControl || $route.name == 'wishList' || mapProduct.select
        }"
      />
    </template>
  </GoogleMapLoader>
</template>
<script>
import { mapState, mapGetters } from 'vuex';
import GoogleMapLoader from '../general/GoogleMapLoader';
// import GoogleMapMarker from './GoogleMapMarker';
import GoogleMapPopup from './GoogleMapPopup';
import GoogleMapControl from './GoogleMapControl';
import { mapSettings } from '@/constants/mapSettings.js';

export default {
  data() {
    return {
      center: null,
      bounds: null,
      zoom: null,
      mapLoaded: false,
      isMapAlive: true,
      popupRefreash: true
    };
  },
  components: {
    GoogleMapLoader,
    GoogleMapControl,
    // GoogleMapMarker,
    GoogleMapPopup
  },
  computed: {
    ...mapGetters(['propertySource', 'paginatedStaySource']),
    ...mapState('MapModules', {
      google: state => state.google,
      mapProduct: state => state.mapProduct,
      // viewAllProductsBounds: state => state.viewAllProductsBounds,
      SearchControlTimer: state => state.SearchControlTimer,
      showSearchControl: state => state.showSearchControl
    }),
    ...mapState('SearchModules', {
      filterSearchProducts: state => state.filterSearchProducts,
      searchFilter: state => state.searchFilter,
      resultsLatLng: state => state.resultsLatLng,
      filteredProductsBounds: state => state.filteredProductsBounds,
      searchBounds: state => state.searchBounds,
      searchByQuery: state => state.searchByQuery,
      //Fix Failed to execute 'insertBefore' on 'Node' by add special key
      searchKey: state => state.searchKey
    }),
    ...mapState(['mapPageMode', 'currentPage']),
    ...mapState('WishlistModules', ['wishListProducts', 'wishListProductsBounds']),
    mapConfig() {
      return {
        ...mapSettings,
        center: { lat: 25.0409201, lng: 121.5720055 },
        streetViewControl: false,
        fullscreenControl: false
      };
    }
    //compute
    // mapLoadedFitWishlistBounds() {
    //   if (this.$route.name == 'wishList') {
    //     return this.mapLoaded && this.$route.name == 'wishList' && this.wishListProductsBounds
    //       ? this.wishListProductsBounds
    //       : false;
    //   } else if (this.$route.name == 'search') {
    //     return this.mapLoaded && this.$route.name == 'search' && this.filteredProductsBounds
    //       ? this.filteredProductsBounds
    //       : false;
    //   }
    //   return false;
    // }
  },
  watch: {
    //user()
    'mapProduct.select'(newItem) {
      // const saveBounds = this.$refs.mapLoader.map.getBounds();
      if (newItem && !this.mapProduct.selectByMap) {
        this.$refs.mapLoader.map.fitBounds(newItem.info.mapInfo.geometry.viewport);
        //
        this.addTempListener(this.controlFadeOut);
        //viewport
        // this.$store.commit('MapModules/SAVE_VIEW_ALL_BOUNDS', saveBounds);
      }
    },
    //Viewport
    'searchFilter.location.mapInfo.geometry.viewport': {
      handler(newViewport) {
        //serchByMap this.searchFilter.locationnull, 
        if (this.searchFilter.location) {
          //
          this.addTempListener(this.controlHide);
          //Viewport
          this.setViewport(newViewport, this.searchFilter.location.mapInfo.geometry.location);
        }
      },
      deep: true
    },
    //Bounds extend
    resultsLatLng(newVal) {
      if (newVal.length < 1) {
        return;
      }
      const bounds = new this.$refs.mapLoader.google.maps.LatLngBounds();
      //
      for (let i = 0; i < newVal.length; i++) {
        bounds.extend(newVal[i]);
      }
      this.$store.commit('SearchModules/SAVE_FILTERED_PRODUCTS_BOUNDS', bounds);
    },
    'searchFilter.pplNum'() {
      this.$store.dispatch('MapModules/setControlVisibleTimer');
      this.$store.dispatch('MapModules/handleSearchControl', true);
    },
    // viewAllProducts(newVal) {
    //   //
    //   this.addTempListener();
    //   console.log('');
    //   //
    //   if (newVal) {
    //     this.$refs.mapLoader.map.fitBounds(this.filteredProductsBounds);
    //     this.$store.dispatch('MapModules/handleSearchControl', false);
    //   }
    // }
    // mapLoadedFitWishlistBounds: {
    //   handler(newVal) {
    //     if (newVal) {
    //       // this.$refs.mapLoader.map.fitBounds(newVal);
    //     }
    //   },
    //   immediate: true
    // }
    //watch
    wishListProductsBounds() {
      if (this.$route.name == 'wishList' && this.wishListProductsBounds) {
        this.$refs.mapLoader.map.fitBounds(this.wishListProductsBounds);
      }
    },
    searchByQuery: {
      handler(newVal) {
        if (newVal.bounds) {
          this.$store.dispatch('SearchModules/filterSearchProducts', {
            searchByQuery: true,
            searchType: 'query',
            searchByMap: newVal.routeQuery.search_by_map == 'true' ? true : false,
            bounds: newVal.bounds
          });
          this.addTempListener(() => {
            setTimeout(() => {
              this.$refs.mapLoader.map.fitBounds(newVal.bounds, 0);
            }, 1500);
          });
        }
      }
    },
    '$route.name'(newVal) {
      if (newVal == 'search' && this.searchBounds) {
        this.$refs.mapLoader.map.fitBounds(this.searchBounds, 0);
      }
    }
  },
  methods: {
    addTempListener(callback) {
      let listener = this.$refs.mapLoader.google.maps.event.addListener(
        this.$refs.mapLoader.map,
        'idle',
        () => {
          //
          callback();
          //listener
          this.$refs.mapLoader.google.maps.event.removeListener(listener);
        }
      );
    },
    controlFadeOut() {
      this.$store.dispatch('MapModules/handleSearchControl', false);
    },
    controlHide() {
      this.$store.dispatch('MapModules/handleSearchControl', false);
    },
    setViewport(viewport, center = null) {
      if (center) {
        this.$refs.mapLoader.map.setCenter(center);
      }
      this.$refs.mapLoader.map.fitBounds(viewport);
      const prevZoom = this.$refs.mapLoader.map.getZoom();
      this.$refs.mapLoader.map.setZoom(prevZoom + 0.8);
    },
    handleIdle() {
      // console.log('idle');
      // const oldBounds = this.$refs.mapLoader.map.getBounds();
      // oldBounds.extends();
    },
    hoverProduct(product) {
      if (product != this.$store.state.MapModules.mapProduct.hover) {
        this.$store.dispatch('MapModules/hoverMapProduct', { product, hoverClass: false });
        this.$store.dispatch('MapModules/scrollToMapProduct', product.id);
        return;
      }
      this.$store.dispatch('MapModules/hoverMapProduct', { product: null, hoverClass: false });
    },
    SearchControlEvent() {
      const boundKeyArray = Object.keys(this.bounds); //key
      this.$store.dispatch('MapModules/handleSearchControl', false);
      //query
      const newQuery = {
        search_by_map: true,
        search_bounds_s: this.bounds[boundKeyArray[1]].i, // 
        search_bounds_e: this.bounds[boundKeyArray[0]].i, // 
        search_bounds_n: this.bounds[boundKeyArray[1]].j, // 
        search_bounds_w: this.bounds[boundKeyArray[0]].j, // 
        guest_number: this.searchFilter.pplNum
      };
      if (this.searchFilter.date) {
        newQuery.startDate = this.searchFilter.date[0];
        newQuery.endDate = this.searchFilter.date[1];
      }
      this.$router.replace({
        query: newQuery
      });
      this.$store.commit('SearchModules/SET_SEARCH_QUERY', newQuery);
      //
      this.$store.dispatch('SearchModules/filterSearchProducts', {
        searchByMap: true,
        searchType: 'map'
      });
      //[vuex]searchFilter.locationnull, searchBar > [method]handleSearch()  'searchFilter.location.value'
      this.$store.dispatch('SearchModules/passSearchLocation', null);
      this.$store.dispatch('MapModules/removeMapProduct');
    },
    latLngToAddress(latLng) {
      this.$refs.mapLoader.geocoder.geocode({ location: latLng }, (results, status) => {
        if (status === 'OK') {
          const addressArr = results[results.length - 5].address_components;
          const formatAddress = addressArr.reduce((formatted, element) => {
            const NewElem = Number(element.long_name);
            if (isNaN(NewElem)) {
              formatted.push(element.long_name);
            }
            return formatted;
          }, []);
          const newAddress = formatAddress.reverse().join(',');
          this.$store.dispatch('MapModules/searchByMapAddress', newAddress);
        } else {
          console.log('not OK', results);
        }
      });
    },
    handleBoundChange() {
      this.$store.dispatch('MapModules/handleSearchControl', true);
      this.$store.dispatch('MapModules/setControlVisibleTimer');
      // this.$nextTick(() => {
      //   let timer = setTimeout(() => {
      //     if (this.SearchControlTimer != timer) return;
      //     this.SearchControlTimer = null;
      //   }, 3000);
      //   this.SearchControlTimer = timer;
      // });
    },
    updateCenter(center) {
      this.center = center;
      this.$store.commit('MapModules/UPDATE_CENTER', center);
    },
    updateBounds(bounds) {
      this.bounds = bounds;
      this.$store.commit('MapModules/UPDATE_BOUNDS', bounds);
      this.handleBoundChange();
    },
    updateZoom(zoom) {
      this.zoom = zoom;
    },
    popupClicked(product) {
      this.$store.dispatch('MapModules/selectMapProduct', [product, true]);
    },
    handleMapLoaded() {
      this.mapLoaded = true;
      // if (this.$route.name == 'wishList') {
      //   console.log(this.$refs.mapLoader.map);
      //   this.addTempListener(this.$refs.mapLoader.map.fitBounds(this.wishListProductsBounds));
      // }
    }
    // fitWishListBounds() {
    //   const bounds = this.wishListProductsBounds;
    //   if (bounds) {
    //     console.log('fitWishListBounds/fitBounds');
    //     this.$refs.mapLoader.map.fitBounds(bounds);
    //   } else {
    //     console.log('fitWishListBounds');
    //   }
    // }
  },
  created() {
    this.$bus.$on('backToProductsList', () => {
      if (!this.mapProduct.selectByMap) {
        this.addTempListener(this.controlFadeOut);
        if (this.$route.name == 'wishList') {
          this.$refs.mapLoader.map.fitBounds(this.wishListProductsBounds);
        } else if (this.$route.name == 'search') {
          this.$refs.mapLoader.map.fitBounds(this.searchBounds, 0);
          // this.$refs.mapLoader.map.fitBounds(this.filteredProductsBounds); //
          this.addTempListener(() => {
            setTimeout(() => {
              this.$refs.mapLoader.map.fitBounds(this.searchBounds); //
              this.$refs.mapLoader.map.setZoom(this.zoom + 1); //
            }, 0);
          });
        }
      }
    });
  },
  mounted() {
    // this.$nextTick(() => {
    //   if (this.$route.name == 'wishList') {
    //     console.log(this.$refs.mapLoader.map);
    //     this.addTempListener(this.$refs.mapLoader.map.fitBounds(this.wishListProductsBounds));
    //   }
    // });
  },
  beforeDestroy() {
    this.$bus.$off('backToProductsList');
  }
  // beforeDestroy: function() {
  //   // 
  // }
};
</script>

<style scoped>
.map {
  height: 100%;
}
div >>> .infoContent .name {
  padding: 4px 2px 0 0;
  font-size: 12px;
  font-weight: bold;
}
div >>> .infoContent .price {
  padding: 4px 2px 0 0;
  font-size: 12px;
}
</style>
