<template>
  <el-popover placement="bottom" width="100" v-model="visible" popper-class="roomtype-popover">
    <div class="poper-option" @click="editHandler(itemSrc.row)">
      
    </div>
    <el-divider></el-divider>
    <div v-if="duplicate" class="poper-option" @click="duplicateHandler(itemSrc.row)">
      
    </div>
    <el-divider></el-divider>
    <el-popconfirm
      :title="'' + title + ''"
      confirmButtonText=""
      cancelButtonText=""
      cancelButtonType="Text"
      @onConfirm="deleteHandler"
    >
      <div class="poper-option" slot="reference">
        
      </div>
    </el-popconfirm>
    <el-button
      type="text"
      slot="reference"
      size="midium"
      :disabled="itemSrc.disable || isLoading"
      :icon="itemSrc.disable || isLoading ? 'el-icon-loading' : 'el-icon-more'"
    >
    </el-button>
  </el-popover>
</template>

<script>
export default {
  props: {
    itemSrc: Object,
    title: String,
    duplicate: Boolean
  },
  data() {
    return {
      visible: false,
      disable: false
    };
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoadingQuery > 0;
    }
  },
  methods: {
    deleteHandler() {
      this.visible = false;
      this.itemSrc.disable = true;
      this.$emit('deleteItem', this.itemSrc.row);
    },
    editHandler(item = null) {
      this.$emit('editItem', item);
    },
    duplicateHandler(item = null) {
      this.visible = false;
      this.itemSrc.disable = true;
      this.$emit('duplicateItem', item);
    }
  }
};
</script>

<style scoped>
.poper-option {
  text-align: center;
  cursor: pointer;
  padding: 8px;
  font-size: 14px;
  transition: 0.25s ease;
  color: #767676 !important;
}
.poper-option:hover {
  background-color: #f5f5f5;
}
.el-divider {
  margin: 0;
  background: #f2f2f2;
}
</style>
