import axios from 'axios';
import Vue from 'vue';
export default {
  namespaced: true,
  state: {
    titleList: {},
    orders: [],
    filteredOrders: [],
    pagination: {}
  },
  actions: {
    async GetRoomManagementData(context) {
      if (context.state.orders.length > 0) {
        context.commit('CLEAR_ORDERS');
      }
      const promise1 = context.dispatch('getOrders');
      const promise2 = context.dispatch('ProductsModules/getProducts', ['admin', 'all', true], {
        root: true
      });
      Promise.all([promise1, promise2]).then(() => {
        context.dispatch('getTitleList');
      });
    },
    getOrders(context, page = 1) {
      return new Promise(resolve => {
        const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/orders?page=${page}`;
        // context.commit('LOADING', true, { root: true });
        axios.get(api).then(response => {
          if (response.data.success) {
            const filteredOrder = response.data.orders.filter(
              obj =>
                obj.message &&
                obj.message.roomInfo &&
                obj.message.roomInfo.propertyId &&
                obj.message.status != 'delete'
            );
            if (response.data.pagination.has_next && filteredOrder.length > 0) {
              // console.log('has_next', response.data.pagination.has_next);
              // console.log('filteredOrder', filteredOrder);
              context.commit('GET_ORDERS', filteredOrder);
              context.dispatch('getOrders', response.data.pagination.current_page + 1).then(() => {
                resolve();
              });
            } else {
              resolve();
            }
          } else {
            context.dispatch('AlertModules/updateMessage', [response.data.message, 'danger'], {
              root: true
            });
          }
          // context.commit('LOADING', false, { root: true });
        });
      });
    },
    getTitleList(context) {
      const titleList = context.state.titleList;
      const newFilteredOrders = [];
      context.state.orders.forEach(obj => {
        newFilteredOrders.push(obj);
        // console.log('obj', obj);
        var orderPropertyId = obj.message.roomInfo.propertyId;
        var orderRoomTypeId = obj.message.roomInfo.roomTypeId;
        var newPropertyObj = context.rootState.ProductsModules.products.find(propertyObj => {
          return propertyObj.id == orderPropertyId;
        });
        // console.log('orderObj', { orderPropertyId, orderRoomTypeId, newPropertyObj });
        if (!newPropertyObj) {
          //
          context.commit('SET_TITLE_LIST_PROPERTY', {
            propertyKey: orderPropertyId,
            propertyTitle: ''
          });
          //
          context.commit('SET_TITLE_LIST_ROOMTYPE', {
            propertyKey: orderPropertyId,
            roomTypeKey: orderRoomTypeId,
            roomTypeObj: {
              name: '',
              price: 0
            }
          });

          return;
        }
        const newRoomTypeObj = newPropertyObj.roomTypes.find(roomTypeObj => {
          return roomTypeObj.id == orderRoomTypeId;
        });
        if (!titleList[orderPropertyId]) {
          context.commit('SET_TITLE_LIST_PROPERTY', {
            propertyKey: orderPropertyId
          });
        }
        if (!titleList[orderPropertyId].roomTypes) {
          context.commit('SET_TITLE_LIST_ROOMTYPE', { propertyKey: orderPropertyId });
        }
        //
        context.commit('SET_TITLE_LIST_PROPERTY', {
          propertyKey: orderPropertyId,
          propertyTitle: newPropertyObj.info.title
        });
        //
        context.commit('SET_TITLE_LIST_ROOMTYPE', {
          propertyKey: orderPropertyId,
          roomTypeKey: orderRoomTypeId,
          roomTypeObj: newRoomTypeObj
        });
      });
      context.commit('PUSH_FILTERED_ORDER', newFilteredOrders);
    }
  },
  mutations: {
    CLEAR_ORDERS(state) {
      state.orders = [];
    },
    GET_ORDERS(state, payload) {
      state.orders = state.orders.concat(payload);
    },
    PUSH_FILTERED_ORDER(state, payload) {
      state.filteredOrders = payload;
    },
    PAGINATION(state, payload) {
      state.pagination = payload;
    },
    SET_TITLE_LIST_PROPERTY(state, payload) {
      //payload:{}
      if (!state.titleList[payload.propertyKey]) {
        Vue.set(state.titleList, payload.propertyKey, {});
      }
      if (payload.propertyTitle) {
        Vue.set(state.titleList[payload.propertyKey], 'propertyTitle', payload.propertyTitle);
      }
    },
    SET_TITLE_LIST_ROOMTYPE(state, payload) {
      if (!state.titleList[payload.propertyKey].roomTypes) {
        Vue.set(state.titleList[payload.propertyKey], 'roomTypes', {});
      }
      if (payload.roomTypeObj) {
        Vue.set(
          state.titleList[payload.propertyKey].roomTypes,
          payload.roomTypeKey,
          payload.roomTypeObj
        );
      }
    }
  },
  getters: {
    orders: state => state.orders,
    pagination: state => state.pagination,
    orderSource: state => state.filteredOrders,
    paginatedOrderArray(state, getters) {
      const setNumber = 10;
      const orderNumber = getters.orderSource.length;
      const remainder = orderNumber % setNumber;
      const pageNumber =
        remainder === 0 ? parseInt(orderNumber / setNumber) : parseInt(orderNumber / setNumber) + 1;
      const paginationArray = [];
      for (let i = 1; i <= pageNumber; i++) {
        let query = i * setNumber;
        let preQuery = (i - 1) * setNumber;
        const queryArray = getters.orderSource.filter((property, index) => {
          return preQuery <= index && index < query;
        });
        paginationArray.push(queryArray);
      }
      // console.log(paginationArray);
      //0-9
      //10-19
      //20-25
      return paginationArray;
    }
  }
};
