<template>
  <el-container ref="mainContainer">
    <el-header
      height="80px"
      :class="{ init: scrollY === 0, 'homepage-nav': $route.name == 'homePage' }"
    >
      <Navbar stage="front" />
    </el-header>
    <keep-alive>
      <router-view />
    </keep-alive>
  </el-container>
</template>

<script>
import Navbar from '@/components/general/Navbar';
import { mapState } from 'vuex';
export default {
  components: { Navbar },
  data() {
    return {
      scrollY: 0
    };
  },
  watch: {
    '$route.name': {
      handler(newVal) {
        if (newVal == 'homePage') {
          window.addEventListener('scroll', this.updateScroll);
        }
      },
      immediate: true
    }
  },
  computed: {
    ...mapState('UIModules', ['datePickBtnShow'])
  },
  methods: {
    updateScroll() {
      this.scrollY = window.scrollY;
    },
    handleDatePickerClose() {
      this.$store.commit('UIModules/SET_DATEPICKER_CLOSE_BUTTON', true);
      this.$bus.$emit('datePickerClose');
    }
  },
  created() {},
  mounted() {},
  beforeDestroy() {
    this.$refs.mainContainer.$el.removeEventListener('scroll', this.updateScroll);
  }
};
</script>
