<template>
  <el-row class="flex-row">
    <el-col :sm="24" :md="22" class="center-col">
      <div class="title">
        
      </div>
      <el-table
        :data="titleList ? tempOrders[currentPage - 1] : null"
        max-height="480"
        class="el-table--border"
        stripe
        style="width: 100%"
      >
        <el-table-column label="" width="60" class-name="center-align-col">
          <template slot-scope="scope">
            <el-tooltip effect="dark" :content="scope.row.create_at | date" placement="top">
              <el-button type="text" icon="el-icon-date"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="" width="60" class-name="center-align-col">
          <template slot-scope="scope">
            <el-tooltip
              v-if="scope.row.message.status === 'confirm'"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button :loading="updating" type="text" icon="el-icon-success"></el-button>
            </el-tooltip>
            <el-tooltip
              v-if="!scope.row.message.status || scope.row.message.status === 'tbd'"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button :loading="updating" type="text" icon="el-icon-warning"></el-button>
            </el-tooltip>
            <el-tooltip
              v-if="scope.row.message.status === 'cancel'"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button :loading="updating" type="text" icon="el-icon-remove"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="" width="140" class-name="">
          <template slot-scope="scope">
            <template v-if="titleList.hasOwnProperty(scope.row.message.roomInfo.propertyId)">
              {{
                titleList[scope.row.message.roomInfo.propertyId]
                  ? titleList[scope.row.message.roomInfo.propertyId].propertyTitle
                  : 'loading'
              }}
            </template>
            <template v-else>
              <div class="content-placeholder"></div>
            </template>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <template v-if="titleList[scope.row.message.roomInfo.propertyId]">
              {{
                titleList[scope.row.message.roomInfo.propertyId].roomTypes[
                  scope.row.message.roomInfo.roomTypeId
                ]
                  ? titleList[scope.row.message.roomInfo.propertyId].roomTypes[
                      scope.row.message.roomInfo.roomTypeId
                    ].name
                  : ''
              }}
            </template>
            <template v-else>
              <div class="content-placeholder"></div>
            </template>
          </template>
        </el-table-column>
        <el-table-column label="" width="60" class-name="">
          <template slot-scope="scope">
            {{ scope.row.message.roomInfo.roomQty }}
          </template>
        </el-table-column>
        <el-table-column label="" width="120" class-name="">
          <template slot-scope="scope">
            {{ scope.row.message.roomInfo.orderDate[0] }}
          </template>
        </el-table-column>
        <el-table-column label="" width="60" class-name="">
          <template slot-scope="scope">
            {{ scope.row.message.roomInfo.orderDate.length + ' ' }}
          </template>
        </el-table-column>
        <el-table-column label="" width="120" class-name="">
          <template slot-scope="scope">
            <template v-if="titleList[scope.row.message.roomInfo.propertyId]">
              {{
                titleList[scope.row.message.roomInfo.propertyId].roomTypes[
                  scope.row.message.roomInfo.roomTypeId
                ]
                  ? scope.row.message.roomInfo.orderDate.length *
                    scope.row.message.roomInfo.roomQty *
                    parseInt(
                      titleList[scope.row.message.roomInfo.propertyId].roomTypes[
                        scope.row.message.roomInfo.roomTypeId
                      ].price
                    )
                  : '0' | currency
              }}
            </template>
            <template v-else>
              <div class="content-placeholder"></div>
            </template>
          </template>
        </el-table-column>
        <el-table-column label="" width="50" class-name="center-align-col">
          <template slot-scope="scope">
            <OrderPopover
              title=""
              :itemSrc="scope"
              :updating="updating"
              @confirmItem="confirmOrder"
              @deleteItem="deleteOrder"
              @cancelItem="cancelOrder"
            ></OrderPopover>
          </template>
        </el-table-column>
        <template slot="empty">
          <div v-if="isLoading"><i class="el-icon-loading"></i> </div>
          <div v-if="!isLoading"><i class="el-icon-loading"></i> </div>
        </template>
      </el-table>

      <!-- <el-pagination
      background
      layout="prev, pager, next"
      :page-count="pagination.total_pages"
      @current-change="paginationHandler"
      @prev-click="paginationHandler(pagination.current_page - 1)"
      @next-click="paginationHandler(pagination.current_page + 1)"
      :disabled="isLoading"
    >
    </el-pagination> -->
      <!--  -->
      <!--  -->
      <!--  -->
      <!-- <Calendar v-if="calendar"></Calendar> -->

      <el-pagination
        background
        :pager-count="5"
        :layout="`prev, pager, next`"
        :page-count="paginatedOrderArray.length"
        :current-page="currentPage"
        @current-change="handlePageChange"
      >
      </el-pagination>
    </el-col>
  </el-row>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex';
// import Calendar from './Calendar';
import OrderPopover from '@/components/backstage/OrderPopover';
export default {
  components: { OrderPopover },
  data() {
    return {
      calendar: false,
      updating: false,
      tempOrders: [],
      currentPage: 1
    };
  },
  computed: {
    ...mapGetters('ProductsModules', ['products']),
    ...mapGetters('OrdersModules', ['paginatedOrderArray']),
    ...mapState('OrdersModules', ['orders', 'filteredOrders', 'pagination', 'titleList']),
    isLoading() {
      return this.$store.state.isLoadingQuery > 0;
    }
  },
  watch: {
    paginatedOrderArray: {
      handler() {
        this.tempOrders = this.paginatedOrderArray;
      }
    }
  },
  methods: {
    ...mapActions('ProductsModules', ['getProducts']),
    // getOrders(page = 1) {
    //   this.$store.commit('COUNT_LOADING', 1);
    //   const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/orders?page=${page}`;
    //   axios.get(api).then(response => {
    //     if (response.data.success) {
    //       const newOrders = response.data.orders.filter(obj => {
    //         return obj.message.demo === true;
    //       });
    //       this.$store.commit('OrdersModules/GET_ORDERS', newOrders); //OrdersStore
    //       this.tempOrders = newOrders;
    //       this.hasOrders = true;
    //     } else {
    //       this.$store.dispatch('AlertModules/updateMessage', [response.data.message, 'danger']);
    //     }
    //     this.hasOrders = true;
    //     this.$store.commit('COUNT_LOADING', -1);
    //   });
    // },
    updateOrders(item) {
      this.updating = true;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/order/${item.id}`;
      this.$http.put(api, { data: item }).then(response => {
        if (response.data.success) {
          this.$message({
            showClose: true,
            center: true,
            type: 'success',
            message: response.data.message
          });
        } else {
          this.$message({
            showClose: true,
            center: true,
            type: 'danger',
            message: response.data.message
          });
        }
        this.$store.dispatch('OrdersModules/GetRoomManagementData');
        this.updating = false;
      });
    },
    cancelOrder(item) {
      const newOrder = JSON.parse(JSON.stringify(item));
      newOrder.message.status = 'cancel';
      this.updateOrders(newOrder);
    },
    deleteOrder(item) {
      const newOrder = JSON.parse(JSON.stringify(item));
      newOrder.message.status = 'delete';
      this.updateOrders(newOrder);
    },
    confirmOrder(item) {
      const newOrder = JSON.parse(JSON.stringify(item));
      newOrder.message.status = 'confirm';
      this.updateOrders(newOrder);
    },
    manualComputed(propertyId, roomTypeId, action) {
      let product = null;
      let roomType = null;
      if (this.products) {
        product = this.products.find(obj => obj.id == propertyId);
        if (product) roomType = product.roomTypes.find(obj => obj.id == roomTypeId);
      }

      if (action === 'property') {
        return product.info.title;
      }
      if (action === 'roomType') {
        if (roomType) return roomType.name;
        else return null;
      }
      if (action === 'calcPrice') {
        return parseInt(roomType.price);
      }
    },
    handlePageChange(currentPage) {
      this.currentPage = currentPage;
    }
  },
  created() {
    // this.$store.dispatch('OrdersModules/getOrders');
    this.$store.dispatch('OrdersModules/GetRoomManagementData');
  }
};
</script>

