<template>
  <div>
    <div class="google-map" ref="googleMap"></div>
    <template v-if="Boolean(this.google) && Boolean(this.map)">
      <slot :google="google" :map="map" />
    </template>
  </div>
</template>

<script>
import GoogleMapsApiLoader from 'google-maps-api-loader';

export default {
  props: {
    mapConfig: Object,
    apiKey: String,
    setMarker: Object,
    initGeolocation: Boolean
  },

  data() {
    return {
      marker: null,
      google: null,
      map: null,
      geocoder: null,
      latLngBounds: null,
      pos: null
    };
  },

  async mounted() {
    const googleMapApi = await GoogleMapsApiLoader({
      apiKey: this.apiKey
    });
    this.google = googleMapApi;
    this.geocoder = new this.google.maps.Geocoder();
    this.initializeMap();
  },

  watch: {
    setMarker: function() {
      this.placeMarker(this.setMarker);
      this.setMapCenter(this.setMarker);
    }
  },
  methods: {
    initializeMap() {
      const mapContainer = this.$refs.googleMap;
      this.map = new this.google.maps.Map(mapContainer, this.mapConfig);
      this.addListeners();

      if (this.initGeolocation) {
        this.getGeolocation();
      }
      if (this.setMarker) {
        this.placeMarker(this.setMarker);
      }
    },
    setMapCenter(location) {
      this.map.setCenter(location);
    },
    placeMarker(latLng) {
      if (this.marker) this.removeMarkers();
      let marker = new this.google.maps.Marker({
        position: latLng,
        map: this.map,
        animation: this.google.maps.Animation.DROP
      });
      this.marker = marker;
    },
    removeMarkers() {
      this.marker.setMap(null);
    },
    getGeolocation() {
      const vm = this;
      const infoWindow = new vm.google.maps.InfoWindow();
      // Try HTML5 geolocation.
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          function(position) {
            vm.pos = {
              lat: position.coords.latitude,
              lng: position.coords.longitude
            };
            var locationMarker = new vm.google.maps.Marker({
              position: vm.pos,
              icon: {
                url: 'images/dot.svg',
                scaledSize: new vm.google.maps.Size(30, 30), // scaled size
                origin: new vm.google.maps.Point(0, 0), // origin
                anchor: new vm.google.maps.Point(15, 15) // anchor
              },
              title: this.$t('map.yourLocation')
            });
            locationMarker.setMap(vm.map);
            infoWindow.setPosition(vm.pos);
            infoWindow.setContent(this.$t('map.yourLocation'));
            // console.log('', this.$t.map.yourLocation);
            // infoWindow.open(vm.map);
            vm.map.setCenter(vm.pos);
            vm.map.setZoom(16);
          },
          function() {
            handleLocationError(true, infoWindow, vm.map.getCenter());
          }
        );
      } else {
        // Browser doesn't support Geolocation
        handleLocationError(false, infoWindow, vm.map.getCenter());
      }

      function handleLocationError(browserHasGeolocation, infoWindow, pos) {
        infoWindow.setPosition(pos);
        infoWindow.setContent(
          browserHasGeolocation
            ? 'Error: The Geolocation service failed.'
            : "Error: Your browser doesn't support geolocation."
        );
      }
    },
    addListeners() {
      const vm = this;
      this.map.addListener('click', function(e) {
        vm.$emit('update:click', e);
      });
      this.map.addListener('center_changed', function() {
        const newCenter = { lat: vm.map.getCenter().lat(), lng: vm.map.getCenter().lng() };
        vm.$emit('update:center', newCenter);
      });
      this.map.addListener('bounds_changed', function() {
        const newBounds = {
          sw: {
            lat: vm.map
              .getBounds()
              .getSouthWest()
              .lat(),
            lng: vm.map
              .getBounds()
              .getSouthWest()
              .lng()
          },
          ne: {
            lat: vm.map
              .getBounds()
              .getNorthEast()
              .lat(),
            lng: vm.map
              .getBounds()
              .getNorthEast()
              .lng()
          }
        };
        vm.$emit('update:bounds', newBounds);
      });

      this.map.addListener('zoom_changed', function() {
        const newZoom = vm.map.getZoom();
        vm.$emit('update:zoom', newZoom);
      });
    }
  }
};
</script>

<style scoped>
.google-map {
  width: 100%;
  min-height: 100%;
}
</style>
