import Vue from 'vue';
import VueI18n from 'vue-i18n';
import locale from 'element-ui/lib/locale';
import { locale as zhTw } from '@/config/i18n/zh-tw';
import { locale as en } from '@/config/i18n/en';
Vue.use(VueI18n);
let messages = {};
messages = { ...messages, 'zh-TW': zhTw, en };
const lang = localStorage.getItem('language') || 'zh-TW';

const i18n = new VueI18n({
  locale: lang,
  messages
});
locale.i18n((key, value) => i18n.t(key, value));
export default i18n;
