<template>
  <div
    @mouseenter="photoBtnShow = true"
    @mouseleave="photoBtnShow = false"
    class="img-container-wrapper"
  >
    <transition name="el-fade-in">
      <span v-if="photoBtnShow" class="button-group">
        <button v-if="panoImgNum" @click="handleShowPanoViewer">
          {{ $t('stayPage.viewPanoramas') }}
        </button>
        <button @click="handleShowViewer">{{ $t('stayPage.viewPhotos') }}</button>
      </span>
    </transition>
    <template v-if="imageLength < 3 || !_windowSize.lg.larger">
      <div class="img-container">
        <img class="imgElm" :src="mapProduct.select.photos.normalImgs[0].url" /></div
    ></template>
    <template v-else-if="imageLength >= 3 && imageLength < 5">
      <div class="img-container three-type">
        <div class="first-image">
          <img class="imgElm" :src="mapProduct.select.photos.normalImgs[0].url" />
        </div>
        <div class="image-col">
          <div>
            <img class="imgElm" :src="mapProduct.select.photos.normalImgs[1].url" />
          </div>
          <div>
            <img class="imgElm" :src="mapProduct.select.photos.normalImgs[2].url" />
          </div>
        </div>
      </div>
    </template>
    <template v-else-if="imageLength >= 5">
      <div class="img-container five-type">
        <div class="first-image">
          <img class="imgElm" :src="mapProduct.select.photos.normalImgs[0].url" />
        </div>
        <div class="image-col">
          <div>
            <img class="imgElm" :src="mapProduct.select.photos.normalImgs[1].url" />
          </div>
          <div>
            <img class="imgElm" :src="mapProduct.select.photos.normalImgs[2].url" />
          </div>
        </div>
        <div class="image-col">
          <div>
            <img class="imgElm" :src="mapProduct.select.photos.normalImgs[3].url" />
          </div>
          <div>
            <img class="imgElm" :src="mapProduct.select.photos.normalImgs[4].url" />
          </div>
        </div>
      </div>
    </template>
    <PhotoPreview
      :showViewer="showPhotoViewer"
      :previewSrcList="mapProduct.select.photos.normalImgs"
    ></PhotoPreview>
    <PanoDialog
      v-if="showPanoViewer"
      :src="mapProduct.select.photos.panoramas"
      :visible="showPanoViewer"
      @closeDialog="handleClosePanoViewer"
    ></PanoDialog>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import PhotoPreview from './PhotoPreview';
import PanoDialog from '@/components/general/PanoDialog';
export default {
  // eslint-disable-next-line vue/no-unused-components
  components: { PanoDialog, PhotoPreview },
  data() {
    return {
      photoBtnShow: false,
      showPanoramasViewer: false
    };
  },
  watch: {
    'mapProduct.select': {
      handler() {
        this.$nextTick(() => {
          this.initImg();
        });
      },
      deep: true
    }
  },
  computed: {
    ...mapState('MapModules', ['mapProduct']),
    ...mapState(['showPhotoViewer', 'showPanoViewer']),
    ...mapState('UIModules', ['_windowSize']),
    imageLength() {
      return this.mapProduct.select.photos.normalImgs.length;
    },
    panoImgNum() {
      return this.mapProduct.select.photos.panoramas;
    }
  },
  methods: {
    initImg() {
      this.$nextTick(() => {
        let imgElements = document.getElementsByClassName('imgElm');

        imgElements.forEach(element => {
          element.addEventListener('load', this.getRatio(event, element));
        });
      });
    },
    async getRatio(event, element) {
      function checkImageLoadedPromise() {
        return new Promise(solve => {
          // eslint-disable-next-line no-unused-vars
          var checkImageLoaded = setInterval(() => {
            // console.log('element', element);
            if (element.clientWidth > 0 && element.clientHeight > 0) {
              clearInterval(checkImageLoaded);
              solve();
            }
          }, 10);
        });
      }
      await checkImageLoadedPromise();
      let parent = element.parentElement;
      let parentRatio = parent.clientWidth / parent.clientHeight;
      let ratio = element.clientWidth / element.clientHeight;
      // console.log('element', element.clientHeight);
      // console.log(parent.clientWidth);
      // console.log(parent.clientHeight);
      // console.log('ratio <= parentRatio', ratio <= parentRatio);
      // console.log('ratio > parentRatio', ratio > parentRatio);
      // console.log('ratio,parentRatio', ratio, parentRatio);
      // console.log('ratioHW', parent.clientWidth / parent.clientHeight);
      // console.log(isFinite(ratio) && isFinite(parentRatio));

      if (ratio <= parentRatio && isFinite(ratio) && isFinite(parentRatio)) {
        if (element.clientHeight > parent.clientHeight) {
          let translateY = (element.clientHeight - parent.clientHeight) / 2;
          element.style.transform = `translateY(-${translateY}px)`;
          element.style.webkitTransform = `translateY(-${translateY}px)`;
        }
      }
      if (ratio > parentRatio && isFinite(ratio) && isFinite(parentRatio)) {
        element.style.width = 'auto';
        element.style.height = '100%';
        if (element.clientWidth > parent.clientWidth) {
          let translateX = (element.clientWidth - parent.clientWidth) / 2;
          element.style.transform = `translateX(-${translateX}px)`;
          element.style.webkitTransform = `translateX(-${translateX}px)`;
        }
      }
      element.removeEventListener('load', this.getRatio);
    },
    handleShowViewer() {
      //PhotoPreview
      this.$store.dispatch('setViewer', { value: true, key: 'showPhotoViewer' });
    },
    handleShowPanoViewer() {
      this.$store.dispatch('setViewer', { value: true, key: 'showPanoViewer' });
    },
    handleClosePanoViewer() {
      this.$store.dispatch('setViewer', { value: false, key: 'showPanoViewer' });
    }
  },
  created() {},
  mounted() {
    // this.initImg();
  },
  beforeDestroy() {}
};
</script>

