<template>
  <div>
    <Modal class="login-modal" @close="handleClose">
      <template v-slot:header>{{ $t('loginModal.login') }}</template>
      <template v-slot:body>
        <form class="login-form" @submit.prevent="signIn">
          <div @click="handleInputClick('emailInput')" class="input-wrapper">
            <label for="inputEmail">
              <div class="label-text" :class="{ shrink: emailInputFocus || user.username }">
                {{ $t('loginModal.email') }}
              </div>
              <input
                ref="emailInput"
                type="email"
                id="inputEmail"
                v-model="user.username"
                class="inner-input"
                required
                @focus="emailInputFocus = true"
                @blur="emailInputFocus = false"
            /></label>
          </div>
          <div @click="handleInputClick('passwordInput')" class="input-wrapper">
            <label for="inputPassword">
              <div class="label-text" :class="{ shrink: passwordInputFocus || user.password }">
                {{ $t('loginModal.password') }}
              </div>
              <input
                ref="passwordInput"
                type="password"
                id="inputPassword"
                v-model="user.password"
                class="inner-input"
                required
                @focus="passwordInputFocus = true"
                @blur="passwordInputFocus = false"
            /></label>
          </div>
          <div v-if="isError" class="login-error"></div>
          <button
            class="submit-btn"
            :style="{
              backgroundPosition: `left ${btnBgPosition.x}px top ${btnBgPosition.y}px`
            }"
            :disabled="loginLoading"
            type="submit"
            @mouseenter="handleBtnHover"
            @mousemove="handleBtnHover"
          >
            <span v-if="!loginLoading">{{ $t('loginModal.login') }}</span>
            <pulse-loader :loading="loginLoading" :color="'#ffffffee'" :size="'8px'"></pulse-loader>
          </button>
        </form>
      </template>
    </Modal>
  </div>
</template>

<script>
import Modal from '@/components/general/Modal.vue';
export default {
  // eslint-disable-next-line vue/no-unused-components
  components: { Modal },
  name: 'Login',
  data() {
    return {
      user: {
        username: null,
        password: null
      },
      emailInputFocus: false,
      passwordInputFocus: false,
      prevOverflow: null,
      btnBgPosition: { x: null, y: null },
      loginLoading: false,
      isError: false,
    };
  },
  methods: {
    signIn() {
      this.loginLoading = true;
      const api = `${process.env.VUE_APP_APIPATH}/admin/signin`;
      const vm = this;
      this.$http.post(api, vm.user).then(response => {
        if (response.data.success) {
          const token = response.data.token;
          const expired = response.data.expired;
          document.cookie = `hexToken=${token};expires=${expired}; Max-Age=${expired};`;
          const authToken = document.cookie.replace(/(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/, '$1');

          this.$http.defaults.headers.common.Authorization = authToken;

          vm.$store.commit('AuthModules/UPDATE_USER_AUTH', true);
          vm.handleClose();
          this.isError = false;
          vm.loginLoading = false;
          if (vm.$route.query.loginPath == 'manageListings') {
            vm.$nextTick(() => {
              vm.$router.push({ name: 'Products' }).catch(err => {
                console.log(err, vm.$router);
              });
            });
          }
          if (this.$route.query.nextUrl) this.$router.push(this.$route.query.nextUrl);
          else this.$router.push('/admin/products');
        } else {
          this.isError = true;
          vm.loginLoading = false;
        }
      });
    },
    handleInputClick(refName) {
      this.$refs[refName].focus();
    },
    handleClose() {
      this.$store.commit('UIModules/SET_LOGIN_MODAL_VISIBLE', false);
    },
    handleBtnHover(e) {
      this.btnBgPosition.x = e.offsetX - e.target.offsetWidth * 1.5;
      this.btnBgPosition.y = e.offsetY - e.target.offsetHeight * 1.5;
    }
  },
  mounted() {
    this.prevOverflow = document.body.style.overflow;
    document.body.style.overflow = 'hidden';
  },
  beforeDestroy() {
    document.body.style.overflow = this.prevOverflow;
    if (this.$route.query.loginPath) {
      let newQuery = Object.assign({}, this.$route.query);
      delete newQuery.loginPath;
      this.$router.replace({
        query: newQuery
      });
    }
  }
};
</script>
