<template>
  <div class="search-bar-short-container">
    <button @click="searchCollapseVisible = !searchCollapseVisible">
      <i class="el-icon-search"></i>
      {{
        searchByMap
          ? $t('homePage.searchBar.customArea')
          : searchFilter.location
          ? searchFilter.location.value
          : ''
      }}
    </button>
    <div ref="searchCollapse" class="search-collapse" :class="{ hide: !searchCollapseVisible }">
      <SearchBarStraight
        @searchSubmit="searchCollapseVisible = false"
        v-if="$route.name != 'wishList'"
      ></SearchBarStraight>
    </div>
    <transition name="el-fade-in">
      <div v-if="searchCollapseVisible" class="mask"></div>
    </transition>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import SearchBarStraight from './SearchBarStraight';
export default {
  data() {
    return {
      searchCollapseVisible: false
    };
  },
  components: { SearchBarStraight },
  computed: {
    ...mapState('UIModules', ['searchBarVisible']),
    ...mapState('SearchModules', ['searchByMap', 'searchFilter'])
  },
  watch: {
    searchCollapseVisible(newVal) {
      document.body.style.overflow = newVal ? 'hidden' : '';
      if (newVal) {
        setTimeout(() => {
          window.addEventListener('click', this.addSearchCollapseListener);
        }, 150);
      }
    }
  },
  methods: {
    addSearchCollapseListener(e) {
      if (!e.path.includes(this.$refs.searchCollapse)) {
        this.searchCollapseVisible = false;
        window.removeEventListener('click', this.addSearchCollapseListener);
      }
    }
  },
  beforeDestroy() {
    document.body.style.overflow = '';
  }
};
</script>

<style lang="scss" scoped>
.mask {
  height: 100vh;
  width: 100%;
  position: absolute;
  left: 0;
  top: 80px;
  background-color: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(3px);
}
.search-bar-short-container {
  transition: all 0.25s ease;
  display: inline-flex;
  margin: auto;
  height: 45px;
  width: 200px;
  max-width: 200px;
  button {
    width: 100%;
    height: auto;
    border-radius: 30px;
    border: 0px solid transparent;
    background-color: #fff;
    padding: 6px 24px;
    box-shadow: 0 0px 2px 1px rgba(0, 0, 0, 0.13);
    outline: unset;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}
.search-collapse {
  z-index: 1;
  transition: height 0.15s ease, opacity 0.15s ease;
  position: absolute;
  width: 100%;
  left: 0;
  top: 80px;
  border-bottom: 1px solid #dcdfe6;
  background-color: #fff;
  overflow: hidden;
  height: auto;
  padding: 30px 16px;
  &.hide {
    height: 0px;
    opacity: 0;
    padding: 0;
  }
  .strech-container {
    margin: auto;
    width: 100%;
  }
}
</style>
