import Vue from 'vue';
import Vuex from 'vuex';

import ProductsModules from './products.js';
import CartModules from './cart.js';
import AlertModules from './alert.js';
import OrdersModules from './orders.js';
import MapModules from './map.js';
import SearchModules from './search.js';
import WishlistModules from './wishlist.js';
import UIModules from './ui.js';
import ReservationModules from './reservation.js';
import AuthModules from './auth.js';

Vue.use(Vuex);

export default new Vuex.Store({
  strict: true,
  state: {
    isLoading: false,
    isLoadingQuery: 0,
    mapRef: {},
    asideScroll: null,
    showPhotoViewer: false,
    showPanoViewer: false,
    mapPageMode: null, //wishList / search
    paginatedStaySource: null,
    currentPage: 1
    // productData: {
    //   products: [],
    //   pagination: {}
    // }
  },
  actions: {
    updateLoading(context, status) {
      context.commit('LOADING', status);
    },
    saveMapRef(context, status) {
      context.commit('SAVE_MAP_REF', status);
    },
    setViewer(context, status) {
      context.commit('SET_PHOTO_VIEWER', status);
    }
  },
  mutations: {
    LOADING(state, status) {
      state.isLoading = status;
    },
    COUNT_LOADING(state, status) {
      state.isLoadingQuery += status;
    },
    SAVE_MAP_REF(state, status) {
      state.mapRef = status;
    },
    UPDATE_ASIDE_SCROLL(state, payload) {
      state.asideScroll = payload;
    },
    SET_PHOTO_VIEWER(state, payload) {
      state[payload.key] = payload.value;
    },
    CHANGE_MAP_PAGE_MODE(state, payload) {
      state.mapPageMode = payload;
    },
    CHANGE_PAGE(state, payload) {
      state.currentPage = payload;
    }
  },
  getters: {
    isLoading: state => state.isLoadingQuery > 0,
    staySource: state => {
      const wishList = state.WishlistModules.wishListProducts || [];
      const orderedWishList = wishList.slice();
      return state.mapPageMode == 'search'
        ? state.SearchModules.filterSearchProducts || []
        : state.mapPageMode == 'wishList'
        ? orderedWishList.reverse() || []
        : [];
    },
    paginatedStaySource(state, getters) {
      const setNumber = 10;
      const propertyNumber = getters.staySource.length;
      const remainder = propertyNumber % setNumber;
      const pageNumber =
        remainder === 0
          ? parseInt(propertyNumber / setNumber)
          : parseInt(propertyNumber / setNumber) + 1;
      const paginationArray = [];
      for (let i = 1; i <= pageNumber; i++) {
        let query = i * setNumber;
        let preQuery = (i - 1) * setNumber;
        const queryArray = getters.staySource.filter((property, index) => {
          return preQuery <= index && index < query;
        });
        paginationArray.push(queryArray);
      }
      // console.log(paginationArray);
      //0-9
      //10-19
      //20-25
      return paginationArray;
    }
  },
  modules: {
    ProductsModules,
    CartModules,
    AlertModules,
    OrdersModules,
    MapModules,
    SearchModules,
    WishlistModules,
    UIModules,
    ReservationModules,
    AuthModules
  }
});
