<template>
  <div class="gm-control" @mouseenter="handleHover(true)" @mouseleave="handleHover(false)">
    <div class="search-control">
      <div>
        <i class="el-icon-search"></i>
        <span v-if="hover || !wait"> {{ $t('map.searchByArea') }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      hover: false
    };
  },
  props: {
    wait: { type: Boolean, default: false },
    google: {
      type: Object,
      required: true
    },
    map: {
      type: Object,
      required: true
    }
  },
  methods: {
    handleHover(val) {
      this.hover = val;
      this.$store.dispatch('MapModules/setControlVisibleTimer');
    },
    creactCustomControl() {
      var controlDiv = document.getElementsByClassName('gm-control')[0];
      setTimeout(() => {
        this.map.controls[this.google.maps.ControlPosition.TOP_CENTER].push(controlDiv);
      }, 1500);
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.creactCustomControl();
    });
  }
};
</script>
