export default function(timestamp) {
  var date = new Date(timestamp * 1000),
    datevalues = `
      ${date.getFullYear()}  ${date.getMonth() + 1}  ${date.getDate()}  ${date.getHours()}:${
      date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes()
    }
    `;
  return datevalues;
}
