import axios from 'axios';
export default {
  namespaced: true,
  state: {
    products: null,
    pagination: {}
  },
  actions: {
    getProducts(context, [auth = '', page = 1, all = false]) {
      return new Promise(resolve => {
        const authPath = auth == 'admin' ? 'admin/' : '';
        let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/${authPath}products?page=${page}`;
        if (all) {
          api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/${authPath}products/all`;
        }
        context.commit('COUNT_LOADING', 1, { root: true });
        axios.get(api).then(response => {
          if (response.data.success) {
            if (all) {
              //, RoomManagement
              //TODO: ActionState
              const newProductsArray = [];
              const resProductsObject = response.data.products;
              for (var key in resProductsObject) {
                newProductsArray.push(resProductsObject[key]);
              }
              context.commit('PRODUCTS', newProductsArray);
            } else {
              //
              context.commit('PRODUCTS', response.data.products);
              context.commit('PAGINATION', response.data.pagination);
            }
          } else {
            this._vm.$message({
              showClose: true,
              center: true,
              type: 'danger',
              message: response.data.message + 'from getProducts2-1'
            });
          }
          context.commit('COUNT_LOADING', -1, { root: true });
          resolve();
        });
      });
      //
    },
    getMapProducts(context, [auth = '', page = 1]) {
      return new Promise(resolve => {
        const authPath = auth == 'admin' ? 'admin/' : '';
        const api = `${process.env.VUE_APP_APIPATH}/api/${
          process.env.VUE_APP_CUSTOMPATH
        }/${authPath}products${page == 'all' ? `/all` : `?page=` + page}`;
        context.commit('COUNT_LOADING', 1, { root: true });
        axios.get(api).then(response => {
          if (response.data.success) {
            //
            context.commit('PRODUCTS', response.data.products);
            context.commit('PAGINATION', response.data.pagination);
          } else {
            this._vm.$message({
              showClose: true,
              center: true,
              type: 'danger',
              message: response.data.message + 'from getMapProducts'
            });
          }
          context.commit('COUNT_LOADING', -1, { root: true });
          resolve();
        });
      });
    }
  },
  mutations: {
    PRODUCTS(state, payload) {
      state.products = payload;
    },
    PAGINATION(state, payload) {
      state.pagination = payload;
    }
  },
  getters: {
    products: state => state.products,
    pagination: state => state.pagination
  }
};
