<template>
  <el-row class="flex-row">
    <el-col :sm="24" :md="22" class="center-col">
      <el-breadcrumb class="mb-3" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <div class="text-right">
        <div style="text-align:right;margin:5px 0;min-height:42px">
          <el-button
            v-if="!state.editRoomType"
            size="medium"
            @click="$router.push({ name: 'Create', params: { isNew: true } })"
            >
          </el-button>
        </div>
      </div>
      <template>
        <el-table
          :data="tempProperties"
          max-height="480"
          class="el-table--border"
          :class="{ 'empty-loading': isLoading }"
          stripe
          style="width: 100%"
        >
          <template v-if="!isLoading">
            <el-table-column class-name="title-cloumn" label="">
              <template slot-scope="scope">
                <el-image
                  :src="scope.row.photos.normalImgs ? scope.row.photos.normalImgs[0].url : ''"
                  fit="cover"
                  :class="{ noPhoto: !scope.row.photos.normalImgs }"
                >
                  <div v-if="!scope.row.photos.normalImgs" class="placeholder" slot="error">
                    
                  </div>
                </el-image>
                <span>{{ scope.row.info.title }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="roomTypes.length" label="" width="50"> </el-table-column>
            <el-table-column prop="info.mapInfo.shortAddress" label="" width="150">
            </el-table-column>
            <el-table-column class-name="edit-cloumn" fixed="right" label="" width="50">
              <template slot-scope="scope">
                <ConfirmPopover
                  :title="scope.row.info.title"
                  :itemSrc="scope"
                  :duplicate="true"
                  @duplicateItem="handleDuplicate"
                  @deleteItem="deleteProperty"
                  @editItem="
                    $router.push({ name: 'Edit', params: { isNew: false, editItem: scope.row } })
                  "
                >
                </ConfirmPopover>
              </template>
            </el-table-column>
          </template>
          <template slot="empty">
            <div v-if="isLoading"></div>
            <div v-if="!isLoading">
              <i class="el-icon-plus"></i> []
            </div>
          </template>
          <template slot="append">
            <div v-if="isLoading" class="table-loading">
              <i class="el-icon-loading"></i> 
            </div>
          </template>
        </el-table>
        <div style="text-align:center">
          <el-pagination
            background
            layout="prev, pager, next"
            :page-count="pagination ? pagination.total_pages : 0"
            @current-change="paginationHandler"
            @prev-click="paginationHandler(pagination.current_page - 1)"
            @next-click="paginationHandler(pagination.current_page + 1)"
            :disabled="isLoading"
          >
          </el-pagination>
        </div>
      </template>
      <!-- product Modal -->
    </el-col>
  </el-row>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import $ from 'jquery';

import ConfirmPopover from '@/components/backstage/ConfirmPopover';
export default {
  components: {
    ConfirmPopover
  },
  data() {
    return {
      isNew: false,
      tempProperties: [],
      state: {
        uploadingProduct: false,
        uploadingFile: false,
        deletingProduct: false,
        editingRoomType: null,
        createRoomType: false,
        comfirmDelRoomType: false
      }
    };
  },
  computed: {
    ...mapGetters('ProductsModules', ['products', 'pagination']),
    isLoading() {
      return this.$store.state.isLoadingQuery > 0;
    }
  },
  watch: {
    products: {
      handler(newVal) {
        this.tempProperties = [];
        newVal.forEach(productObj => {
          this.tempProperties.push(productObj);
        });
      }
    }
  },
  methods: {
    ...mapActions('ProductsModules', ['getProducts']),
    paginationHandler(val) {
      this.getProducts(['admin', val]);
    },
    updateProperty() {
      this.$store.dispatch('updateLoading', true);
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`;
      const vm = this;
      let page;
      let httpMethod = 'post';
      if (!vm.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${this.tempProduct.id}`;
        httpMethod = 'put';
        page = vm.pagination.current_page;
      }
      this.state.uploadingProduct = true;
      this.$http[httpMethod](api, { data: vm.tempProduct }).then(response => {
        if (response.data.success) {
          this.$store.dispatch('ProductsModules/getProducts', ['admin', page]);
          $('#productModal').modal('hide');
          vm.clearUploadFileInput();
          this.$message({
            showClose: true,
            center: true,
            type: 'success',
            message: response.data.message
          });
        } else {
          this.$store.dispatch('ProductsModules/getProducts', ['admin', page]);
          $('#productModal').modal('hide');
          vm.clearUploadFileInput();
          this.$message({
            showClose: true,
            center: true,
            type: 'danger',
            message: response.data.message
          });
        }
        this.$store.dispatch('updateLoading', true);
      });
    },
    deleteProperty(property) {
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${property.id}`;
      const vm = this;
      this.$store.dispatch('updateLoading', true);
      this.$http.delete(api).then(response => {
        if (response.data.success) {
          this.$message({
            showClose: true,
            center: true,
            type: 'warning',
            message: `${response.data.message}: ${property.info.title}`
          });
          $('#delProductModal').modal('hide');
          vm.getProducts(['admin', vm.pagination.current_page]);
        } else {
          this.$message({
            showClose: true,
            center: true,
            type: 'danger',
            message: response.data.message
          });
        }
        this.$store.dispatch('updateLoading', false);
      });
    },
    handleDuplicate(property) {
      // eslint-disable-next-line no-unused-vars
      const duplicatedProperty = JSON.parse(JSON.stringify(property));
      duplicatedProperty.id = this.$custom.getUuid();
      duplicatedProperty.info.title += '-';
      // eslint-disable-next-line no-unreachable
      this.$store.dispatch('updateLoading', true);
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`;
      // let page;
      const vm = this;
      const httpMethod = 'post';
      this.state.uploadingProperty = true;
      this.$http[httpMethod](api, { data: duplicatedProperty }).then(response => {
        if (response.data.success) {
          this.$message({
            showClose: true,
            center: true,
            type: 'success',
            message: `${response.data.message}: ${duplicatedProperty.info.title}`
          });
          vm.tempProperties.unshift(duplicatedProperty);
        } else {
          this.$message({
            showClose: true,
            center: true,
            type: 'danger',
            message: response.data.message
          });
        }
        this.getProducts(['admin']);
      });
    }
  },
  created() {
    this.getProducts(['admin']);
  }
};
</script>
