  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
.el-table >>> .edit-cloumn .cell {
  text-align: center;
}
.el-table >>> .title-cloumn .cell {
  display: flex;
}
.el-table >>> .title-cloumn .cell .el-image {
  width: 50px;
  height: 50px;
  border-radius: 4px;
}
.el-table >>> .title-cloumn .cell span {
  margin: auto 5px;
}
