<script>
export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    detail: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  methods: {
    handleClose() {
      this.$emit('update:visible', false);
    }
  },
  beforeDestroy() {
    this.$emit('update:visible', false);
  }
};
</script>

