<template>
  <button
    @click="handleMapVisible"
    v-if="!_windowSize.mapPointLg.larger || !asideVisible"
    class="map-btn"
    aria-label="Open the map"
  >
    <template v-if="asideVisible"><i class="el-icon-map-location"></i><span></span></template>
    <template v-if="!asideVisible"><i class="el-icon-notebook-2"></i><span></span></template>
  </button>
</template>

<script>
import { mapState } from 'vuex';
export default {
  computed: {
    ...mapState('UIModules', ['_windowSize', 'asideVisible'])
  },
  methods: {
    handleMapVisible() {
      this.$store.commit('UIModules/SET_ASIDE_VISIBLE', !this.asideVisible);
    }
  }
};
</script>

<style lang="scss" scoped>
/* map switch button */
.map-btn {
  transition: all 0.3s ease;
  position: absolute;
  right: 50%;
  bottom: 12px;
  transform: translate(50%, 0);
  border: none;
  font-size: 18px;
  border-radius: 30px;
  padding: 8px 24px;
  z-index: 5;
  color: #fff;
  background-color: rgb(36, 36, 36);
  box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.2);
  span {
    font-weight: 500;
    margin-left: 6px;
    font-size: 16px;
  }
}
.map-btn:hover {
  transform: translate(50%, 0) scale(1.03);
}
.map-btn:active,
.map-btn:focus {
  outline: none;
}
</style>
