export default {
  namespaced: true, //WishlistModules
  state: {
    wishList: null,
    wishListProducts: null,
    wishListProductsBounds: null,
    wishListRoomTypeNumber: null
  },
  actions: {
    getWishList(context) {
      if (!localStorage.wishList) {
        const newWishList = [];
        localStorage.setItem('wishList', JSON.stringify(newWishList));
      }
      const wishList = JSON.parse(localStorage.getItem('wishList'));
      context.commit('SET_WISHLIST', wishList);
    },
    handleWishList(context, [productId, mapPageMode]) {
      context.commit('SET_WISHLIST_PRODUCTS', []);
      const localWishList = JSON.parse(localStorage.getItem('wishList'));
      if (localWishList.includes(productId)) {
        const itemIndex = localWishList.indexOf(productId);
        localWishList.splice(itemIndex, 1);
      } else {
        localWishList.push(productId);
      }
      localStorage.setItem('wishList', JSON.stringify(localWishList));
      context.commit('SET_WISHLIST', localWishList);
      if (mapPageMode == 'wishList') {
        context.dispatch('mapWishListProducts');
      }
    },
    async mapWishListProducts(context) {
      context.commit('SET_WISHLIST_PRODUCTS', []);
      const vm = this;
      context.dispatch('getWishList');
      let roomTypeNumber = 0;
      if (!context.rootState.ProductsModules.products) {
        await context.dispatch('ProductsModules/getProducts', [null, 'all'], {
          root: true
        });
      }
      const localWishList = localStorage.wishList
        ? JSON.parse(localStorage.getItem('wishList'))
        : [];
      const products = context.rootState.ProductsModules.products;
      if (localWishList.length > 0) {
        const mappedProducts = localWishList.reduce((mapped, wishId) => {
          const matchProduct = products.find(obj => obj.id == wishId);
          if (!matchProduct) {
            //localStorageId
            const itemIndex = localWishList.indexOf(wishId);
            localWishList.splice(itemIndex, 1);
            localStorage.setItem('wishList', JSON.stringify(localWishList));
            return mapped;
          }
          const { sortedPrices, matchRoomTypesPrices } = handlePrices(matchProduct.roomTypes);
          roomTypeNumber += matchProduct.roomTypes.length;
          matchProduct.computedResult = {
            matchRoomTypes: matchProduct.roomTypes,
            prices: matchRoomTypesPrices,
            sortedPrices
          };
          mapped.push(matchProduct);
          return mapped;
        }, []);
        const allLatLng = mappedProducts.map(obj => obj.info.LatLng);
        //debug: MapModulesgetGoogleAPI resolvemapMode.vuewatch google
        const bounds = new context.rootState.MapModules.google.maps.LatLngBounds();
        //
        for (let i = 0; i < allLatLng.length; i++) {
          bounds.extend(allLatLng[i]);
        }
        context.commit('SET_WISHLIST_PRODUCTS_ROOMTYPE_NUMBER', roomTypeNumber);
        context.commit('SET_WISHLIST_PRODUCTS_BOUNDS', bounds);
        context.commit('SET_WISHLIST_PRODUCTS', mappedProducts);
      } else {
        context.commit('SET_WISHLIST_PRODUCTS', []);
      }

      // utility
      function handlePrices(roomTypes) {
        //
        const matchRoomTypesPrices = [];
        roomTypes.forEach(element => {
          matchRoomTypesPrices.push(element.price);
        });
        const sortedPrices = vm._vm.$custom.quickSort(matchRoomTypesPrices);
        return { matchRoomTypesPrices, sortedPrices };
      }
    }
  },
  mutations: {
    SET_WISHLIST(state, payload) {
      state.wishList = payload;
    },
    SET_WISHLIST_PRODUCTS(state, payload) {
      state.wishListProducts = payload;
    },
    SET_WISHLIST_PRODUCTS_BOUNDS(state, payload) {
      state.wishListProductsBounds = payload;
    },
    SET_WISHLIST_PRODUCTS_ROOMTYPE_NUMBER(state, payload) {
      state.wishListRoomTypeNumber = payload;
    }
  },
  getters: {
    // eslint-disable-next-line no-unused-vars
    wishListProductsGetter: (state, getters, rootState) => {
      return state.map;
    }
  }
};
