<template>
  <el-container ref="container">
    <keep-alive>
      <el-aside
        v-if="asideVisible && (mapPageMode == 'search' ? searchKey : true)"
        ref="mapAside"
        :width="
          _windowSize.mapPointLg.larger == true ? (mapProduct.select ? '1000px' : '800px') : '100%'
        "
        :class="{ hideOverflow: showPhotoViewer || showPanoViewer }"
      >
        <MapProducts v-if="!mapProduct.select" />
        <MapProduct v-if="mapProduct.select" class="mapProduct" />
      </el-aside>
    </keep-alive>
    <el-main>
      <!-- <Map ref="mapComponent" /> -->
      <SearchResultMap />
    </el-main>
    <MapSwitchButton
      v-if="
        (!_windowSize.mapPointLg.larger && mapPageMode == 'search' ? searchKey : true) ||
          (_windowSize.mapPointLg.larger && !asideVisible)
      "
    ></MapSwitchButton>
  </el-container>
</template>

<script>
import SearchResultMap from '@/components/forestage/SearchResultMap';
import MapProducts from '@/components/forestage/MapProducts';
import MapProduct from '@/components/forestage/MapProduct';
import MapSwitchButton from '@/components/forestage/MapSwitchButton';
import { mapState } from 'vuex';
export default {
  data() {
    return {};
  },
  components: { SearchResultMap, MapProducts, MapProduct, MapSwitchButton },
  computed: {
    ...mapState('MapModules', ['mapProduct', 'google']),
    ...mapState(['showPhotoViewer', 'showPanoViewer', 'mapPageMode']),
    ...mapState('SearchModules', [
      'filterSearchProducts',
      'searchKey',
      'searchQuery',
      'searchExecuted'
    ]),
    ...mapState('WishlistModules', ['wishListProducts']),
    ...mapState('UIModules', ['_windowSize', 'asideVisible'])
  },
  watch: {
    'mapProduct.select': {
      handler(newVal) {
        if (newVal) {
          this.$store.commit('UIModules/SET_ASIDE_VISIBLE', true);
          this.$nextTick(() => {
            if (this.$refs.mapAside) {
              this.$refs.mapAside.$el.scrollTop = 0;
              this.$store.commit('UPDATE_ASIDE_SCROLL', 0);
            }
          });
        }
      },
      deep: true
    },
    google(newVal) {
      if (newVal && this.$route.name == 'wishList')
        this.$store.dispatch('WishlistModules/mapWishListProducts');
    },
    '$route.name': {
      handler(val) {
        if (val == 'search') {
          this.$store.commit('UIModules/SHOW_SEARCHBAR', true);
        }
        this.$store.commit('CHANGE_MAP_PAGE_MODE', val);
        if (this.google) {
          this.$store.dispatch('WishlistModules/mapWishListProducts');
        }
        if (this.searchQuery && val == 'search') {
          this.$router.replace({ query: this.searchQuery }).catch(err => {
            err;
          });
        }
      },
      immediate: true
    }
  },
  methods: {
    updateAsideScroll() {
      this.$store.commit('UPDATE_ASIDE_SCROLL', this.$refs.mapAside.$el.scrollTop);
    },
    async handleSearchByQuery() {
      this.$store.commit('SearchModules/SET_SEARCH_QUERY', this.$route.query);
      if (
        !this.searchExecuted &&
        this.$route.query.search_bounds_s &&
        this.$route.query.search_bounds_w &&
        this.$route.query.search_bounds_e &&
        this.$route.query.search_bounds_n
      ) {
        // console.log('handleSearchByQuery');
        const searchLatLngBounds = {
          south: parseFloat(this.$route.query.search_bounds_s),
          west: parseFloat(this.$route.query.search_bounds_w),
          north: parseFloat(this.$route.query.search_bounds_n),
          east: parseFloat(this.$route.query.search_bounds_e)
        };
        const sw = await new this.google.maps.LatLng({
          lat: searchLatLngBounds.west,
          lng: searchLatLngBounds.south
        });
        const ne = await new this.google.maps.LatLng(
          searchLatLngBounds.east,
          searchLatLngBounds.north
        );
        const bounds = await new this.google.maps.LatLngBounds();
        bounds.extend(sw);
        bounds.extend(ne);

        this.$store.dispatch('SearchModules/handleQuerySearch', {
          routeQuery: this.$route.query,
          bounds
        });
      }
    },
    handleSelectStayQuery(newVal) {
      //stayIdquery
      if (newVal && !this.$route.query.stayId) {
        const newQuery = JSON.parse(JSON.stringify(this.$route.query));
        newQuery.stayId = newVal.id;
        this.$router.replace({ query: newQuery });
      } else if (!newVal) {
        const newQuery = JSON.parse(JSON.stringify(this.$route.query));
        if (newQuery.stayId) {
          delete newQuery.stayId;
          this.$router.replace({ query: newQuery });
        }
      }
    }
  },
  created() {
    if (this.$route.query.stayId) {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${this.$route.query.stayId}`;
      this.$http.get(api).then(response => {
        if (response.data.success == true) {
          vm.$store.dispatch('MapModules/selectMapProduct', [response.data.product, false]);
        }
      });
      // this.$store.dispatch('MapModules/selectMapProduct', [product, false]);
    }
  },
  mounted() {
    this.$store.dispatch('WishlistModules/getWishList');
    this.$bus.$on('addProductScrollListener', () => {
      this.$nextTick(() => {
        this.$refs.mapAside.$el.scrollTop = 0;
        this.$refs.mapAside.$el.addEventListener('scroll', this.updateAsideScroll);
      });
    });
    this.$bus.$on('destroyProductScrollListener', () => {
      this.$store.commit('UPDATE_ASIDE_SCROLL', this.$refs.mapAside.$el.scrollTop);
      this.$refs.mapAside.$el.removeEventListener('scroll', this.updateAsideScroll);
    });
    this.$bus.$on('handleScrollTo', val => {
      this.$refs.mapAside.$el.scrollTop = val;
    });
    this.$bus.$on('mapLoaded', () => {
      this.handleSearchByQuery();
    });
  },
  beforeDestroy() {
    // this.$store.commit('SearchModules/SEARCH_EXECUTED', false);
    // this.$store.dispatch('MapModules/selectMapProduct', [null]);
    this.$store.commit('UIModules/SHOW_SEARCHBAR', false);
    this.$bus.$off('addProductScrollListener');
    this.$bus.$off('destroyProductScrollListener');
    this.$bus.$off('handleScrollTo');
    this.$bus.$off('mapLoaded');
  }
};
</script>
<style lang="scss" scoped src="@/assets/css/components/_MapMode.scss"></style>
