<template>
  <div>
    <el-button style="width:100%" @click="openMapDialog"></el-button>
    <el-dialog
      id="map-dialog"
      :visible.sync="mapDialogVisible"
      :lock-scroll="true"
      width="75%"
      custom-class="map-dialog"
      :center="true"
      top="40px"
    >
      <el-button aria-label="Close" class="el-dialog__headerbtn" @click="$emit('closeDialog')">
        <i class="el-dialog__close el-icon el-icon-close"></i>
      </el-button>
      <GoogleMapLoader
        class="google-map-loader"
        ref="mapLoader"
        :initGeolocation="true"
        :mapConfig="searchMapConfig"
        apiKey="AIzaSyDTP6E9ofDGuoa6fo0egyM8CSOKQ4cGVAg"
        @update:click="updateClick"
      >
      </GoogleMapLoader>
      <div class="buttons">
        <el-row :gutter="1">
          <el-col :xs="24" :md="24" class="my-2">
            <customSelect
              id="map-select"
              filterable
              remote
              clearable
              @change="mapSelectionOn"
              @clear="
                mapSelections = null;
                searchByClick = false;
              "
              :remote-method="addressToGocode"
              v-model="mapSelect"
              placeholder=""
              no-match-text="Enter"
              :no-data-text="noDataText"
            >
              <el-option
                icon="el-icon-search"
                v-for="item in mapSelections"
                :key="item.place_id"
                :label="item.formatted_address"
                :value="item.formatted_address"
                :latLng="item"
              ></el-option>
            </customSelect>
          </el-col>
          <el-col :xs="24" :md="24" class="my-2 text-right">
            <el-button @click="mapDialogVisible = false"> </el-button>
            <el-button
              @click="
                $emit('updateAddress', mapInfo);
                mapDialogVisible = false;
              "
              ></el-button
            >
          </el-col>
        </el-row>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import GoogleMapLoader from './GoogleMapLoader';
import { mapSettings } from '@/constants/mapSettings.js';
import customSelect from '@/components/forestage/CustomSelect';
import $ from 'jquery';
export default {
  props: {
    // mapDialogVisible: Boolean
  },
  data() {
    return {
      // UI component data
      mapDialogVisible: false,
      noDataText: 'Enter',
      searchChange: false,
      searchByClick: false,
      searchVal: null,
      mapSelectLatlng: null,
      mapSelect: null,
      mapInfo: {},
      mapSelections: [],
      marker: null
    };
  },
  computed: {
    searchMapConfig() {
      return {
        ...mapSettings,
        clickableIcons: false,
        streetViewControl: false,
        fullscreenControl: false,
        center: { lat: 25.033976, lng: 121.5645389 }
      };
    }
  },
  components: { GoogleMapLoader, customSelect },
  methods: {
    openMapDialog() {
      this.mapDialogVisible = true;
    },
    updateClick(e) {
      this.placeMarker(e.latLng, this.$refs.mapLoader.map);
      this.latLngToAddress(e.latLng);
      this.searchByClick = true;
      $('#map-select').focus();
    },
    mapSelectionOn(item) {
      this.mapInfo = item;
      this.$refs.mapLoader.map.panTo(item.geometry.location);
      this.placeMarker(item.geometry.location, this.$refs.mapLoader.map, this.searchByClick);
    },
    addressToGocode(val) {
      this.mapSelections = null;
      this.searchByClick = false;
      let newSearchVal = val;
      if (newSearchVal == '' || newSearchVal == this.searchVal) {
        this.noDataText = 'Enter';
        this.searchChange = false;
        return;
      }
      this.searchVal = newSearchVal;
      const vm = this;
      this.$refs.mapLoader.geocoder.geocode({ address: vm.searchVal }, (results, status) => {
        if (status === 'OK') {
          const newArray = [];
          for (let i = 0; i < results.length; i++) {
            newArray.push(results[i]);
          }
          vm.mapSelections = newArray;
        } else {
          console.log('not OK', results, status);
          if (results.length == 0) this.noDataText = '';
        }
      });
    },
    latLngToAddress(latLng) {
      const vm = this;
      // console.log('latLng', latLng);
      this.$refs.mapLoader.geocoder.geocode({ location: latLng }, (results, status) => {
        // console.log('noCondition', results);
        if (status === 'OK') {
          console.log('OK', results);
          //Array3,4
          const newArray = [];
          for (let i = 0; i < results.length / 4; i++) {
            newArray.push(results[i]);
          }
          vm.mapSelections = newArray;
        } else {
          // console.log('not OK', results);
        }
      });
    },
    placeMarker(latLng, map, ifClick = false) {
      const vm = this;
      if (this.marker) this.removeMarkers();
      var marker;
      if (!ifClick) {
        marker = new this.$refs.mapLoader.google.maps.Marker({
          position: latLng,
          map: map,
          animation: vm.$refs.mapLoader.google.maps.Animation.DROP
        });
      } else {
        marker = new this.$refs.mapLoader.google.maps.Marker({
          position: latLng,
          map: map,
          animation: vm.$refs.mapLoader.google.maps.Animation.BOUNCE
        });
        this.searchByClick = false;
      }
      this.marker = marker;
      // axios.get(api).then(response => {});
    },
    removeMarkers() {
      this.marker.setMap(null);
    }
  }
};
</script>

<style scoped>
.el-select {
  width: 100%;
}
.el-dialog__wrapper {
  overflow: auto;
  scrollbar-width: 0;
  -ms-overflow-style: none;
}
.el-dialog__wrapper::-webkit-scrollbar {
  display: none;
}

.el-dialog__wrapper >>> .map-dialog .el-dialog__body {
  padding: 0 !important;
}
.el-dialog__wrapper >>> .map-dialog .el-dialog__header {
  display: none !important;
}
.el-dialog__wrapper >>> .map-dialog .buttons {
  padding: 10px;
}
.google-map-loader {
  height: 75vh;
}
</style>
