export default {
  namespaced: true, //SearchModules
  state: {
    filterSearchProducts: null,
    searchFilter: {
      location: null,
      pplNum: 0,
      date: null
    },
    searchByMap: false,
    searchBounds: null,
    searchCenter: null,
    searchZoom: null,
    resultsLatLng: null,
    filteredProductsBounds: null,
    roomTypeNumber: null,
    searchKey: null,
    searchQuery: null,
    searchExecuted: false,
    searchByQuery: null,
    searchType: null
  },
  actions: {
    async filterSearchProducts(
      context,
      { searchByMap = false, searchByQuery = false, bounds = null, searchType = null }
    ) {
      //query
      context.commit('SEARCH_EXECUTED', true);
      context.commit('SEARCH_BY_MAP', searchByMap);
      const vm = this;
      let roomTypeNum = 0;
      context.dispatch('MapModules/selectMapProduct', [null, false], { root: true });
      await context.dispatch('ProductsModules/getMapProducts', [null, 'all'], { root: true });
      const mapModulesState = context.rootState.MapModules;
      const products = context.rootState.ProductsModules.products;
      const pplNumFilter = context.state.searchFilter.pplNum;
      let filterResult;
      if (searchByMap && !searchByQuery) {
        bounds = mapModulesState.bounds;
      } else if (!searchByQuery) {
        bounds =
          context.state.searchFilter.location.mapInfo.geometry.bounds ||
          context.state.searchFilter.location.mapInfo.geometry.viewport;
      } else if (searchByQuery) {
        // bounds = context.state.searchByQuery.bounds;
      }
      // const center = searchByMap
      //   ? mapModulesState.center
      //   : context.state.searchFilter.location.mapInfo.geometry.location;
      // const zoom = searchByMap ? mapModulesState.zoom : null;
      context.commit('SET_SEARCH_BOUNDS', { bounds });

      //TODO:
      // console.log('mapInfo', context.state.searchFilter.location.mapInfo);
      // if (!searchByMap) {
      //   const countryCode = context.state.searchFilter.location.mapInfo.address_components.find(
      //     element => element.types.includes('country')
      //   ).short_name;
      //   console.log(countryCode);
      // }

      //
      // const filteredProductsByLocation = products.filter(
      //   obj => mapModulesState.bounds.contains(obj.info.LatLng) === true
      // );
      const filteredProductsByLocation = products.reduce((filtered, product) => {
        // console.log(
        //   'bounds',
        //   bounds.Va.i,
        //   bounds.Va.j,
        //   bounds.Za.i,
        //   bounds.Za.j,
        //   'product.info.LatLng',
        //   product.info.LatLng.lat,
        //   'contain',
        //   bounds.contains(product.info.LatLng)
        // );
        if (bounds.contains(product.info.LatLng) === true) {
          if (!pplNumFilter) {
            const { matchRoomTypesPrices, sortedPrices } = handlePrices(product.roomTypes);
            //TODO:
            const sortedRoomTypes = vm._vm.$custom.quickSortObj(product.roomTypes, 'pplNum');
            // console.log('sortedRoomTypes', sortedRoomTypes, product.roomTypes);

            //map
            product.computedResult = {
              matchRoomTypes: sortedRoomTypes,
              prices: matchRoomTypesPrices,
              sortedPrices
            };
            //
            roomTypeNum += product.roomTypes.length;
          }
          filtered.push(product);
        }
        return filtered;
      }, []);
      //filterResult
      filterResult = filteredProductsByLocation;

      //
      if (pplNumFilter) {
        //reduce filterpushaccumulator
        const filteredProductsByPplNum = filteredProductsByLocation.reduce((filtered, product) => {
          //
          const matchRoomTypes = product.roomTypes.filter(roomType => {
            return pplNumFilter + 1 >= roomType.pplNum && roomType.pplNum >= pplNumFilter;
          });
          //
          roomTypeNum += matchRoomTypes.length;
          //
          if (matchRoomTypes.length > 0) {
            const { matchRoomTypesPrices, sortedPrices } = handlePrices(matchRoomTypes);
            //TODO:
            const sortedRoomTypes = vm._vm.$custom.quickSortObj(matchRoomTypes, 'pplNum');

            //map
            product.computedResult = {
              matchRoomTypes: sortedRoomTypes,
              prices: matchRoomTypesPrices,
              sortedPrices
            };
            filtered.push(product);
          }
          return filtered;
        }, []);

        //filterResult
        filterResult = filteredProductsByPplNum;
      }
      //TODO: 
      const allLatLng = filterResult.map(obj => obj.info.LatLng);
      context.commit('SAVE_RESULTS_LATLNG', allLatLng);
      context.commit('SAVE_FILTERED_PRODUCTS', filterResult);
      //
      context.commit('SAVE_ROOMTYPES_NUMBER', roomTypeNum);
      context.commit('GENERATE_SEARCH_KEY', vm._vm.$custom.getUuid());

      function handlePrices(roomTypes) {
        //
        const matchRoomTypesPrices = [];
        roomTypes.forEach(element => {
          matchRoomTypesPrices.push(element.price);
        });
        const sortedPrices = vm._vm.$custom.quickSort(matchRoomTypesPrices);
        return { matchRoomTypesPrices, sortedPrices };
      }
      context.commit('SET_SEARCH_TYPE', searchType);
    },
    passSearchLocation(context, LocationInfo) {
      //
      context.commit('SAVE_SEARCH_LOCATION', LocationInfo);
    },
    async handleQuerySearch(context, { routeQuery, bounds }) {
      if (routeQuery.search_by_map == 'true') {
        // console.log('searchLatLngBounds', searchLatLngBounds);
      } else {
        context.commit('SAVE_SEARCH_LOCATION', {
          value: routeQuery.place_name,
          mapInfo: { geometry: { viewport: bounds } }
        });
      }
      if (routeQuery.startDate && routeQuery.endDate) {
        const tempDateArray = [routeQuery.startDate, routeQuery.endDate];
        context.commit('updateDate', tempDateArray);
      }
      context.commit('updatePplNum', routeQuery.guest_number);
      context.commit('SET_SEARCH_BY_QUERY', { bounds, routeQuery });
      // context.dispatch('filterSearchProducts', { searchByQuery: true });
    }
  },
  mutations: {
    SEARCH_BY_MAP(state, payload) {
      state.searchByMap = payload;
    },
    SAVE_FILTERED_PRODUCTS(state, payload) {
      state.filterSearchProducts = payload;
    },
    SAVE_SEARCH_LOCATION(state, payload) {
      state.searchFilter.location = payload;
    },
    updatePplNum(state, payload) {
      state.searchFilter.pplNum = payload;
    },
    updateDate(state, payload) {
      state.searchFilter.date = payload;
    },
    SAVE_RESULTS_LATLNG(state, payload) {
      state.resultsLatLng = payload;
    },
    SAVE_FILTERED_PRODUCTS_BOUNDS(state, payload) {
      state.filteredProductsBounds = payload;
    },
    SAVE_ROOMTYPES_NUMBER(state, payload) {
      state.roomTypeNumber = payload;
    },
    GENERATE_SEARCH_KEY(state, payload) {
      state.searchKey = payload;
    },
    SET_SEARCH_BOUNDS(state, payload) {
      state.searchBounds = payload.bounds;
      // state.searchCenter = payload.center;
      // state.searchZoom = payload.zoom;
    },
    SET_SEARCH_QUERY(state, payload) {
      state.searchQuery = payload;
    },
    SEARCH_EXECUTED(state, payload) {
      state.searchExecuted = payload;
    },
    SET_SEARCH_BY_QUERY(state, payload) {
      state.searchByQuery = payload;
    },
    SET_SEARCH_TYPE(state, payload) {
      state.searchType = payload;
    }
  }
};
