<template>
  <el-row :gutter="8">
    <el-col :xs="24" :sm="24">
      <el-card shadow="hover" class="image-card">
        <template slot="header">
          <span class="el-form-item__label">
            
          </span>
          <el-button
            class="cover-img-input-trigger"
            style="margin:auto 0;"
            icon="el-icon-upload"
            size="small"
            @click="$refs.photoImgs.click()"
            ></el-button
          >
        </template>
        <el-row :gutter="8">
          <el-col :xs="12" :sm="6" v-for="(item, index) in photoSrc.normalImgs" :key="item.id">
            <EditImage
              :uploading="item.uploading"
              :imgSrc="item.url"
              :previewSrcList="imgPrewviewSrcList"
              @delete="removeImg(index, 'normalImg')"
            ></EditImage>
          </el-col>
          <el-col
            v-if="photoSrc.normalImgs ? photoSrc.normalImgs.length < 1 : false"
            style="color: #C0C4CC;text-align:center;padding:10%"
            :span="24"
          >
            
          </el-col>
        </el-row>
      </el-card>
      <el-card shadow="hover" class="image-card">
        <template slot="header">
          <span class="el-form-item__label">
            
          </span>
          <el-button
            class="cover-img-input-trigger"
            style="margin-left:5px"
            icon="el-icon-upload"
            size="small"
            @click="$refs.panoImgs.click()"
            ></el-button
          >
        </template>
        <el-row :gutter="8">
          <el-col :xs="12" :sm="8" v-for="(item, index) in photoSrc.panoramas" :key="item.id">
            <EditImage
              :panoramas="true"
              :panoramasData="item.uploading ? {} : item.data"
              :uploading="item.uploading"
              :imgSrc="item.data.display_url"
              @delete="removeImg(index, 'panoramas')"
            ></EditImage>
            <!-- <div class="img-container">
                    <el-image
                      style="width: 100%; height: 100%"
                      :src="item.data.display_url"
                      fit="cover"
                    ></el-image>
                    <span class="img-mask"></span>
                  </div> -->
          </el-col>
          <el-col
            v-if="photoSrc.panoramas ? photoSrc.panoramas.length < 1 : true"
            style="color: #C0C4CC;text-align:center;padding:10%"
            :span="24"
          >
            
          </el-col>
        </el-row>
      </el-card>
    </el-col>
    <div style="display:none;">
      <input type="text" id="image" v-model="photoSrc.coverUrl" />
      <input type="file" id="customFile" ref="photoImgs" @change="uploadFile" />
      <input type="file" id="panoFile" ref="panoImgs" @change="uploadPano" />
    </div>
  </el-row>
</template>

<script>
import EditImage from './EditImage';
import imageUploadMixins from '@/mixins/image-compress';
export default {
  components: {
    EditImage
  },
  mixins: [imageUploadMixins],
  props: {
    photoSrc: {
      type: Object,
      default: () => {
        return { normalImgs: [], panoramas: [] };
      },
      required: true
    },
    uploadingFile: {
      type: Number,
      required: true
    }
  },
  computed: {
    imgPrewviewSrcList() {
      const listArray = this.photoSrc.normalImgs.map(obj => obj.url);

      return listArray;
    }
  },
  created() {
    if (!this.photoSrc.normalImgs) {
      this.$set(this.photoSrc, 'normalImgs', []);
      // this.photoSrc.normalImgs = [];
    }
    if (!this.photoSrc.panoramas) {
      this.$set(this.photoSrc, 'panoramas', []);
      // this.photoSrc.panoramas = [];
    }
  },
  methods: {
    notifyMessage(title, message, type, duration = 5000) {
      this.$notify({
        title,
        message,
        type,
        duration
      });
    },
    async uploadPano() {
      this.$emit('update:uploadingFile', this.uploadingFile + 1);
      const uploadFile = this.$refs.panoImgs.files[0];
      const inputRefFiles = this.$refs.panoImgs.files;
      if (uploadFile === undefined) return;
      const vm = this;
      const loadImg = uploadFile => {
        return new Promise(resolve => {
          const _URL = window.URL || window.webkitURL;
          let img = new Image();
          var objectUrl = _URL.createObjectURL(uploadFile);
          img.src = objectUrl;
          img.onload = function() {
            resolve({ img, objectUrl });
          };
        });
      };
      const { img, objectUrl } = await loadImg(uploadFile);
      //
      if (!(img.width == img.height * 2)) {
        vm.notifyMessage('', '2:1', 'warning');
        return;
      }
      const api =
        'http://localhost:8080/api/https://api.imgbb.com/1/upload?key=dd68d6f707987b863567c93d13c0f472';
      const newObjId = vm.$custom.getUuid();
      this.checkAndHandleCompression(uploadFile).then(fileOptions => {
        const formData = new FormData();
        formData.append('image', fileOptions.uploadFile);
        const panoObj = {
          uploading: true,
          id: newObjId,
          title: '',
          data: {
            display_url: objectUrl
          }
        };
        vm.photoSrc.panoramas.push(panoObj);
        handleUpload(formData, newObjId, objectUrl);
      });
      function handleUpload(formData, objId, imgUrl) {
        // console.log('');
        let _URL = window.URL || window.webkitURL;
        vm.$http
          .post(api, formData, {
            headers: {
              'Access-Control-Allow-Origin': '*',
              'Content-Type': 'multipart/form-data'
            }
          })
          .then(response => {
            if (response.data.success) {
              const imgObj = vm.photoSrc.panoramas.find(obj => obj.id == objId);
              imgObj.data = response.data.data;
              delete imgObj.uploading;
              _URL.revokeObjectURL(imgUrl);
              vm.clearUploadFileInput(inputRefFiles);
            }
            this.$emit('update:uploadingFile', this.uploadingFile - 1);
          });
      }
    },
    removeImg(index, type) {
      if (type == 'normalImg') this.photoSrc.normalImgs.splice(index, 1);
      else if (type == 'panoramas') this.photoSrc.panoramas.splice(index, 1);
    },
    uploadFile() {
      this.$emit('update:uploadingFile', this.uploadingFile + 1);
      const uploadFile = this.$refs.photoImgs.files[0];
      const inputRefFiles = this.$refs.photoImgs.files;
      if (uploadFile === undefined) return;
      const _URL = window.URL || window.webkitURL;
      const newObjId = this.$custom.getUuid();
      const objectUrl = _URL.createObjectURL(uploadFile);
      const imgObj = {
        id: newObjId,
        uploading: true,
        title: '',
        url: objectUrl
      };
      this.photoSrc.normalImgs.push(imgObj);

      const formData = new FormData();
      formData.append('file-to-upload', uploadFile);
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`;

      vm.$http
        .post(api, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        .then(response => {
          if (response.data.success) {
            const imgObj = this.photoSrc.normalImgs.find(obj => obj.id == newObjId);
            imgObj.url = response.data.imageUrl;
            delete imgObj.uploading;
            _URL.revokeObjectURL(objectUrl);
            vm.clearUploadFileInput(inputRefFiles);
          } else {
            this.$bus.$emit('message:push', response.data.message, 'danger');
          }
          this.$emit('update:uploadingFile', this.uploadingFile - 1);
        });
    },
    clearUploadFileInput(inputRefFiles) {
      inputRefFiles = [];
      inputRefFiles.type = 'text';
      inputRefFiles.type = 'file';
    }
  }
};
</script>

