<template>
  <div class="search-bar-container">
    <CustomAutocomplete
      ref="searchRef"
      :placeholder="
        large
          ? _windowSize.md.larger
            ? $t('homePage.searchBar.location_long')
            : $t('homePage.searchBar.location_short')
          : _windowSize.lg.larger
          ? $t('homePage.searchBar.location_short')
          : $t('homePage.searchBar.location_short')
      "
      @blur="handleBlur"
      @select="handleSelect"
      @input="handleInput"
      @KeyEnter="handleKeyEnter"
      v-model="query"
      placement="bottom"
      :noData="noData"
      :loading.sync="suggestionLoading"
      :fetch-suggestions="querySearchAsync"
      :suggestions.sync="suggestions"
      :popper-append-to-body="false"
      class="search-bar-group"
      :class="{
        'input-active': activeInput,
        'btn-active': !activeInput,
        'large-search-bar': large
      }"
    >
      <el-button
        v-if="!activeInput"
        slot="append"
        @click="changeInput(true)"
        class="input-btn"
        :class="{ notFill: !searchByMap || $route.name == 'homePage' }"
      >
        <span class="label" v-if="large">{{ $t('homePage.searchBar.location') }}</span>
        <span>{{ tempSelectLocation ? tempSelectLocation.value : searchBarTitle }}</span>
      </el-button>
      <el-popover
        slot="append"
        placement="bottom"
        width="400"
        trigger="click"
        class="ppl-popover-wrapper"
        popper-class="ppl-popover"
      >
        <div class="input-wrapper">
          <el-date-picker
            v-model="computedDate"
            type="daterange"
            size="small"
            align="center"
            range-separator="-"
            value-format="yyyy/M/d"
            :start-placeholder="$t('homePage.searchBar.checkInDates')"
            :end-placeholder="$t('homePage.searchBar.checkOutDates')"
            :picker-options="datePickerOptions"
          >
          </el-date-picker>
        </div>
        <el-button class="ppl-popover-btn" :class="{ notFill: !computedDate }" slot="reference">
          <div class="label" v-if="large">{{ $t('homePage.searchBar.date') }}</div>
          {{
            displayDate
              ? displayDate[0] + ' - ' + displayDate[1]
              : $t('homePage.searchBar.addDates')
          }}
        </el-button>
      </el-popover>
      <el-popover
        slot="append"
        placement="bottom"
        width="250"
        trigger="click"
        class="ppl-popover-wrapper"
        popper-class="ppl-popover"
      >
        <div class="input-wrapper">
          <span class="label">{{ $t('homePage.searchBar.peopleNum_short') }}</span>
          <el-input-number v-model="pplNum" size="mini" :min="0"></el-input-number>
        </div>
        <el-button class="ppl-popover-btn" :class="{ notFill: !pplNum }" slot="reference">
          <div class="label" v-if="large">{{ $t('homePage.searchBar.guests') }}</div>
          {{
            pplNum
              ? pplNum +
                (pplNum > 1
                  ? $t('homePage.searchBar.peopleUnits')
                  : $t('homePage.searchBar.peopleUnit'))
              : _windowSize.md.larger
              ? $t('homePage.searchBar.peopleNum')
              : $t('homePage.searchBar.peopleNum_short')
          }}</el-button
        >
      </el-popover>
      <el-button
        slot="append"
        :icon="large ? '' : 'el-icon-search'"
        :class="{ ready: selectLocation.mapInfo, 'search-btn': large }"
        @click="selectLocation.mapInfo ? handleSearch() : leadToInput()"
      >
        <template v-if="large">
          <span
            class="search-btn-inner"
            :style="{
              backgroundPosition: `left ${btnBgPosition.x}px top ${btnBgPosition.y}px`
            }"
            @mouseenter="handleSearchBtnHover"
            @mousemove="handleSearchBtnHover"
          >
            <span> <i class="el-icon-search"></i></span>
          </span>
        </template>
      </el-button>

      <el-tooltip
        slot="suffix"
        class="tipBtn"
        effect="dark"
        :content="$t('homePage.searchBar.tipTooltip')"
        placement="bottom"
        :disabled="!activeEnterTip"
        :class="{ tipActive: activeEnterTip }"
      >
        <span @click="handleTipClick">
          <i v-if="suggestionLoading" class="el-icon-loading"></i>
          {{ suggestionLoading ? '' : '' }}
        </span>
      </el-tooltip>
