<script>
import CustomAutocomplete from '@/components/general/customAutocomplete/CustomAutocomplete';
import { mapState } from 'vuex';
import searchMixin from '@/mixins/searchMixin';
export default {
  components: { CustomAutocomplete },
  mixins: [searchMixin],
  props: {
    large: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      noData: false,
      suggestions: [],
      resultArray: [],
      google: null,
      fakePplNum: null,
      options: [],
      //bind input
      query: null,
      //
      searchVal: null,
      activeInput: false,
      suggestionLoading: false,
      activeEnterTip: false,
      selectLocation: {
        value: null
      },
      selectLocationInit: {
        value: null
      },
      viewportContainsCenter: true,
      datePickerOptions: {
        disabledDate(date) {
          let today = new Date();
          today.setDate(today.getDate() - 1);
          return date < today;
        }
      },
      btnBgPosition: {
        x: null,
        y: null
      },
      tempSelectLocation: null
    };
  },
  computed: {
    ...mapState('UIModules', ['_windowSize'])
    // ...mapState('SearchModules', {
    //   pplNum: state => state.searchFilter.pplNum
    // })
  },
  watch: {},
  methods: {
    handleSearchBtnHover(e) {
      if (this.large) {
        this.btnBgPosition.x = e.offsetX - e.target.offsetWidth * 1.5;
        this.btnBgPosition.y = e.offsetY - e.target.offsetHeight * 1.5;
      }
    }
  }
};
</script>
