<template>
  <div class="nav-container">
    <router-link style="display: contents;" :to="{ name: 'homePage' }">
      <span
        class="logo-wrapper"
        :class="{ 'logo-wrapper-front': stage === 'front', 'logo-wrapper-back': stage === 'back' }"
      >
        <Logo class="logo" :class="{ animate: isLoading }"></Logo>
        <LogoWord
          v-if="stage === 'front' && (_windowSize.md.larger || !searchBarVisible)"
          class="logo-word"
        ></LogoWord>
      </span>
    </router-link>
    <div class="strech-container">
      <el-menu v-if="stage === 'back'" :default-active="navActiveIndex" mode="horizontal">
        <el-menu-item @click="$router.push({ name: 'Products' })" index="Products"
          ><router-link to="/admin/products"></router-link></el-menu-item
        >
        <el-menu-item @click="$router.push({ name: 'RoomManagement' })" index="RoomManagement"
          ><router-link to="/admin/management"></router-link></el-menu-item
        >
      </el-menu>
      <template v-if="searchBarVisible && $route.name != 'wishList'">
        <SearchBar v-if="_windowSize.lg.larger"></SearchBar>
        <SearchCollapse v-else></SearchCollapse>
      </template>
      <!-- <div
        v-else-if="searchBarVisible && $route.name != 'wishList'"
        class="search-bar-short-container"
      >
        <button @click="searchCollapseVisible = !searchCollapseVisible">
          <i class="el-icon-search"></i> 
        </button>
        <div class="search-collapse" :class="{ hide: !searchCollapseVisible }">
          <SearchBar v-if="$route.name != 'wishList'"></SearchBar>
        </div>
      </div> -->
    </div>
    <el-dropdown @command="handleDropDownCommand" trigger="click">
      <button class="user-btn">
        <div class="avatar"><img src="@/assets/images/user.png" /></div>
      </button>
      <el-dropdown-menu class="dropdown-container" slot="dropdown">
        <el-dropdown-item v-if="$route.name != 'wishList'" command="wishList">{{
          $t('mainMenu.myWishList')
        }}</el-dropdown-item>
        <el-dropdown-item v-if="$route.name != 'search'" command="searchMode">{{
          $t('mainMenu.searchStay')
        }}</el-dropdown-item>
        <el-dropdown-item v-if="!$route.path.includes('admin')" command="changeLanguage">{{
          $t('mainMenu.language')
        }}</el-dropdown-item>
        <el-dropdown-item v-if="!$route.path.includes('admin')" command="manageListings">{{
          $t('mainMenu.manageListing')
        }}</el-dropdown-item>
        <!-- <el-dropdown-item command="checkLogin"></el-dropdown-item> -->
        <el-dropdown-item v-if="!userAuth" command="LogIn">{{
          $t('mainMenu.logIn')
        }}</el-dropdown-item>
        <el-dropdown-item v-if="userAuth" command="LogOut">{{
          $t('mainMenu.logOut')
        }}</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <!-- <button class="user-btn">
      <div class="avatar"><img src="/images/user-01.png" /></div>
    </button> -->
    <el-dialog
      class="lang-dialog"
      :title="$t('mainMenu.langDialogTitle')"
      :visible.sync="langDialogVisible"
      :append-to-body="true"
      :center="true"
    >
      <el-row>
        <el-col :md="12" :sm="24"
          ><el-button
            @click="handleChangeLang('zh-TW')"
            :class="{ active: currentLang == 'zh-TW' }"
          >
            <span></span>
          </el-button>
        </el-col>
        <el-col :md="12" :sm="24">
          <el-button @click="handleChangeLang('en')" :class="{ active: currentLang == 'en' }">
            <span>English</span>
          </el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
import Logo from '@/assets/images/logo.svg?inline';
import LogoWord from '@/assets/images/logo-word.svg?inline';
import SearchBar from './SearchBar';
import SearchCollapse from './SearchCollapse';
import { mapState } from 'vuex';
export default {
  components: { Logo, LogoWord, SearchBar, SearchCollapse },
  props: {
    stage: String
  },
  data() {
    return {
      navActiveIndex: '0',
      windowWidth: null,
      langDialogVisible: false,
      currentLang: null,
      searchCollapseVisible: false
    };
  },
  computed: {
    ...mapState(['mapPageMode']),
    ...mapState('MapModules', ['mapProduct']),
    ...mapState('UIModules', ['searchBarVisible', '_windowSize']),
    ...mapState('AuthModules', ['userAuth']),
    isLoading() {
      return this.$store.state.isLoadingQuery > 0;
    }
  },
  watch: {
    // '$route.name': {
    //   handler(newName, oldName) {
    //     if (newName != oldName) {
    //       this.$store.commit('UIModules/SHOW_SEARCHBAR', false);
    //       this.$nextTick(() => {
    //         this.$store.commit('UIModules/SHOW_SEARCHBAR', true);
    //       });
    //     }
    //   },
    //   immediate: true
    // }
  },
  methods: {
    LogOut() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/logout`;
      this.$store.commit('COUNT_LOADING', 1);
      document.cookie = 'hexToken=; Max-Age=0'
      // document.cookie = 'hexTokenExpireAt=; Max-Age=0'
      this.$http.post(api).then(response => {
        if (response.data.success) {
          this.$store.commit('AuthModules/UPDATE_USER_AUTH', false);
          if (this.$route.meta.requiresAuth) {
            this.$router.push('/');
          }
        }
        vm.$store.commit('COUNT_LOADING', -1);
      });
    },
    onResize() {
      this.$bus.$emit('resize');
      this.windowWidth = window.innerWidth;
      this.$store.commit('UIModules/SET_WINDOW_WIDTH', window.innerWidth);
    },
    handleDropDownCommand(command) {
      if (command == 'searchMode') {
        this.$router.push({ name: 'search' });
      } else if (command == 'wishList') {
        this.$router.push({ name: 'wishList' });
      }
      if (command == 'manageListings') {
        if (this.userAuth) {
          this.$router.push({ name: 'Products' }).catch(err => {
            console.log(err);
          });
        } else {
          const newQuery = Object.assign({ loginPath: 'manageListings' }, this.$route.query);
          this.$router.replace({
            query: newQuery
          });
          this.$store.commit('UIModules/SET_LOGIN_MODAL_VISIBLE', true);
        }
      }
      if (command == 'LogIn') {
        this.$store.commit('UIModules/SET_LOGIN_MODAL_VISIBLE', true);
      }
      if (command == 'LogOut') {
        this.LogOut();
      }
      if (command == 'changeLanguage') {
        this.langDialogVisible = true;
      }
      if (command == 'checkLogin') {
        let api = `${process.env.VUE_APP_APIPATH}/api/user/check`;
        this.$http.post(api).then(response => {
          console.log('success', response.data.success, response.data);
        });
      }
    },
    handleChangeLang(lang) {
      this._i18n.locale = lang;
      this.currentLang = lang;
      localStorage.setItem('language', lang);
      this.langDialogVisible = false;
      document.title = this.$t('pageTitle');
      if (this.$route.name == ('search' || 'wishList') && !this.mapProduct.select) {
        window.location.reload();
      }
    }
  },
  created() {
    this.navActiveIndex = this.$route.name;
  },
  mounted() {
    this.$store.commit('UIModules/SET_WINDOW_WIDTH', window.innerWidth);
    this.currentLang = localStorage.getItem('language') || 'zh-TW';
    this.windowWidth = window.innerWidth;
  }
};
</script>
