import GoogleMapsApiLoader from 'google-maps-api-loader';
// eslint-disable-next-line no-unused-vars
import Vue from 'vue';
export default {
  namespaced: true, //MapModules
  state: {
    google: null,
    geocoder: null,
    bounds: null,
    center: null,
    zoom: null,
    showSearchControl: false,
    mapProduct: {
      select: null,
      hover: null,
      hoverClass: false,
      scrollToId: null,
      selectByMap: null
    },
    //TODO: viewport
    // viewAllProducts: null,
    viewAllProductsBounds: null,
    SearchControlTimer: null
  },
  actions: {
    async setGoogleAPI(context) {
      return new Promise(resolve => {
        GoogleMapsApiLoader({
          apiKey: 'AIzaSyDTP6E9ofDGuoa6fo0egyM8CSOKQ4cGVAg',
          language: localStorage.getItem('language') || 'en'
        }).then(googleMapApi => {
          context.commit('SET_API', googleMapApi);
          resolve();
        });
      });
    },
    handleSearchControl(context, val) {
      context.commit('SET_SEARCH_CONTROL', val);
    },
    selectMapProduct(context, [product, selectByMap = false]) {
      context.commit('SELECT_BY_MAP', selectByMap);
      context.commit('SET_SELECT_PRODUCT', product);
      // context.commit('VIEW_ALL_PRODUCTS', false);
    },
    hoverMapProduct(context, { product, hoverClass = false }) {
      context.commit('MAP_PRODUCT_HOVER', { product, hoverClass });
    },
    removeMapProduct(context) {
      context.commit('REMOVE_SELECT_MAP_PRODUCT');
    },
    scrollToMapProduct(context, productId) {
      context.commit('SCROLL_TO_MAP_PRODUCT', productId);
    },
    // viewAllProducts(context, boolean) {
    //   console.log('viewAllProducts', boolean);

    //   context.commit('VIEW_ALL_PRODUCTS', boolean);
    // },
    setControlVisibleTimer(context) {
      let timer = setTimeout(() => {
        if (context.state.SearchControlTimer != timer) return;
        // this.SearchControlTimer = null;
        context.commit('SET_CONTROL_TIMER', null);
      }, 3000);
      context.commit('SET_CONTROL_TIMER', timer);
    }
  },
  mutations: {
    SET_API(state, payload) {
      state.google = payload;
      state.geocoder = new state.google.maps.Geocoder();
    },
    UPDATE_BOUNDS(state, payload) {
      state.bounds = payload;
    },
    UPDATE_CENTER(state, payload) {
      state.center = payload;
    },
    UPDATE_ZOOM(state, payload) {
      state.zoom = payload;
    },
    SET_SEARCH_CONTROL(state, payload) {
      state.showSearchControl = payload;
    },
    MAP_PRODUCT_HOVER(state, payload) {
      state.mapProduct.hover = payload.product;
      state.mapProduct.hoverClass = payload.hoverClass;
    },
    SET_SELECT_PRODUCT(state, payload) {
      state.mapProduct.select = payload;
    },
    SELECT_BY_MAP(state, payload) {
      state.mapProduct.selectByMap = payload;
    },
    REMOVE_SELECT_MAP_PRODUCT(state) {
      state.mapProduct.select = null;
    },
    SCROLL_TO_MAP_PRODUCT(state, payload) {
      state.mapProduct.scrollToId = payload;
    },
    // VIEW_ALL_PRODUCTS(state, payload) {
    //   state.viewAllProducts = payload;
    // },
    SAVE_VIEW_ALL_BOUNDS(state, payload) {
      state.viewAllProductsBounds = payload;
    },
    SET_CONTROL_TIMER(state, payload) {
      state.SearchControlTimer = payload;
    }
  }
};
