<template>
  <div>
    <Modal class="reserve-modal" @close="handleClose">
      <template v-slot:header>
        <span>{{ $t('reserveModal.reserveStay') }}</span>
      </template>
      <template v-slot:body>
        <transition name="el-zoom-in-center">
          <div v-if="reserveSucceed" class="success-info">
            <div>
              <i class="el-icon-check"></i>
            </div>
            <span>{{ $t('reserveModal.reservationComplete') }}</span>
          </div>
        </transition>
        <template v-if="!reserveSucceed">
          <div class="room-info">
            <div class="property-title">{{ selectStay.info.title }}</div>
            <div class="room-title">{{ selectRoom.name }}</div>
            <div class="room-price">
              {{ selectRoom.price | currency }}/{{ $t('reserveModal.night') }}
            </div>
          </div>
          <div class="option-wrapper">
            <div ref="datePickerBtn" class="date-picker-btn btn" @click="handleDatePickerClick">
              <el-date-picker
                ref="datePicker"
                style="display:none"
                v-model="reserveDate"
                popper-class="reserve-date-picker"
                type="daterange"
                size="small"
                align="center"
                range-separator="-"
                value-format="yyyy/M/d"
                :start-placeholder="$t('homePage.searchBar.checkInDates')"
                :end-placeholder="$t('homePage.searchBar.checkOutDates')"
                :picker-options="datePickerOptions"
              >
              </el-date-picker>
              <div class="check-in date-area">
                <div class="label">{{ $t('reserveModal.checkIn') }}</div>
                <div class="input">
                  {{ reserveDate ? reserveDate[0] : $t('reserveModal.selectDate') }}
                </div>
              </div>
              <div class="check-out date-area">
                <div class="label">{{ $t('reserveModal.checkOut') }}</div>
                <div class="input">
                  {{ reserveDate ? reserveDate[1] : $t('reserveModal.selectDate') }}
                </div>
              </div>
            </div>
            <div class="ppl-num-btn btn">
              <div class="ppl-num-area">
                <div @click="handleRoomNumInputFocus" class="label">
                  {{ $t('reserveModal.roomNum') }}
                </div>
                <div @click="handleRoomNumInputFocus" class="input" v-if="!roomNumInputVisible">
                  {{
                    roomNumber > 0
                      ? roomNumber + ' ' + $t('reserveModal.roomNumUnit')
                      : ''
                  }}
                </div>
                <input
                  ref="roomNumInput"
                  v-if="roomNumInputVisible"
                  class="room-num-input"
                  type="number"
                  @blur="handleRoomNumBlur"
                  @keyup.enter="handleRoomNumEnter"
                  v-model="roomNumber"
                  min="1"
                />
              </div>
            </div>
          </div>
          <div v-if="totalNights" class="detail-wrapper">
            <ul class="charge-list">
              <li>
                <div class="charge-title-wrapper">
                  <span class="charge-title">
                    {{ roomNumber + '' + $t('reserveModal.roomNumUnit') }}</span
                  >
                  <span class="charge-calc-detail">
                    {{ totalNights + ' ' + $t('reserveModal.night') }} x
                    {{ selectRoom.price | currency }}
                  </span>
                </div>
                <span class="charge-price">{{
                  (selectRoom.price * totalNights * roomNumber) | currency
                }}</span>
              </li>
            </ul>
            <div class="total">
              {{ $t('reserveModal.total') }}
              {{ (selectRoom.price * totalNights * roomNumber) | currency }}
            </div>
          </div>
          <button
            class="reserve-btn"
            :style="{
              backgroundPosition: `left ${btnBgPosition.x}px top ${btnBgPosition.y}px`
            }"
            :disabled="reserveLoading"
            @mouseenter="handleBtnHover"
            @mousemove="handleBtnHover"
            @click="handleReserveBtnClick"
          >
            <pulse-loader
              :loading="reserveLoading"
              :color="'#ffffffee'"
              :size="'8px'"
            ></pulse-loader>
            {{
              reserveLoading
                ? ''
                : totalNights
                ? $t('reserveModal.reserve')
                : $t('reserveModal.checkAvailableDate')
            }}
          </button>
        </template>
      </template>
    </Modal>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Modal from '@/components/general/Modal.vue';
export default {
  components: { Modal },
  props: {
    visible: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      reserveDate: null,
      reserveDateArray: [],
      btnBgPosition: {
        x: null,
        y: null
      },
      datePickerOptions: {
        disabledDate(date) {
          let today = new Date();
          today.setDate(today.getDate() - 1);
          return date < today;
        }
      },
      roomNumber: 1,
      roomNumInputVisible: false,
      reserveSucceed: false,
      reserveLoading: false
    };
  },
  watch: {
    reserveDate() {
      const dateArray = [];
      const startDay = new Date(this.reserveDate[0]);
      for (let i = 0; i < this.totalNights; i++) {
        let tempDate = new Date(startDay.getTime() + 1000 * 3600 * 24 * i);
        let tempDateString = [
          tempDate.getFullYear(),
          tempDate.getMonth() + 1,
          tempDate.getDate()
        ].join('/');
        dateArray.push(tempDateString);
        this.reserveDateArray = dateArray;
      }
    }
  },
  computed: {
    ...mapState('ReservationModules', ['selectRoom', 'selectStay']),
    ...mapState('UIModules', ['_windowSize']),
    totalNights() {
      if (this.reserveDate) {
        const startDay = new Date(this.reserveDate[0]);
        const endDay = new Date(this.reserveDate[1]);
        const totalNights = (endDay.getTime() - startDay.getTime()) / (1000 * 3600 * 24);
        return totalNights;
      }
      return null;
    }
  },
  methods: {
    handleClose() {
      this.$emit('update:visible', !this.visible);
    },

    handleBtnHover(e) {
      this.btnBgPosition.x = e.offsetX - e.target.offsetWidth * 1.5;
      this.btnBgPosition.y = e.offsetY - e.target.offsetHeight * 1.5;
    },
    handleDatePickerClick() {
      this.$refs.datePicker.focus();
      // console.log(document.body.getElementsByClassName('reserve-date-picker')[0]);
      // console.log(this.$refs.datePickerBtn);
      this.$nextTick(() => {
        const pickerElm = document.body.getElementsByClassName('reserve-date-picker')[0];
        if (pickerElm) {
          if (this._windowSize.lg.larger) {
            pickerElm.className = pickerElm.className.replace(/\bdate-pick-full-page\b/g, '');
          } else {
            pickerElm.className += ' ' + 'reserve-date-pick-full-page';
          }
          setTimeout(() => {
            this.handleDatePickerPosition();
          }, 0);
        }
      });
      setTimeout(() => {
        this.handleDatePickerPosition();
      }, 0);
    },
    handleDatePickerPosition() {
      setTimeout(() => {
        const pickerElm = document.body.getElementsByClassName('reserve-date-picker')[0];
        pickerElm.style.top = `${this.$refs.datePickerBtn.offsetParent.offsetParent.offsetTop -
          this.$refs.datePickerBtn.offsetParent.offsetParent.clientHeight / 2 +
          this.$refs.datePickerBtn.offsetParent.offsetTop +
          this.$refs.datePickerBtn.offsetTop +
          this.$refs.datePickerBtn.clientHeight -
          10.5}px`;
        pickerElm.style.display = 'block';
        pickerElm.style.left = `${this.$refs.datePickerBtn.offsetParent.offsetParent.offsetLeft -
          pickerElm.clientWidth / 2 -
          1}px`;
        if (!this._windowSize.lg.larger) {
          pickerElm.style.height = `calc(100vh - ${this.$refs.datePickerBtn.offsetParent
            .offsetParent.offsetTop -
            this.$refs.datePickerBtn.offsetParent.offsetParent.clientHeight / 2 +
            this.$refs.datePickerBtn.offsetParent.offsetTop +
            this.$refs.datePickerBtn.offsetTop +
            this.$refs.datePickerBtn.clientHeight}px)`;
        } else {
          pickerElm.style.height = 'auto';
        }
      }, 0);
    },
    handleRoomNumInputFocus() {
      this.roomNumInputVisible = true;
      this.$nextTick(() => {
        this.$refs.roomNumInput.focus();
      });
    },
    handleRoomNumBlur() {
      this.roomNumInputVisible = false;
    },
    handleRoomNumEnter() {
      this.$nextTick(() => {
        this.$refs.roomNumInput.blur();
      });
    },
    handleReserveBtnClick(e) {
      if (this.totalNights) {
        this.reserveLoading = true;
        this.$store
          .dispatch('ReservationModules/reserve', {
            reserveDate: this.reserveDateArray,
            roomQty: this.roomNumber
          })
          .then(() => {
            this.reserveSucceed = true;
            console.log(' from component');
          })
          .catch(() => {
            console.log(' from component');
          })
          .finally(() => {
            this.reserveLoading = false;
          });
      } else {
        this.handleDatePickerClick(e);
      }
    }
  },
  created() {},
  mounted() {
    this.$bus.$on('resize', () => {
      if (this.$refs.datePicker) {
        this.$refs.datePicker.handleClose();
      }
    });
  },
  beforeDestroy() {
    this.$bus.$off('resize', this.handleDatePickerPosition);
  }
};
</script>
<style lang="scss" scoped src="@/assets/css/components/_ReserveModal.scss"></style>
<style>
.reserve-date-picker {
  display: none;
}
</style>
