<template>
  <div class="el-image">
    <template v-if="preview">
      <image-viewer
        :z-index="zIndex"
        :initial-index="imageIndex"
        v-if="showViewer"
        :on-close="closeViewer"
        :url-list="calcPreviewSrcList"
      />
    </template>
  </div>
</template>

<script>
import ImageViewer from 'element-ui/packages/image/src/image-viewer';
let prevOverflow = '';

export default {
  name: 'ElImagePreview',

  components: {
    ImageViewer
  },

  props: {
    previewSrcList: {
      type: Array,
      default: () => []
    },
    zIndex: {
      type: Number,
      default: 2000
    },
    showViewer: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {};
  },

  computed: {
    preview() {
      const { previewSrcList } = this;
      return Array.isArray(previewSrcList) && previewSrcList.length > 0;
    },
    imageIndex() {
      let previewIndex = 0;
      const srcIndex = this.previewSrcList.indexOf(this.src);
      if (srcIndex >= 0) {
        previewIndex = srcIndex;
      }
      return previewIndex;
    },
    calcPreviewSrcList() {
      const urlList = [];
      this.previewSrcList.forEach(obj => {
        urlList.push(obj.url);
      });
      return urlList;
    }
  },

  watch: {},

  mounted() {},

  beforeDestroy() {},

  methods: {
    clickHandler() {
      // don't show viewer when preview is false
      if (!this.preview) {
        return;
      }
      // prevent body scroll
      prevOverflow = document.body.style.overflow;
      document.body.style.overflow = 'hidden';
      // this.showViewer = true;
    },
    closeViewer() {
      document.body.style.overflow = prevOverflow;
      // this.showViewer = false;
      this.$store.dispatch('setViewer', { value: false, key: 'showPhotoViewer' });
    }
  }
};
</script>
<style lang="scss" scoped>
.el-image {
  /deep/ .el-image-viewer__mask {
    opacity: 0.9;
  }
  /deep/ .el-icon-circle-close::before {
    opacity: 1;
    color: #fff;
  }
}
</style>
