const fns = {
  getUuid: function() {
    var d = Date.now();
    if (typeof performance !== 'undefined' && typeof performance.now === 'function') {
      d += performance.now(); //use high-precision timer if available
    }
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      var r = (d + Math.random() * 16) % 16 | 0;
      d = Math.floor(d / 16);
      return (c === 'x' ? r : (r & 0x3) | 0x8).toString(16);
    });
  },
  generateUID: function() {
    // I generate the UID from two parts here
    // to ensure the random number provide enough bits.
    var firstPart = (Math.random() * 46656) | 0;
    var secondPart = (Math.random() * 46656) | 0;
    firstPart = ('000' + firstPart.toString(36)).slice(-3);
    secondPart = ('000' + secondPart.toString(36)).slice(-3);
    return firstPart + secondPart;
  },
  quickSort(arr) {
    if (arr.length <= 1) {
      return arr;
    }

    const less = [];
    const greater = [];
    const equal = [];
    const pivot = Number(arr[Math.floor(Math.random() * arr.length)]);

    for (let i = 0; i < arr.length; ++i) {
      const num = Number(arr[i]);
      if (num < pivot) {
        less.push(num);
      } else if (num > pivot) {
        greater.push(num);
      } else if (num == pivot) {
        equal.push(num);
      }
    }
    //
    equal.splice(equal[equal.length - 1], 1);
    return [...fns.quickSort(less), ...equal, pivot, ...fns.quickSort(greater)];
  },
  quickSortObj(arr, key) {
    if (arr.length <= 1) {
      return arr;
    }

    const less = [];
    const greater = [];
    const equal = [];
    const pivotArray = arr[Math.floor(Math.random() * arr.length)];
    const pivot = Number(pivotArray[key]);

    for (let i = 0; i < arr.length; ++i) {
      const numArray = arr[i];
      const num = Number(numArray[key]);
      if (num < pivot) {
        less.push(numArray);
      } else if (num > pivot) {
        greater.push(numArray);
      } else if (num == pivot && numArray.id != pivotArray.id) {
        equal.push(numArray);
      }
    }
    // const pivotInEqual = equal.find(obj => obj.id == pivotArray.id);
    // const pivotInEqualIndex = equal.indexOf(pivotInEqual);
    // equal.splice(pivotInEqualIndex, 1);

    return [
      ...fns.quickSortObj(less, key),
      ...equal,
      pivotArray,
      ...fns.quickSortObj(greater, key)
    ];
  }
};

export default {
  install(Vue) {
    Vue.prototype.$custom = fns;
  }
};
