<template>
  <div style="margin-top:80px">
    <div class="page-container">
      <div class="welcome-title">{{ $t('homePage.welcomeTitle') }}</div>
      <SearchBar ref="searchBar" :large="true" stage="front"></SearchBar>
      <section>
        <header>{{ $t('homePage.carousel.carouselTitle') }}</header>
        <ul ref="carousel" class="carousel">
          <li class="card-wrapper">
            <a href="javascript:;">
              <div class="card">
                <div class="img-wrapper">
                  <img src="@/assets/images/home-page/carousel/join-the-world.jpg" alt="" />
                </div>
                <div class="text-area-wrapper">
                  <div class="text-area">
                    <header>{{ $t('homePage.carousel.joinWorldTitle') }}</header>
                    <div class="sub-title">
                      {{ $t('homePage.carousel.joinWorldDescription') }}
                    </div>
                  </div>
                </div>
              </div>
            </a>
          </li>
          <li class="card-wrapper">
            <a href="javascript:;">
              <div class="card">
                <div class="img-wrapper">
                  <img src="@/assets/images/home-page/carousel/online-experience.jpg" alt="" />
                </div>
                <div class="text-area-wrapper">
                  <div class="text-area">
                    <header>{{ $t('homePage.carousel.onlineExpTitle') }}</header>
                    <div class="sub-title">
                      {{ $t('homePage.carousel.onlineExpDescription') }}
                    </div>
                  </div>
                </div>
              </div>
            </a>
          </li>
          <li class="card-wrapper">
            <a href="javascript:;">
              <div class="card">
                <div class="img-wrapper">
                  <img src="@/assets/images/home-page/carousel/article.jpg" alt="" />
                </div>
                <div class="text-area-wrapper">
                  <div class="text-area">
                    <header>{{ $t('homePage.carousel.featuredContentTitle') }}</header>
                    <div class="sub-title">
                      {{ $t('homePage.carousel.featuredContentDescription') }}
                    </div>
                  </div>
                </div>
              </div>
            </a>
          </li>
        </ul>
      </section>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import SearchBar from '@/components/general/SearchBar';
import Footer from '@/components/general/Footer';
export default {
  components: { SearchBar, Footer },
  methods: {
    handleScroll() {
      this.handleSearchBarVisible();
    },
    handleSearchBarVisible(arg = true) {
      if (!arg) {
        console.log('handleSearchBarVisible', false);
        this.$store.commit('UIModules/SHOW_SEARCHBAR', false);
        return;
      }
      if (window.scrollY > this.$refs.searchBar.$el.offsetTop - 15) {
        this.$store.commit('UIModules/SHOW_SEARCHBAR', true);
      } else {
        this.$store.commit('UIModules/SHOW_SEARCHBAR', false);
      }
    }
  },
  watch: {
    $route: {
      handler(newVal) {
        if (newVal.name == 'homePage') {
          this.handleSearchBarVisible(false);
        }
      }
    }
  },
  created() {
    // this._i18n.locale = 'en';
    window.addEventListener('scroll', this.handleScroll);
  },
  mounted() {
    this.handleSearchBarVisible();
  },
  beforeDestroy() {
    this.handleSearchBarVisible(false);
    window.removeEventListener('scroll', this.handleScroll);
  }
};
</script>
<style lang="scss" scoped src="@/assets/css/components/_HomePage.scss"></style>
