export default {
  namespaced: true,
  state: {
    messages: []
  },
  actions: {
    updateMessage(context, [message, status]) {
      const timestamp = Math.floor(new Date() / 1000);
      const messages = {
        message,
        status,
        timestamp
      };
      context.commit('UPDATE_MESSAGES', messages);
      context.dispatch('removeMessageWithTiming', timestamp);
    },
    removeMessage(context, num) {
      context.commit('REMOVE_MESSAGES_WITH_TIMING', num);
    },
    removeMessageWithTiming(context, timestamp) {
      setTimeout(() => {
        context.state.messages.forEach((item, i) => {
          if (item.timestamp === timestamp) {
            context.commit('REMOVE_MESSAGES_WITH_TIMING', i);
          }
        });
      }, 5000);
    }
  },
  mutations: {
    UPDATE_MESSAGES(state, payload) {
      state.messages.push(payload);
    },
    REMOVE_MESSAGES(state, payload) {
      state.messages.splice(payload, 1);
    },
    REMOVE_MESSAGES_WITH_TIMING(state, payload) {
      state.messages.splice(payload, 1);
    }
  },
  getters: {
    messages: state => state.messages
  }
};
