<template>
  <div
    class="img-container"
    @mouseenter.self="hoverHandler(true)"
    @mouseleave.self="hoverHandler(false)"
  >
    <CustomImage
      :class="{ hover: hover }"
      style="width: 100%; height: 100%"
      :src="imgSrc"
      fit="cover"
      :preview-src-list="previewSrcList"
      :previewListVisible="!panoramas && previewVisible"
      @closeViewer="previewVisible = false"
    >
      <div slot="uploading" v-if="uploading" class="uploading-slot">
        <div class="state-container">
          <i class="el-icon-loading"></i>
          <span></span>
        </div>
      </div>
      <div slot="placeholder" class="image-slot">
        <i class="el-icon-loading"></i>
      </div>
      <div slot="error" class="image-slot">
        <i class="el-icon-warning-outline"></i>
        <div style="font-size:14px;position: absolute;left: 50%;transform: translateX(-50%);">
          
        </div>
      </div>
    </CustomImage>
    <div class="btn-wrapper" v-if="!uploading">
      <el-popconfirm
        title="?"
        placement="right-start"
        confirmButtonText=""
        cancelButtonText=""
        cancelButtonType="Text"
        @onConfirm="deleteHandler"
        :hideIcon="true"
      >
        <div slot="reference">
          <transition name="el-fade-in">
            <el-button v-if="hover" icon="el-icon-delete" size="mini" plain></el-button>
          </transition>
        </div>
      </el-popconfirm>

      <div slot="reference">
        <transition name="el-fade-in">
          <el-button
            v-if="hover"
            @click="
              previewVisible = true;
              hover = false;
            "
            icon="el-icon-view"
            size="mini"
            plain
          ></el-button>
        </transition>
      </div>
    </div>
    <!-- Dialog -->
    <PanoDialog
      v-if="panoramas && previewVisible"
      :src="panoramasData.image.url"
      :placeHolderSrc="imgSrc"
      :visible="previewVisible"
      @closeDialog="closePreview"
    >
    </PanoDialog>
  </div>
</template>

<script>
import CustomImage from './CustomImage';
import PanoDialog from './PanoDialog';
export default {
  data() {
    return {
      hover: false,
      previewVisible: false
    };
  },
  props: {
    uploading: Boolean,
    panoramas: Boolean,
    imgSrc: String,
    panoramasData: Object,
    previewSrcList: Array
  },
  methods: {
    closePreview() {
      this.previewVisible = false;
    },
    deleteHandler() {
      this.$emit('delete');
    },
    // eslint-disable-next-line no-unused-vars
    hoverHandler(val) {
      this.hover = val;
    }
  },
  components: {
    CustomImage,
    PanoDialog
  }
};
</script>

