// eslint-disable-next-line no-unused-vars
import { mapState } from 'vuex';
export default {
  computed: {
    pplNum: {
      get: function() {
        return this.$store.state.SearchModules.searchFilter.pplNum;
      },
      set: function(val) {
        this.$store.commit('SearchModules/updatePplNum', val);
      }
    },
    isLoading() {
      return this.$store.state.isLoadingQuery > 0;
    },
    ...mapState('MapModules', {
      searchLocation: state => state.searchLocation,
      mapProduct: state => state.mapProduct,
      mapCenter: state => state.center
    }),
    ...mapState('SearchModules', {
      searchByMap: state => state.searchByMap,
      searchFilter: state => state.searchFilter,
      searchBounds: state => state.searchBounds,
      searchExecuted: state => state.searchExecuted,
      searchType: state => state.searchType,
      searchByQuery: state => state.searchByQuery
    }),
    searchBarTitle() {
      if (this.searchType == 'map') {
        return this.$t('homePage.searchBar.customArea');
      } else if (this.searchType == 'location') {
        return this.selectLocation
          ? this.selectLocation.value
          : this.$t('homePage.searchBar.location_short');
      } else if (this.searchType == 'query') {
        if (this.searchByMap) {
          return this.$t('homePage.searchBar.customArea');
        } else {
          return this.searchByQuery.routeQuery.place_name;
        }
      } else {
        if (this.large) {
          return this._windowSize.lg.larger
            ? this.$t('homePage.searchBar.location_long')
            : this.$t('homePage.searchBar.location_short');
        } else {
          return this._windowSize.lg.larger
            ? this.$t('homePage.searchBar.location_short')
            : this.$t('homePage.searchBar.location_short');
        }
      }
    },
    computedDate: {
      get() {
        return this.searchFilter.date;
      },
      set(value) {
        this.$store.commit('SearchModules/updateDate', value);
      }
    },
    displayDate() {
      if (this.searchFilter.date) {
        const tempDate = this.searchFilter.date.map(elm => {
          const tempElm = new Date(elm);
          const tempArray = [tempElm.getMonth() + 1, tempElm.getDate()].join('/');
          return tempArray;
        });
        return tempDate;
      }
    }
  },
  watch: {
    mapCenter(newVal) {
      if (!this.searchLocation) return;
      this.viewportContainsCenter = this.searchLocation.mapInfo.geometry.viewport.contains(newVal);
    },
    searchByMap(newVal) {
      if (newVal) {
        this.query = '';
        this.selectLocation = Object.assign({}, this.selectLocationInit);
      }
    },
    'searchFilter.location.value'(newVal) {
      if (!this.searchExecuted && newVal) {
        this.selectLocation.value = this.searchFilter.location.value;
      }
    },
    searchByQuery: {
      handler(newVal) {
        this.query = newVal.routeQuery.place_name;
      }
    },
    date: {
      handler(newVal) {
        this.$store.commit('SearchModules/updateDate', newVal);
      }
    }
  },
  methods: {
    updatePplNum(e) {
      this.$store.commit('SearchModules/updatePplNum', e.target.value);
    },
    leadToInput() {
      if (!this.activeInput) {
        this.activeInput = true;
        setTimeout(() => {
          this.$refs.searchRef.focus();
        }, 0);
      }
      this.$store.dispatch('MapModules/handleSearchControl', true);
      this.$store.dispatch('MapModules/setControlVisibleTimer');
    },
    handleSearch() {
      this.tempSelectLocation = null;
      //searchFilter.location.value
      if (
        this.selectLocation && this.searchFilter.location
          ? this.selectLocation.value != this.searchFilter.location.value
          : true
      ) {
        this.$store.dispatch('SearchModules/filterSearchProducts', {
          searchByMap: false,
          searchType: 'location'
        });
        //Vuex, this.selectLocationrerender
        this.$store.dispatch('SearchModules/passSearchLocation', this.selectLocation);
        //query
        const boundKeyArray = Object.keys(this.selectLocation.mapInfo.geometry.viewport); //key
        const viewport = this.selectLocation.mapInfo.geometry.viewport;
        const newQuery = {
          search_by_map: false,
          place_name: this.selectLocation.value,
          search_bounds_s: viewport[boundKeyArray[1]].i, // 
          search_bounds_e: viewport[boundKeyArray[0]].i, // 
          search_bounds_n: viewport[boundKeyArray[1]].j, // 
          search_bounds_w: viewport[boundKeyArray[0]].j, // 
          guest_number: this.searchFilter.pplNum
        };
        if (this.searchFilter.date) {
          newQuery.startDate = this.searchFilter.date[0];
          newQuery.endDate = this.searchFilter.date[1];
        }
        this.$router.replace({
          query: newQuery
        });
        //queryvuex
        this.$store.commit('SearchModules/SET_SEARCH_QUERY', newQuery);
        if (this.$route.name != 'search') {
          this.$router.push({ name: 'search' });
        }
        this.$emit('searchSubmit');
      }
    },
    handleBlur(e) {
      //
      if (
        !(e.relatedTarget ? e.relatedTarget.className.includes('tipBtn') : false) &&
        this.suggestions.length == 0
      ) {
        //
        this.changeInput(false);
        //
        if (this.searchType != 'query') {
          this.query = null;
        }
        if (this.selectLocation.value) {
          //
          this.query = this.selectLocation.value;
        }
      }
    },
    handleTipClick() {
      this.$bus.$emit('debouncedGetData');
      //tipTip
      this.handleEnterTip(false);
    },
    handleKeyEnter() {
      //EnterTip
      this.handleEnterTip(false);
    },
    handleInput() {
      //Tip
      this.handleEnterTip(true);

      //
      this.suggestions = [];
      this.searchVal = '';
    },
    handleSelect(item) {
      this.selectLocation = item;
      this.tempSelectLocation = item;
      this.query = item.value;
      //
      this.suggestions = [];
      //Input
      this.changeInput(false);
    },
    handleEnterTip(val) {
      if (
        this.query == '' ||
        this.query == this.searchVal ||
        this.query == this.selectLocation.value
      ) {
        this.activeEnterTip = false;
        return;
      }
      this.activeEnterTip = val;
    },
    changeInput(val) {
      if (val) {
        this.handleEnterTip(val);
        this.activeInput = val;
        setTimeout(() => {
          this.$refs.searchRef.focus();
        }, 0);
      } else {
        this.activeInput = val;
      }
    },
    async querySearchAsync(queryString, cb) {
      //
      if (
        this.query == '' ||
        queryString == this.searchVal ||
        this.query == this.selectLocation.value
      ) {
        return;
      }
      this.suggestionLoading = true;
      this.searchVal = queryString;
      const vm = this;
      // API
      if (!this.$store.state.MapModules.geocoder) {
        await this.$store.dispatch('MapModules/setGoogleAPI');
      }
      this.$store.state.MapModules.geocoder.geocode(
        { address: vm.searchVal },
        (results, status) => {
          if (status === 'OK') {
            // console.log('OK', results);
            const newArray = [];
            for (let i = 0; i < results.length; i++) {
              const newResultObj = {
                value: results[i].formatted_address,
                mapInfo: results[i]
              };
              newArray.push(newResultObj);
            }
            this.resultArray = newArray;
            cb(this.resultArray);
          } else {
            console.log('not OK', results, status);
          }
          if (results.length === 0 || status === 'ZERO_RESULTS') {
            this.noData = true;
            this.suggestionLoading = false;
          }
        }
      );
    },
    addDatePickerListener(e) {
      if (!e.path.includes(document.getElementsByClassName('date-pick-full-page'[0]))) {
        this.$store.commit('UIModules/SET_DATEPICKER_CLOSE_BUTTON', false);
        window.removeEventListener('click', this.addDatePickerListener);
      }
    },
    handleDatePickerClick() {
      this.$store.commit('UIModules/SET_DATEPICKER_CLOSE_BUTTON', true);
      this.$refs.datePicker.focus();
      setTimeout(() => {
        window.addEventListener('click', this.addDatePickerListener);
      }, 200);
    },
    handleDatePickerChange() {},
    handleDatePickerClose() {
      this.$refs.datePicker.handleClose();
      this.$store.commit('UIModules/SET_DATEPICKER_CLOSE_BUTTON', false);
    }
  },
  created() {
    if (this.searchFilter.location) {
      this.selectLocation = this.searchFilter.location;
    }
    // if (this.$route.name == 'search' && this.searchFilter) {
    //   this.selectLocation = Object.assign({}, this.searchFilter);
    // }
  }
};
