<template>
  <div>
    <transition name="el-fade-in">
      <div v-if="show || !animate" @click="handleClose" class="modal-mask"></div>
    </transition>
    <transition name="el-fade-in">
      <div v-if="show || !animate" class="modal-layout" :class="{ md: !_windowSize.lg.larger }">
        <button @click="handleClose" class="close-btn"><i class="el-icon-close"></i></button>
        <header>
          <slot name="header"></slot>
        </header>
        <div class="modal-body">
          <slot name="body"></slot>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapState } from 'vuex';
export default {
  props: {
    visible: {
      type: Boolean,
      default: true
    },
    animate: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      show: false
    };
  },
  computed: {
    ...mapState('UIModules', ['_windowSize'])
  },
  methods: {
    handleClose() {
      this.$emit('close');
    }
  },
  mounted() {
    this.show = true;
  }
};
</script>
<style lang="scss" scoped src="@/assets/css/components/_ModalLayout.scss"></style>
