<template>
  <el-card class="room-type-card" shadow="never">
    <div class="content">
      <div class="image-container">
        <el-carousel
          :indicator-position="roomType.photos.normalImgs.length < 2 ? 'none' : ''"
          :arrow="roomType.photos.normalImgs.length < 2 ? 'never' : 'hover'"
          :autoplay="false"
          trigger="click"
        >
          <!-- style module -->
          <el-carousel-item
            v-for="item in roomType.photos.normalImgs"
            :key="keyPrefix + 'product-image-' + item.id + $custom.getUuid()"
          >
            <el-image :src="item.url" :alt="item.title" fit="cover"></el-image>
          </el-carousel-item>
        </el-carousel>
      </div>
      <div class="room-info">
        <div class="room-header">
          <div class="left-col">
            <span class="title">{{ roomType.name }}</span>
            <br v-if="_windowSize.lg.larger" />
            <span class="sub-title">
              <span>{{ $t(`roomTypeCategory.${roomType.type}`) }}</span> 
              <span
                ><i class="el-icon-user-solid"></i>
                {{
                  roomType.pplNum > 1
                    ? $t('stayPage.guestsUnit', { num: roomType.pplNum })
                    : $t('stayPage.guestUnit', { num: roomType.pplNum })
                }}</span
              >
              
              <span> {{ roomType.floorArea }}m</span>
            </span>
          </div>
          <div class="right-col">
            <span v-if="!_windowSize.lg.larger" @click="handleMoreInfo" class="facility-detail"
              ></span
            >
            <span class="price"
              >{{ $t('stayPage.priceBefore') }} {{ roomType.price | currency(false) }} TWD
              {{ $t('stayPage.priceAfter') }}</span
            >
            <button class="small-reserve-btn" @click="handleReserve(roomType)">
              {{ $t('stayPage.reserve') }}
            </button>
          </div>
        </div>
        <div v-if="_windowSize.lg.larger" class="card-title-divider"></div>
        <div
          ref="facilitiesContainer"
          v-if="_windowSize.lg.larger"
          :class="{ show: showAllFacilities }"
          class="facilities-container"
        >
          <div ref="facilitiesInnerContainer" class="facilities-inner-container">
            <div class="left-col">
              <div class="bed-type-title">{{ $t('stayPage.bedTypeTitle') }}</div>
              <ul>
                <li class="bed-type" v-for="bedType in bedTypes" :key="bedType.bedTypeName">
                  {{ bedType.bedNumber + ` ${$t(`bedType.${bedType.bedTypeName}`)}` }}
                </li>
              </ul>
            </div>
            <div class="right-col">
              <div ref="facilitiesGroup" class="facilities-group">
                <div class="facilities-subject" v-for="(subject, index) in facilities" :key="index">
                  <div v-if="subject.length > 0" class="facility-title">
                    {{ $t(`stayPage.${(Object.keys(facilities), index)}`) }}
                  </div>
                  <ul>
                    <li
                      v-for="(facility, indexOfFacility) in facilities[
                        (Object.keys(facilities), index)
                      ]"
                      :key="indexOfFacility"
                    >
                      <span class="collapse-item">
                        <i :class="facility.icon"></i>
                        {{ $t(`facilitiesSheet.${facility.labelKey}.label`) }}
                      </span>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          v-if="facilityContainerHeight < facilityInnerContainerHeight && _windowSize.lg.larger"
          class="more"
        >
          <span @click="showAllFacilities = !showAllFacilities">{{
            showAllFacilities ? $t('roomTypeCard.collapse') : $t('roomTypeCard.showAll')
          }}</span>
          <i v-if="!showAllFacilities" class="el-icon-arrow-down"></i>
          <i v-if="showAllFacilities" class="el-icon-arrow-up"></i>
        </div>
      </div>
    </div>
    <el-divider v-if="index + 1 < number && _windowSize.lg.larger"></el-divider>
  </el-card>
</template>

<script>
import { mapState } from 'vuex';
// eslint-disable-next-line no-unused-vars
export default {
  props: {
    roomType: {
      type: Object,
      required: true
    },
    index: {
      type: Number,
      required: true
    },
    number: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      collapseActiveNames: null,
      facilityContainerHeight: null,
      facilityInnerContainerHeight: null,
      showAllFacilities: false
    };
  },
  watch: {
    '_windowSize.lg.larger'(newVal) {
      if (newVal) {
        if (!this.facilityContainerHeight || !this.facilityInnerContainerHeight) {
          this.checkFacilityContainer();
        }
      }
    }
  },
  computed: {
    ...mapState('UIModules', ['_windowSize']),
    facilities() {
      const facilities = this.roomType.facilities;

      // const facilitiesArray = [];
      let facilitiesObj = null;
      const basicFacilities = [];
      const bedroomFacilities = [];
      const bathroomFacilities = [];
      for (let key in facilities.basicFacilities) {
        if (facilities.basicFacilities[key]) {
          const obj = {
            id: this.$custom.getUuid(),
            icon: this.$t(`facilitiesSheet.${key}.icon`),
            labelKey: key
          };
          basicFacilities.push(obj);
        }
      }
      for (let key in facilities.bedroomFacilities) {
        if (facilities.bedroomFacilities[key]) {
          const obj = {
            id: this.$custom.getUuid(),
            icon: this.$t(`facilitiesSheet.${key}.icon`),
            labelKey: key,
            value: facilities.bedroomFacilities[key]
          };
          bedroomFacilities.push(obj);
        }
      }
      for (let key in facilities.bathroomFacilities) {
        if (facilities.bathroomFacilities[key]) {
          const obj = {
            id: this.$custom.getUuid(),
            icon: this.$t(`facilitiesSheet.${key}.icon`),
            labelKey: key,
            value: facilities.bathroomFacilities[key]
          };
          bathroomFacilities.push(obj);
        }
      }
      facilitiesObj = { basicFacilities, bedroomFacilities, bathroomFacilities };
      return facilitiesObj;
    },
    keyPrefix() {
      return this.$route.name == 'search'
        ? 'search-'
        : this.$route.name == 'wishList'
        ? 'wishList-'
        : 'general-prefix';
    },
    bedTypes() {
      const bedTypesArray = [];
      const tempArray = [];
      for (let key in this.roomType.bedType) {
        if (key != 'total' && this.roomType.bedType[key] > 0) {
          const bedTypesObj = {
            bedTypeName: key,
            bedNumber: this.roomType.bedType[key]
          };
          tempArray.push(bedTypesObj);
          bedTypesArray.push(bedTypesObj);
        }
        const order = { single: 1, double: 2, king: 3, crib: 4, floorMattress: 5 };
        bedTypesArray.sort(function(a, b) {
          return order[a.bedTypeName] - order[b.bedTypeName];
        });
      }
      return bedTypesArray;
    }
  },
  methods: {
    checkFacilityContainer() {
      this.$nextTick(() => {
        if (this.$refs.facilitiesContainer && this.$refs.facilitiesInnerContainer) {
          this.facilityContainerHeight = this.$refs.facilitiesContainer.offsetHeight;
          this.facilityInnerContainerHeight = this.$refs.facilitiesInnerContainer.offsetHeight;
        }
      });
    },
    handleMoreInfo() {
      this.$emit('showDetail', { bedTypes: this.bedTypes, facilities: this.facilities });
    },
    handleReserve(roomType) {
      this.$emit('showReserveModal');
      this.$store.commit('ReservationModules/SET_SELECT_ROOM', roomType);
    }
  },
  mounted() {
    this.$emit('mounted');
    // console.log(
    //   'roomtypeoffsetHeight',
    //   this.$refs.facilitiesContainer.offsetHeight,
    //   this.$refs.facilitiesInnerContainer.offsetHeight
    // );
    this.checkFacilityContainer();
  }
};
</script>

<style lang="scss" scoped src="@/assets/css/components/_RoomTypeCard.scss"></style>
