<template>
  <div ref="productContainer" :class="{ md: !_windowSize.lg.larger }" class="product-container">
    <transition name="el-fade-in">
      <el-button
        class="fix-top-back-btn"
        v-if="navHalfShow"
        type="text"
        @click.prevent="close"
        :class="{
          'half-show': navHalfShow,
          'all-show': navAllShow
        }"
        ><i class="el-icon-back"></i>
      </el-button>
    </transition>
    <div
      v-if="navAllShow"
      class="product-nav"
      :style="{ width: _windowSize.mapPointLg.larger ? '1000px' : '100%' }"
    >
      <!-- <transition name="el-fade-in">
          <el-button v-if="asideScroll > 42" type="text" @click.prevent="close"
            ><i class="el-icon-back"></i
          ></el-button>
        </transition> -->
      <span class="title">{{ mapProduct.select.info.title }}</span>

      <div ref="subjectListWrapper" class="sbj-list-wrapper">
        <div class="sbj-list">
          <button
            ref="roomTypeBtn"
            class="sbj-btn"
            @click="handleScrollTo('roomType')"
            :class="{ view: isInRoomTypeView }"
            :disabled="isInRoomTypeView"
          >
            {{ $t('stayPage.roomType') }}
          </button>
          <button
            ref="introBtn"
            class="sbj-btn"
            @click="handleScrollTo('intro')"
            :class="{ view: isInIntroView }"
            :disabled="isInIntroView"
          >
            {{
              $t('stayPage.stayIntro', {
                stayCategory: $t(`stayCategory.${mapProduct.select.info.category}`)
              })
            }}
          </button>
          <button
            ref="serviceBtn"
            class="sbj-btn"
            @click="handleScrollTo('service')"
            :class="{ view: isInServiceView }"
            :disabled="isInServiceView"
          >
            {{ $t(`stayPage.facilityService`) }}
          </button>
        </div>
      </div>
      <!--  -->
      <button
        @click.stop="handleWishList(mapProduct.select.id, wishList.includes(mapProduct.select.id))"
        class="wish-list"
        aria-label=" add to wish list"
      >
        <span>
          <i v-if="!wishList.includes(mapProduct.select.id)" class="far fa-heart"></i>
          <i v-if="wishList.includes(mapProduct.select.id)" class="fas fa-heart"></i>
        </span>
      </button>
    </div>
    <div ref="topBackNav">
      <el-button class="top-back-nav" type="text" @click.prevent="close"
        ><i class="el-icon-back"></i> {{ $t('stayPage.back') }}</el-button
      >
    </div>
    <PhotoDisplaySection @showViewer="showViewer = true"></PhotoDisplaySection>
    <div ref="productHeader" class="header">
      <!--  -->
      <button
        @click.stop="handleWishList(mapProduct.select.id, wishList.includes(mapProduct.select.id))"
        class="wish-list"
        aria-label="add to wish list"
      >
        <i v-if="!wishList.includes(mapProduct.select.id)" class="far fa-heart"></i>
        <i v-if="wishList.includes(mapProduct.select.id)" class="fas fa-heart"></i>
      </button>
      <!--   -->
      <div class="sub-title">
        {{
          $t('mapList.sortDescription', {
            location:
              mapProduct.select.info.mapInfo.address_components[
                mapProduct.select.info.mapInfo.address_components.length - 4
              ].long_name,
            roomCategory: $t(`stayCategory.${mapProduct.select.info.category}`)
          })
        }}
      </div>
      <div class="title">{{ mapProduct.select.info.title }}</div>
      <div class="title-divider"></div>

      <div class="sbj-list-wrapper">
        <div ref="subjectHeader" class="sbj-list">
          <button @click="handleScrollTo('roomType')" class="sbj-btn">
            {{ $t('stayPage.roomType') }}
          </button>
          <button @click="handleScrollTo('intro')" class="sbj-btn">
            {{
              $t('stayPage.stayIntro', {
                stayCategory: $t(`stayCategory.${mapProduct.select.info.category}`)
              })
            }}
          </button>
          <button @click="handleScrollTo('service')" class="sbj-btn">
            {{ $t(`stayPage.facilityService`) }}
          </button>
        </div>
      </div>
    </div>
    <el-divider class="header-divider"></el-divider>
    <section>
      <div ref="roomTypeSubject" class="suject-title room-type-section">
        {{ $t('stayPage.roomType') }}
        <el-dropdown
          v-if="mapPageMode == 'search'"
          @command="handleCommand"
          placement="bottom"
          trigger="click"
        >
          <button v-if="mapProduct.select.computedResult" class="el-dropdown-link">
            {{
              roomTypeListFilter == 'fitSearch'
                ? $t('stayPage.searchResults')
                : roomTypeListFilter == 'showAll'
                ? $t('stayPage.allRooms')
                : $t('stayPage.searchResults')
            }}
            <i class="el-icon-arrow-down el-icon--right"></i>
          </button>
          <el-dropdown-menu
            v-if="mapProduct.select.computedResult"
            class="roomtype-dropdown"
            slot="dropdown"
          >
            <el-dropdown-item command="fitSearch">{{
              $t('stayPage.searchResults')
            }}</el-dropdown-item>
            <el-dropdown-item command="showAll">{{ $t('stayPage.allRooms') }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>

      <template v-if="roomTypeListFilter == 'fitSearch' && mapProduct.select.computedResult">
        <roomTypeCard
          v-for="(roomType, index) in mapProduct.select.computedResult.matchRoomTypes"
          :roomType="roomType"
          :index="index"
          :number="mapProduct.select.computedResult.matchRoomTypes.length"
          :key="'roomType-' + roomType.id"
          @showDetail="handleShowDetail"
          @showReserveModal="handleShowReserveModal"
          @mounted="handleRoomTypeCardMounted('fitSearch')"
        >
        </roomTypeCard>
      </template>
      <template v-if="roomTypeListFilter == 'showAll'">
        <roomTypeCard
          v-for="(roomType, index) in mapProduct.select.roomTypes"
          :roomType="roomType"
          :index="index"
          :number="mapProduct.select.roomTypes.length"
          :key="'roomType-' + roomType.id"
          @showDetail="handleShowDetail"
          @showReserveModal="handleShowReserveModal"
          @mounted="handleRoomTypeCardMounted('showAll')"
        >
        </roomTypeCard>
      </template>
    </section>
    <el-divider></el-divider>
    <section>
      <div ref="introSubject" class="suject-title">
        {{
          $t('stayPage.stayIntro', {
            stayCategory: $t(`stayCategory.${mapProduct.select.info.category}`)
          })
        }}
      </div>
      <el-card class="product-info-card" shadow="never">
        <div v-html="convertedDescription" class="description-content"></div>
      </el-card>
    </section>
    <el-divider></el-divider>
    <section>
      <div ref="serviceSubject" class="suject-title">{{ $t(`stayPage.facilityService`) }}</div>
      <el-card shadow="never" class="stay-facility">
        <PropertyFacility :product="mapProduct.select"></PropertyFacility>
      </el-card>
    </section>
    <!-- <PhotoPreview
      :showViewer="showPhotoViewer"
      :previewSrcList="mapProduct.select.photos.normalImgs"
    ></PhotoPreview> -->
    <Reserve v-if="reserveModalVisible" :visible.sync="reserveModalVisible"></Reserve>
    <RoomTypeDetail
      v-if="!_windowSize.lg.larger"
      :visible.sync="detailVisible"
      :detail="roomDetail"
    ></RoomTypeDetail>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import roomTypeCard from './roomTypeCard';
import PropertyFacility from './PropertyFacility';
import PhotoDisplaySection from './PhotoDisplaySection';
// import PhotoPreview from './PhotoPreview';
import RoomTypeDetail from './RoomTypeDetail';
import Reserve from './Reserve';
export default {
  data() {
    return {
      imageLength: 4,
      topBackNavHeight: null,
      subjectHeaderPosition: null,
      roomTypeViewPoint: null,
      introViewPoint: null,
      serviceViewPoint: null,
      roomTypeListFilter: 'showAll',
      productContainerHeight: null,
      showViewer: false,
      detailVisible: false,
      roomDetail: null,
      reserveModalVisible: false
    };
  },
  components: {
    roomTypeCard,
    RoomTypeDetail,
    PropertyFacility,
    PhotoDisplaySection,
    Reserve
  },
  watch: {
    'mapProduct.select': {
      handler() {
        this.$nextTick(() => {
          this.initialUI();
        });
      },
      deep: true
    },
    mapPageMode() {
      this.$store.dispatch('MapModules/selectMapProduct', [null]);
      this.$store.dispatch('MapModules/hoverMapProduct', { product: null, hoverClass: false });
    },
    isInRoomTypeView(newVal) {
      if (newVal && this.$refs.subjectListWrapper) {
        this.$refs.subjectListWrapper.scrollLeft = this.$refs.roomTypeBtn.offsetLeft - 159;
      }
    },
    isInIntroView(newVal) {
      if (newVal && this.$refs.subjectListWrapper) {
        this.$refs.subjectListWrapper.scrollLeft = this.$refs.introBtn.offsetLeft - 159;
      }
    },
    isInServiceView(newVal) {
      if (newVal && this.$refs.subjectListWrapper) {
        this.$refs.subjectListWrapper.scrollLeft = this.$refs.serviceBtn.offsetLeft - 159;
      }
    }
  },
  computed: {
    ...mapState('MapModules', ['mapProduct']),
    ...mapState(['asideScroll', 'mapPageMode']),
    ...mapState('WishlistModules', ['wishList']),
    ...mapState('UIModules', ['_windowSize']),
    isInRoomTypeView() {
      return (
        this.introViewPoint > this.calcScrollRefPoint &&
        this.calcScrollRefPoint >= this.roomTypeViewPoint
      );
    },
    isInIntroView() {
      return (
        this.serviceViewPoint > this.calcScrollRefPoint &&
        this.calcScrollRefPoint >= this.introViewPoint
      );
    },
    isInServiceView() {
      return this.calcScrollRefPoint >= this.serviceViewPoint;
    },
    views() {
      return {
        isInRoomTypeView: this.isInRoomTypeView,
        isInIntroView: this.isInIntroView,
        isInServiceView: this.isInServiceView
      };
    },
    navHalfShow() {
      return this.asideScroll > this.topBackNavHeight;
    },
    navAllShow() {
      return this.asideScroll > this.subjectHeaderPosition - 3;
    },
    calcScrollRefPoint() {
      //1/2
      return this.asideScroll + ((this.productContainerHeight - 75) * 3) / 5;
    },
    convertedDescription() {
      // this.mapProduct.select.info.description
      const convertedText = this.mapProduct.select.info.description.replace(
        /\r\n|\r|\n/g,
        '<br />'
      );
      return convertedText;
    }
  },
  methods: {
    close() {
      //idscroll
      this.$bus.$emit('backToProductsList');
      this.$store.dispatch('MapModules/scrollToMapProduct', this.mapProduct.select.id);
      this.$store.dispatch('MapModules/selectMapProduct', [null, true]);
      this.$store.dispatch('MapModules/hoverMapProduct', { product: null, hoverClass: false });
    },
    initialUI(
      roomTypeListFilter = this.mapProduct.select.computedResult ? 'fitSearch' : 'showAll'
    ) {
      // console.log('initialUI');

      this.topBackNavHeight = this.$refs.topBackNav.offsetHeight;
      this.subjectHeaderPosition =
        this.$refs.productHeader.offsetTop + this.$refs.subjectHeader.offsetTop - 10.5;
      this.roomTypeViewPoint = this.$refs.roomTypeSubject.offsetTop - 24;
      this.introViewPoint = this.$refs.introSubject.offsetTop - 24;
      this.serviceViewPoint = this.$refs.serviceSubject.offsetTop - 24;
      this.roomTypeListFilter = roomTypeListFilter;
      // console.log('roomTypeViewPoint', this.roomTypeViewPoint);
    },
    handleCommand(command) {
      // console.log(command);
      this.$nextTick(() => {
        this.initialUI(command);
      });
    },
    handleScrollTo(val) {
      if (val == 'roomType') {
        this.$bus.$emit('handleScrollTo', this.$refs.roomTypeSubject.offsetTop - 98);
      }
      if (val == 'intro') {
        this.$bus.$emit('handleScrollTo', this.$refs.introSubject.offsetTop - 98);
      }
      if (val == 'service') {
        this.$bus.$emit('handleScrollTo', this.$refs.serviceSubject.offsetTop - 98);
      }
    },
    setProdocutContainerHeight() {
      //TODO: scrollsrollTop
      this.initialUI();
      //parenet elementclientHeight
      this.productContainerHeight = this.$refs.productContainer.parentNode.clientHeight;
      // console.log('productContainerHeight', this.productContainerHeight);
    },
    handleRoomTypeCardMounted(command) {
      this.initialUI(command);
    },
    handleWishList(id, included) {
      if (this.$route.name == 'wishList') {
        if (included) {
          this.$confirm('', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$store.dispatch('WishlistModules/handleWishList', [id, this.$route.name]);
            this.close();
          });
        } else {
          this.$store.dispatch('WishlistModules/handleWishList', [id, this.$route.name]);
        }
      } else {
        this.$store.dispatch('WishlistModules/handleWishList', [id, this.$route.name]);
      }
    },
    handleShowDetail(detail) {
      this.detailVisible = true;
      // console.log('roomInfo', detail);
      this.roomDetail = detail;
    },
    handleShowReserveModal() {
      if (!this.reserveModalVisible) {
        this.reserveModalVisible = true;
        this.$store.commit('ReservationModules/SET_SELECT_PROPERTY', this.mapProduct.select);
      }
    }
  },
  created() {
    this.$bus.$emit('addProductScrollListener');
  },
  mounted() {
    this.$nextTick(() => {
      this.setProdocutContainerHeight();
    });
    this.$bus.$on('resize', this.setProdocutContainerHeight);
    this.$nextTick(() => {
      this.initialUI();
    });
  },
  beforeDestroy() {
    this.$bus.$off('resize', this.setProdocutContainerHeight);
    this.$bus.$emit('destroyProductScrollListener');
    this.$store.dispatch('MapModules/selectMapProduct', [null]);
  }
};
</script>

<style lang="scss" scoped src="@/assets/css/components/_MapProduct.scss"></style>
<style>
.roomtype-dropdown li {
  transition: all 0.3s ease;
  padding: 0px 18px;
  font-size: 14px;
}
</style>
