        photos: { normalImgs: [], panoramas: [] },
        bedType: {
          single: 0,
          double: 0,
          king: 0,
          floorMattress: 0,
          crib: 0,
          total: 0
        },
        //TODO: ObjectArray
        facilities: {
          basicFacilities: {
            independentBathroom: false,
            smokingRoom: false,
            kitchen: false,
            parlor: false,
            electronicLock: false,
