            bathrobe: false
          }
        }
      },
      editPropertyOption: {
        collapeActiveNames: [],
        service: [
          { label: '24', model: 'wholeDayCounter' },
          { label: '', model: 'selfCheck' },
          { label: '', model: 'pickUp' },
          { label: '', model: 'luggageDeposit' },
          { label: '', model: 'earlyCheckIn' },
          { label: '', model: 'lostPropertyStorage' },
          { label: '', model: 'parkLot' },
          { label: '', model: 'valetParking' }
        ],
        facility: {
          catering: [
            { label: '', model: 'restaurant' },
            { label: '', model: 'bar' },
            { label: '', model: 'kitchen' }
          ],
          entertainment: [
            { label: '', model: 'readingRoom' },
