      mapSelect: null,
      mapSelections: [],
      marker: null,
      mapDialogVisible: false,
      tabActiveName: 'info',
      state: {
        uploadingProperty: false,
        editRoomType: false,
        updateRoomType: false,
        createRoomType: false
      },
      displayRoomTypes: null
    };
  },
  computed: {
    ...mapState('ProductsModules', ['products', 'pagination']),
    isLoading() {
      return this.$store.state.isLoading;
    },
    displayMapConfig() {
      return {
        ...mapSettings,
        zoom: 18,
        gestureHandling: 'none',
        zoomControl: false,
        disableDefaultUI: false,
        panControlOptions: false,
        clickableIcons: false,
        streetViewControl: false,
        fullscreenControl: false,
        center: this.tempProperty.info.LatLng
      };
    },
    imgPrewviewSrcList() {
      const listArray = this.tempProperty.photos.normalImgs.map(obj => obj.url);
      return listArray;
    }
  },
  watch: {
    'tempRoom.bedType': {
      handler(newNum, oldNum) {
        const newTotal = Object.values(newNum).reduce((t, n) => t + n) - oldNum.total;
        this.tempRoom.bedType.total = newTotal;
      },
      deep: true
    },
    state: {
      handler() {
        this.$set(this.editRoomTypeOption, 'collapeActiveNames', []);
      },
      immediate: true
    },
    tabActiveName: {
      handler() {
        this.handleCancelCreateRoomType();
      }
    },
    tempRoom: {
      handler(newTempRoom) {
        if (!newTempRoom.photos) {
          this.$set(this.tempRoom, 'photos', {});
        }
      },
      immediate: true
    }
  },
  methods: {
    ...mapActions('ProductsModules', ['getProducts']),
    handleCancelCreateRoomType() {
      this.state.editRoomType = false;
      this.tempRoom = JSON.parse(JSON.stringify(this.tempRoomInit));
    },
    formatter(row) {
      return this.$options.filters.currency(row.price);
    },
    roomTypeFormatter(row) {
      return this.$t(`roomTypeCategory.${row.type}`);
    },
    updateProduct() {
      if (this.uploadingFile > 0) {
        this.notifyMessage('', '', 'warning');
        return;
      }
      this.$store.dispatch('updateLoading', true);
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`;
      const vm = this;
      let httpMethod = 'post';
      if (!this.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${this.tempProperty.id}`;
        httpMethod = 'put';
      }
      this.state.uploadingProperty = true;
      this.$http[httpMethod](api, { data: vm.tempProperty }).then(response => {
        if (response.data.success) {
          this.$message({
            showClose: true,
            center: true,
            type: 'success',
            message: response.data.message
          });
          this.$router.push({ name: 'Products' }).catch(err => {
            console.log(err);
          });
        } else {
          this.$message({
            showClose: true,
            center: true,
            type: 'danger',
            message: response.data.message
          });
        }
      });
    },
    editRoomType(item = null) {
      this.state.editRoomType = true;
      if (item) {
        this.state.createRoomType = false;
        this.tempRoom = JSON.parse(JSON.stringify(item));
      } else {
        this.state.createRoomType = true;
        this.tempRoom = JSON.parse(JSON.stringify(this.tempRoomInit));
        // this.state.createRoomType = true;
      }
    },
    duplicateRoomType(item = null) {
      const newObj = JSON.parse(JSON.stringify(item));
      newObj.id = this.$custom.getUuid();
      newObj.name += ' - ';

      this.tempProperty.roomTypes.push(newObj);
      this.orderRoomType();
    },
    finishEditRoomType() {
      if (this.uploadingFile > 0) {
        this.notifyMessage('', '', 'warning');
        return;
      }
      if (this.state.createRoomType) {
        this.addRoomType();
      } else if (this.state.editRoomType) {
        this.updateRoomType();
      }
      this.tempRoom = JSON.parse(JSON.stringify(this.tempRoomInit));
      this.state.editRoomType = false;
    },
    addRoomType() {
      this.state.createRoomType = true;
      this.tempRoom.id = this.$custom.getUuid();
      if (!this.tempProperty.roomTypes) {
        this.tempProperty.roomTypes = [];
      }
      this.tempProperty.roomTypes.unshift(JSON.parse(JSON.stringify(this.tempRoom)));
      this.orderRoomType();
      this.tempRoom = JSON.parse(JSON.stringify(this.tempRoomInit));
      this.state.createRoomType = false;
    },
    orderRoomType() {
      //TODO:
      this.tempProperty.roomTypes = this.$custom.quickSortObj(
        this.tempProperty.roomTypes,
        'pplNum'
      );
    },
    updateRoomType() {
      let updateTarget = this.tempProperty.roomTypes.find(obj => obj.id == this.tempRoom.id);
      let index = this.tempProperty.roomTypes.indexOf(updateTarget);
      this.tempProperty.roomTypes[index] = this.tempRoom;
    },
    removeRoomType(item) {
      this.tempProperty.roomTypes.splice(this.tempProperty.roomTypes.indexOf(item), 1);
    },
    initState() {
      this.state.editingRoomType = null;
      this.state.createRoomType = false;
      this.state.comfirmDelRoomType = null;
    },
    saveAddress(mapInfo) {
      this.tempProperty.info.mapInfo = mapInfo;
      this.tempProperty.info.LatLng = mapInfo.geometry.location;
      this.tempProperty.info.address = mapInfo.formatted_address;
      this.tempProperty.info.mapInfo.shortAddress = `${
        mapInfo.address_components[mapInfo.address_components.length - 4].long_name
      }, ${mapInfo.address_components[mapInfo.address_components.length - 3].long_name}`;
    },
    validNotify(invalid) {
      if (invalid) {
        const getErrors = setInterval(() => {
          let errors = this.$refs.validObserver.errors;
          let errorsArray = [];
          for (var key in errors) {
            if (errors[key].length > 0) {
              errorsArray.push(errors[key][0].split(' ')[0]);
            }
          }
          if (errorsArray.length < 1) {
            clearInterval(getErrors);
            return;
          }
          const message = errorsArray.join('');
          this.notifyMessage('', message, 'warning');
          // this.$notify({
          //   title: '',
          //   message,
          //   type: 'warning',
          //   duration: 5000
          // });
          clearInterval(getErrors);
        }, 100);
      }
    },
    notifyMessage(title, message, type, duration = 5000) {
      this.$notify({
        title,
        message,
        type,
        duration
      });
    }
  },
  created() {
    this.isNew = this.$route.params.isNew;
    this.editItem = this.$route.params.editItem ? this.$route.params.editItem : null;
    if (!this.isNew && !this.editItem) {
      this.$router.push({ name: 'Products' }).catch(err => {
        console.log(err);
      });
    }
    if (this.editItem) {
      if (!this.editItem.photos.panoramas) {
        this.editItem.photos.panoramas = [];
      }
      if (!this.editItem.photos.normalImgs) {
        this.editItem.photos.normalImgs = [];
      }
      this.tempProperty = JSON.parse(JSON.stringify(this.editItem));
    }
  }
};
</script>
