<template>
  <div class="facilities-group">
    <template v-for="(subject, index) in subjects">
      <div v-if="subject.length > 0" class="facility-subject" :key="index">
        <div class="facility-title">{{ $t(`stayPage.${(Object.keys(subjects), index)}`) }}</div>
        <!-- <div class="facility-title">{{ (Object.keys(subjects), index) }}</div> -->
        <div class="facility-list-wrapper">
          <ul class="facility-list">
            <li class="collapse-item" v-for="facility in subject" :key="facility.id">
              <i :class="facility.icon"></i>
              {{ ` ${facility.label}` }}
            </li>
          </ul>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  props: {
    product: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      facilitiesSheet: {
        wholeDayCounter: { icon: 'fas fa-user-clock', label: '24' },
        selfCheck: { icon: 'el-icon-check', label: '' },
        pickUp: { icon: 'fas fa-bus-alt', label: '' },
        luggageDeposit: { icon: 'fas fa-luggage-cart', label: '' },
        earlyCheckIn: { icon: 'el-icon-finished', label: '' },
        lostPropertyStorage: { icon: 'fas fa-toolbox', label: '' },
        parkLot: { icon: 'el-icon-house', label: '' },
        valetParking: { icon: 'fas fa-car-side', label: '' },
        restaurant: { icon: 'fas fa-utensils', label: '' },
        bar: { icon: 'fas fa-wine-glass-alt', label: '' },
        kitchen: { icon: 'fas fa-utensils', label: '' },
        readingRoom: { icon: 'fas fa-book-open', label: '' },
        banquetRoom: { icon: 'el-icon-video-camera-solid', label: '' },
        audiovisualHall: { icon: 'fas fa-cocktail', label: '' },
        swimmingPoor: { icon: 'fas fa-swimming-pool', label: '' },
        publicBathhouse: { icon: 'fas fa-water', label: '' },
        meetingRoom: { icon: 'fas fa-chalkboard-teacher', label: '' },
        publicComputers: { icon: 'fas fa-laptop-house', label: '' },
        wholeDayWasher: { icon: 'fas fa-tint', label: '24' },
        wholeDayDryer: { icon: 'fas fa-tshirt', label: '24' },
        ironingFacility: { icon: 'fas fa-weight-hanging', label: '' },
        convenienceStore: { icon: 'fas fa-store-alt', label: '24' }
      }
    };
  },
  computed: {
    subjects() {
      return {
        service: this.service,
        foodAndDrink: this.catering,
        entertainment: this.entertainment,
        businessAndLeisure: this.bussinessLife
      };
    },
    service() {
      const serviceObj = this.product.service;
      const serviceArray = [];
      for (let key in serviceObj) {
        if (serviceObj[key]) {
          const obj = {
            id: this.$custom.getUuid(),
            icon: this.$t(`stayServiceAndFacilities.${key}.icon`),
            label: this.$t(`stayServiceAndFacilities.${key}.label`)
          };
          serviceArray.push(obj);
        }
      }
      return serviceArray;
    },
    catering() {
      const cateringObj = this.product.facility.catering;
      const cateringArray = [];
      for (let key in cateringObj) {
        if (cateringObj[key]) {
          const obj = {
            id: this.$custom.getUuid(),
            icon: this.$t(`stayServiceAndFacilities.${key}.icon`),
            label: this.$t(`stayServiceAndFacilities.${key}.label`)
          };
          cateringArray.push(obj);
        }
      }
      return cateringArray;
    },
    entertainment() {
      const entertainmentObj = this.product.facility.entertainment;
      const entertainmentArray = [];
      for (let key in entertainmentObj) {
        if (entertainmentObj[key]) {
          const obj = {
            id: this.$custom.getUuid(),
            icon: this.$t(`stayServiceAndFacilities.${key}.icon`),
            label: this.$t(`stayServiceAndFacilities.${key}.label`)
          };
          entertainmentArray.push(obj);
        }
      }
      return entertainmentArray;
    },
    bussinessLife() {
      const bussinessLifeObj = this.product.facility.bussinessLife;
      const bussinessLifeArray = [];
      for (let key in bussinessLifeObj) {
        if (bussinessLifeObj[key]) {
          const obj = {
            id: this.$custom.getUuid(),
            icon: this.$t(`stayServiceAndFacilities.${key}.icon`),
            label: this.$t(`stayServiceAndFacilities.${key}.label`)
          };
          bussinessLifeArray.push(obj);
        }
      }
      return bussinessLifeArray;
    }
  }
};
</script>

