<template>
  <div
    class="stays-container"
    :class="{ 'hover-wrapper': hoverWrapper, md: !_windowSize.mapPointLg.larger }"
    ref="staysContainer"
  >
    <div class="listHeader">
      <template v-if="$route.name == 'wishList'">
        <template v-if="staySource.length > 0">
          <div class="resultsNum">
            {{
              `${staySource.length} ${this.$t(
                'mapList.staysNumber'
              )}   ${this.$t('mapList.roomTypesNumber', { num: wishListRoomTypeNumber })}`
            }}
          </div>
          <div class="searchRange">
            {{ $t('mapList.myWishList') }}
          </div>
        </template>
        <template v-if="staySource.length < 1 && !isLoading">
          <div class="noResult">
            {{ $t('mapList.emptyWishList') }}
          </div>
        </template>
      </template>
      <template v-if="$route.name == 'search'">
        <template v-if="staySource.length > 0">
          <div class="resultsNum">
            {{
              `${staySource.length} ${this.$t(
                'mapList.staysNumber'
              )}  ${this.$t('mapList.roomTypesNumber', { num: roomTypeNumber })}`
            }}
          </div>
          <div class="searchRange">
            {{
              $t('mapList.searchRange', {
                location: `${
                  searchByMap
                    ? $t('mapList.designatedArea')
                    : `${searchFilter.location.value ? searchFilter.location.value : null}`
                }`
              })
            }}
          </div>
        </template>
        <template
          v-if="staySource.length < 1 && !isLoading && (searchFilter.location || searchByMap)"
        >
          <div class="noResult">
            {{
              $t('mapList.noMatchStay', {
                location: `${
                  searchByMap
                    ? $t('mapList.designatedArea')
                    : `${searchFilter.location ? searchFilter.location.value : ''}`
                }`
              })
            }}
          </div>
        </template>
      </template>
    </div>
    <template v-for="item in paginatedStaySource[currentPage - 1]">
      <div
        class="stay-container"
        :key="keyPrefix + 'stay-container-' + item.id + searchKey"
        :ref="'stay-container-' + item.id"
        :class="{ hover: item == $store.state.MapModules.mapProduct.hover }"
        @click="selectProduct(item)"
        @mouseenter="hoverProduct(item, true)"
        @mouseleave="hoverProduct(item, false)"
      >
        <button
          @click.stop="handleWishList(item.id, wishList.includes(item.id))"
          class="wish-list"
          aria-label="add to wish list"
        >
          <i v-if="!wishList.includes(item.id)" class="far fa-heart"></i>
          <i
            v-if="wishList.includes(item.id)"
            :class="{
              'fas fa-heart': $route.name != 'wishList',
              'el-icon-delete': $route.name == 'wishList'
            }"
          ></i>
        </button>
        <div class="img-container">
          <el-carousel
            :indicator-position="item.photos.normalImgs.length < 2 ? 'none' : ''"
            :arrow="item.photos.normalImgs.length < 2 ? 'never' : 'hover'"
            :autoplay="false"
            trigger="click"
          >
            <el-carousel-item
              v-for="item in item.photos.normalImgs"
              :key="keyPrefix + 'product-image-' + item.id + searchKey"
            >
              <keep-alive>
                <el-image :src="item.url" :alt="item.title" fit="cover"></el-image>
              </keep-alive>
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="info-container">
          <span class="sub-title">
            {{
              $t('mapList.sortDescription', {
                location:
                  item.info.mapInfo.address_components[
                    item.info.mapInfo.address_components.length - 4
                  ].long_name,
                roomCategory: $t(`stayCategory.${item.info.category}`)
              })
            }}
          </span>
          <div class="title">{{ item.info.title }}</div>
          <div style="border-top: 1px solid #DDDDDD;width:30px;margin-top:10px"></div>
          <!-- <div class="description">{{ item.info.description }}</div> -->
          <ul class="roomType-list">
            <template v-if="item.computedResult.matchRoomTypes.length < 4">
              <li
                v-for="roomType in item.computedResult.matchRoomTypes"
                :key="keyPrefix + roomType.id + searchKey"
              >
                {{
                  `${roomType.name}  ${$t(`roomTypeCategory.${roomType.type}`)}  ${
                    roomType.pplNum > 1
                      ? $t('mapList.guestsUnit', { num: roomType.pplNum })
                      : $t('mapList.guestUnit', { num: roomType.pplNum })
                  }  ${
                    roomType.bedType.total > 1
                      ? $t('mapList.bedsNumberUnit', { num: roomType.bedType.total })
                      : $t('mapList.bedNumberUnit', { num: roomType.bedType.total })
                  }  `
                }}
                {{ roomType.price | currency }}
              </li>
            </template>
            <template v-else>
              <li
                v-for="roomType in item.computedResult.matchRoomTypes.slice(0, 3)"
                :key="keyPrefix + roomType.id + searchKey"
              >
                {{
                  `${roomType.name}  ${$t(`roomTypeCategory.${roomType.type}`)}  ${
                    roomType.pplNum > 1
                      ? $t('mapList.guestsUnit', { num: roomType.pplNum })
                      : $t('mapList.guestUnit', { num: roomType.pplNum })
                  }  ${
                    roomType.bedType.total > 1
                      ? $t('mapList.bedsNumberUnit', { num: roomType.bedType.total })
                      : $t('mapList.bedNumberUnit', { num: roomType.bedType.total })
                  }  `
                }}
                {{ roomType.price | currency }}
              </li>
              <li>
                <span style="writing-mode: vertical-rl;">...</span>
              </li>
            </template>
          </ul>
          <div class="price">
            <span
              >{{ $t('mapList.eachNight.before') == 'true' ? $t('mapList.eachNight.text') : '' }}
              <span class="price-bold">
                {{ item.computedResult.sortedPrices[0] | currency(false)
                }}{{
                  item.computedResult.sortedPrices.length > 1 &&
                  item.computedResult.sortedPrices[0] !=
                    item.computedResult.sortedPrices[item.computedResult.sortedPrices.length - 1]
                    ? ' - '
                    : ''
                }}
                <span
                  v-if="
                    item.computedResult.sortedPrices.length > 1 &&
                      item.computedResult.sortedPrices[0] !=
                        item.computedResult.sortedPrices[
                          item.computedResult.sortedPrices.length - 1
                        ]
                  "
                >
                  {{
                    item.computedResult.sortedPrices.length > 1
                      ? item.computedResult.sortedPrices[
                          item.computedResult.sortedPrices.length - 1
                        ]
                      : '' | currency(false)
                  }}</span
                >
                TWD</span
              >
              {{
                $t('mapList.eachNight.before') == 'false' ? '/ ' + $t('mapList.eachNight.text') : ''
              }}
            </span>
          </div>
        </div>
      </div>
      <el-divider
        v-if="paginatedStaySource[currentPage - 1].length > 1"
        :key="keyPrefix + item.id + 'divider' + searchKey"
      />
    </template>

    <span
      v-if="paginatedStaySource.length > 1"
      @mouseenter="paginationHover = true"
      @mouseleave="paginationHover = false"
      class="pagination-wrapper"
    >
      <span class="sort-page" v-if="!paginationHover && _windowSize.mapPointLg.larger && false">{{
        ` ${currentPage} `
      }}</span>
      <el-pagination
        :pager-count="5"
        :layout="`prev, pager, next`"
        :page-count="paginatedStaySource.length"
        :current-page="currentPage"
        @current-change="handlePageChange"
      >
      </el-pagination>
    </span>
    <!-- <span style="display:none">{{ pagination }}</span> -->
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
export default {
  data() {
    return {
      pageNumber: 1,
      paginationHover: false
    };
  },
  computed: {
    ...mapGetters(['isLoading', 'staySource', 'paginatedStaySource']),
    ...mapState('SearchModules', [
      'searchFilter',
      'filterSearchProducts',
      'searchByMap',
      'roomTypeNumber',
      'searchKey'
    ]),
    ...mapState('MapModules', {
      google: state => state.google,
      scrollToId: state => state.mapProduct.scrollToId,
      bounds: state => state.bounds
    }),
    ...mapState(['mapPageMode', 'currentPage']),
    ...mapState('WishlistModules', ['wishList', 'wishListProducts', 'wishListRoomTypeNumber']),
    ...mapState('UIModules', ['_windowSize', 'asideVisible']),
    hoverWrapper() {
      return this.$store.state.MapModules.mapProduct.hover;
    },
    keyPrefix() {
      return this.$route.name == 'search'
        ? 'search-'
        : this.$route.name == 'wishList'
        ? 'wishList-'
        : 'general-prefix';
    }
  },
  watch: {
    scrollToId(newVal) {
      if (newVal) {
        this.scrollToProduct(newVal);
      }
    },
    '$route.name'() {
      this.$parent.$el.scrollTo(0, 0);
    }
  },
  methods: {
    selectProduct(product) {
      this.$store.dispatch('MapModules/selectMapProduct', [product, false]);
    },
    hoverProduct(product, val) {
      //TODO: if this.bound doesn't contain given point, google.maps.extend(point)
      if (product != this.$store.state.MapModules.mapProduct.hover) {
        this.$store.dispatch(
          'MapModules/hoverMapProduct',
          val ? { product, hoverClass: true } : { product: null, hoverClass: val }
        );
        return;
      } else {
        this.$store.dispatch('MapModules/hoverMapProduct', { product: null, hoverClass: false });
      }
    },
    scrollToProduct(id) {
      //
      if (!this.$refs[`stay-container-${id}`]) {
        return;
      }
      const target = this.$refs[`stay-container-${id}`][0];
      const containerHeight = this.$parent.$parent.$parent.$el.clientHeight;

      if (this.$refs.staysContainer.clientHeight > containerHeight) {
        this.$parent.$el.scrollTo(0, target.offsetTop - 81);
      }
    },
    handleWishList(id, included) {
      if (this.$route.name == 'wishList') {
        if (included) {
          this.$confirm(this.$t('popconfirm.deleteFromWishList'), {
            confirmButtonText: this.$t('popconfirm.confirm'),
            cancelButtonText: this.$t('popconfirm.cancel'),
            type: 'warning'
          }).then(() => {
            this.$store.dispatch('WishlistModules/handleWishList', [id, this.$route.name]);
          });
        } else {
          this.$store.dispatch('WishlistModules/handleWishList', [id, this.$route.name]);
        }
      } else {
        this.$store.dispatch('WishlistModules/handleWishList', [id, this.$route.name]);
      }
    },
    handlePageChange(currentPage) {
      this.$store.commit('CHANGE_PAGE', currentPage);
      // console.log('handlePageChange', currentPage);
    },
    handleMapVisible() {
      this.$store.commit('UIModules/SET_ASIDE_VISIBLE', !this.asideVisible);
    }
  }
};
</script>

<style lang="scss" scoped src="@/assets/css/components/_MapProducts.scss"></style>
