import axios from 'axios';
import $ from 'jquery';
export default {
  namespaced: true,
  state: {
    cartData: {
      cart: {},
      removingItem: []
    }
  },
  actions: {
    getCart(context) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      context.commit('LOADING', true, { root: true });
      axios.get(api).then(response => {
        if (response.data.success) {
          context.commit('GET_CART', response.data.data);
          console.log(response.data);
        } else {
          this.$store.dispatch('AlertModules/updateMessage', [response.data.message, 'danger']);
        }
        context.commit('LOADING', false, { root: true });
      });
    },
    addToCart(context, [id, title, qty = 1]) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      const cart = {
        product_id: id,
        qty: qty
      };
      context.dispatch('updateLoading', true, { root: true });
      axios.post(api, { data: cart }).then(response => {
        if (response.data.success) {
          context.dispatch('getCart');
          $('#productModal').modal('hide');
          context.dispatch(
            'AlertModules/updateMessage',
            [`${title.trim()}`, 'success'],
            {
              root: true
            }
          );
        } else {
          context.dispatch('AlertModules/updateMessage', [response.data.message, 'success'], {
            root: true
          });
        }
        context.dispatch('updateLoading', false, { root: true });
      });
    },
    deleteCartItem(context, [id, title]) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`;
      context.commit('REMOVING_CART_ITEM', id);
      axios.delete(api).then(response => {
        if (response.data.success) {
          context.dispatch('getCart').then(() => {
            context.commit('REMOVING_CART_ITEM', id);
          });
          context.dispatch(
            'AlertModules/updateMessage',
            [`${title.trim()}`, 'warning'],
            {
              root: true
            }
          );
        } else {
          context.dispatch('AlertModules/updateMessage', [response.data.message, 'danger']);
        }
      });
    }
  },
  mutations: {
    GET_CART(state, payload) {
      state.cartData.cart = payload;
    },
    REMOVING_CART_ITEM(state, payload) {
      const itemArr = state.cartData.removingItem;
      if (itemArr.indexOf(payload) == -1) {
        itemArr.push(payload);
      } else {
        itemArr.splice(itemArr.indexOf(payload), 1);
      }
    }
  },
  getters: {
    cart: state => state.cartData.cart,
    removingItem: state => state.cartData.removingItem
  }
};
