import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import VueAxios from 'vue-axios';
import 'bootstrap';
import ElementUI from 'element-ui';
import i18n from './config/i18n/plugins/vue-i18n';

import Loading from 'vue-loading-overlay';
import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
import 'vue-loading-overlay/dist/vue-loading.css';
import { Datetime } from 'vue-datetime';
import 'vue-datetime/dist/vue-datetime.css';
import { extend } from 'vee-validate';
import { localize } from 'vee-validate';
import { ValidationProvider, ValidationObserver } from 'vee-validate';
import { required, email, numeric, min_value } from 'vee-validate/dist/rules.js';
import zhTW from 'vee-validate/dist/locale/zh_TW.json';

import App from './App.vue';
import router from './router';
import './bus';
import currencyFilter from './filters/currency';
import dateFilter from './filters/timestampToDate';
import simpleTimeFilter from './filters/simpleTime';
import store from './store';
import CustomFns from './customFns';

Vue.config.productionTip = false;
Vue.use(ElementUI);
Vue.use(Vuex);
Vue.use(CustomFns);
Vue.use(VueAxios, axios);
Vue.component('ValidationProvider', ValidationProvider);
Vue.component('ValidationObserver', ValidationObserver);
Vue.component('Datetime', Datetime);
Vue.component('Loading', Loading);
Vue.component('PulseLoader', PulseLoader);
Vue.filter('currency', currencyFilter);
Vue.filter('date', dateFilter);
Vue.filter('simpleTime', simpleTimeFilter);
Vue.config.productionTip = false;
axios.defaults.withCredentials = true;

localize('zh_TW', zhTW);
extend('email', email);
extend('required', required);
extend('numeric', numeric);
extend('min_value', min_value);

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app');


function getCookie(cookieName) {
  let cookie = {};
  document.cookie.split(';').forEach(function(el) {
    let [key,value] = el.split('=');
    cookie[key.trim()] = value;
  })
  return cookie[cookieName];
  }

router.beforeEach((to, from, next) => {
  
  if (to.meta.requiresAuth) {
    store.commit('COUNT_LOADING', 1);

    const hexToken = getCookie('hexToken');
    const authToken = document.cookie.replace(/(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/, '$1');

    if (hexToken) {
      axios.defaults.headers.common.Authorization = authToken;
    } else {
      store.commit('AuthModules/UPDATE_USER_AUTH', false);
    }

    const api = `${process.env.VUE_APP_APIPATH}/api/user/check`;
    axios.post(api).then(response => {
      if (response.data.success) {
        next();
      }

      store.commit('COUNT_LOADING', -1);
    });
  } else {
    next();
  }
});
