export default {
  namespaced: true, //UIModules
  state: {
    datePickBtnShow: false,
    searchBarVisible: false,
    asideVisible: true,
    _windowWidth: 0,
    _windowSize: {
      xs: {
        less: false,
        inRange: false,
        larger: false
      },
      sm: {
        less: false,
        inRange: false,
        larger: false
      },
      md: {
        less: false,
        inRange: false,
        larger: false
      },
      lg: {
        less: false,
        inRange: false,
        larger: false
      },
      mapPointMd: {
        larger: false
      },
      mapPointLg: {
        larger: false
      }
    },
    loginModalVisible: false
  },
  actions: {},
  mutations: {
    SHOW_SEARCHBAR(state, payload) {
      state.searchBarVisible = payload;
    },
    SET_WINDOW_WIDTH(state, payload) {
      state._windowWidth = payload;
      state._windowSize.xs.less = payload < 321;
      state._windowSize.xs.inRange = 320 < payload && payload < 421;
      state._windowSize.xs.larger = 320 < payload;
      state._windowSize.sm.less = payload < 421;
      state._windowSize.sm.inRange = 420 < payload && payload < 481;
      state._windowSize.sm.larger = 420 < payload;
      state._windowSize.md.less = payload < 481;
      state._windowSize.md.inRange = 480 < payload && payload < 769;
      state._windowSize.md.larger = 480 < payload;
      state._windowSize.lg.less = payload < 1225;
      state._windowSize.lg.inRange = 768 < payload && payload < 1225;
      state._windowSize.lg.larger = 768 < payload;
      state._windowSize.mapPointMd.larger = 1080 < payload;
      state._windowSize.mapPointLg.larger = 1080 < payload;
    },
    SET_ASIDE_VISIBLE(state, payload) {
      state.asideVisible = payload;
    },
    SET_DATEPICKER_CLOSE_BUTTON(state, payload) {
      state.datePickBtnShow = payload;
    },
    SET_LOGIN_MODAL_VISIBLE(state, payload) {
      state.loginModalVisible = payload;
    }
  },
  getters: {}
};
