<template>
  <div>
    <div class="google-map" ref="googleMap"></div>
    <template v-if="Boolean(this.google) && Boolean(this.map)">
      <slot :google="google" :map="map" />
    </template>
  </div>
</template>

<script>
// import GoogleMapsApiLoader from 'google-maps-api-loader';

export default {
  props: {
    mapConfig: Object,
    setMarker: Object,
    initGeolocation: { type: Boolean, default: false },
    searchControl: { type: Boolean, default: false }
  },

  data() {
    return {
      marker: null,
      google: null,
      map: null,
      geocoder: null,
      latLngBounds: null,
      pos: null
    };
  },
  async mounted() {
    if (!this.$store.state.MapModules.google) {
      await this.$store.dispatch('MapModules/setGoogleAPI');
    }
    this.google = this.$store.state.MapModules.google;
    this.geocoder = this.$store.state.MapModules.geocoder;

    //without Vuex setGoogleMapAPI
    // const googleMapApi = await GoogleMapsApiLoader({
    //   apiKey: this.apiKey
    // });
    // this.google = googleMapApi;
    // this.geocoder = new this.google.maps.Geocoder();
    this.initializeMap();
    this.$nextTick(() => {
      // this.removeDefaultClass();
      // setTimeout(() => {
      //   console.log(document.getElementsByClassName('gm-style')[0]);
      //   document.getElementsByClassName('gm-style')[0].className = '';
      // }, 500);
    });
  },

  watch: {
    setMarker: function() {
      this.placeMarker(this.setMarker);
      this.setMapCenter(this.setMarker);
    }
  },
  methods: {
    removeDefaultClass(count = 0) {
      if (document.getElementsByClassName('gm-style')[0]) {
        document.getElementsByClassName('gm-style')[0].className = '';
      } else {
        setTimeout(() => {
          count += 1;
          if (count > 30) return; //
          this.removeDefaultClass(count);
        }, 0 + 60 * count);
      }
    },
    async initializeMap() {
      const mapContainer = this.$refs.googleMap;
      this.map = await new this.google.maps.Map(mapContainer, this.mapConfig);
      this.$emit('mapLoaded');
      this.addListeners();
      this.getGeolocation();
      //Marker
      if (this.setMarker) {
        this.placeMarker(this.setMarker);
      }
      this.$bus.$emit('mapLoaded');
      this.$store.commit('COUNT_LOADING', -1);
    },
    setMapCenter(location) {
      this.map.setCenter(location);
    },
    placeMarker(latLng) {
      if (this.marker) this.removeMarkers();
      let marker = new this.google.maps.Marker({
        position: latLng,
        map: this.map,
        animation: this.google.maps.Animation.DROP
      });
      this.marker = marker;
    },
    removeMarkers() {
      this.marker.setMap(null);
    },
    //UserMarker
    getGeolocation() {
      const vm = this;
      const infoWindow = new vm.google.maps.InfoWindow();
      // Try HTML5 geolocation.
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          function(position) {
            vm.pos = {
              lat: position.coords.latitude,
              lng: position.coords.longitude
            };
            var locationMarker = new vm.google.maps.Marker({
              position: vm.pos,
              icon: {
                url: 'images/dot.svg',
                scaledSize: new vm.google.maps.Size(30, 30), // scaled size
                origin: new vm.google.maps.Point(0, 0), // origin
                anchor: new vm.google.maps.Point(15, 15) // anchor
              },
              title: vm.$t('map.yourLocation')
            });
            var locationInfoWindow = new vm.google.maps.InfoWindow({
              content: vm.$t('map.yourLocation')
            });
            locationInfoWindow.setZIndex(401);
            locationMarker.addListener('mouseover', function() {
              locationInfoWindow.open(vm.map, locationMarker);
              if (document.getElementsByClassName('gm-style-iw')[0]) {
                document
                  .getElementsByClassName('gm-style-iw')[0]
                  .getElementsByClassName('gm-ui-hover-effect')[0].style.display = 'none';
              }
            });
            locationMarker.addListener('mouseout', function() {
              locationInfoWindow.close();
            });
            locationMarker.setMap(vm.map);

            if (vm.initGeolocation) {
              vm.map.setCenter(vm.pos);
              vm.map.setZoom(16);
            }
          },
          function() {
            handleLocationError(true, infoWindow, vm.map.getCenter());
          }
        );
      } else {
        // Browser doesn't support Geolocation
        handleLocationError(false, infoWindow, vm.map.getCenter());
      }

      function handleLocationError(browserHasGeolocation, infoWindow, pos) {
        infoWindow.setPosition(pos);
        infoWindow.setContent(
          browserHasGeolocation
            ? 'Error: The Geolocation service failed.'
            : "Error: Your browser doesn't support geolocation."
        );
      }
    },
    addListeners() {
      const vm = this;
      this.map.addListener('click', function(e) {
        vm.$emit('update:click', e);
      });
      this.map.addListener('center_changed', function() {
        const newCenter = { lat: vm.map.getCenter().lat(), lng: vm.map.getCenter().lng() };
        vm.$emit('update:center', newCenter);
      });
      this.map.addListener('bounds_changed', function() {
        const originalBounds = vm.map.getBounds();
        vm.$emit('update:bounds', originalBounds);
      });

      this.map.addListener('zoom_changed', function() {
        const newZoom = vm.map.getZoom();
        vm.$emit('update:zoom', newZoom);
      });
      // this.map.addListener('dragend', function() {
      //   vm.$emit('drag', false);
      // });
      // this.map.addListener('dragstart', function() {
      //   vm.$emit('drag', true);
      // });
      this.map.addListener('idle', function() {
        vm.$emit('idle');
      });
    }
  },
  created() {
    this.$store.commit('COUNT_LOADING', 1);
  },
  beforeDestroy() {
    this.google.maps.event.clearInstanceListeners(this.map);
  }
};
</script>

<style scoped>
