  transition: all 0.25s ease !important;
  margin-top: 6px;
  border-radius: 4px;
  cursor: pointer;
  padding: 0px 10px;
  text-align: center;
  background: rgba(255, 255, 255, 1);
  font-weight: 600;
  font-size: 14px;
  color: #484848;
  line-height: 38px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px -1px;
}
.google-map >>> .gm-control {
  transition: all 0.3s ease !important;
}
.google-map >>> .gm-control.hide {
  display: none;
}
.google-map >>> .gm-control.fade-out .search-control {
  background: rgba(255, 255, 255, 0.5);
}
.google-map >>> .gm-control.fade-out:hover .search-control {
  background: rgba(255, 255, 255, 1);
}
.google-map >>> .search-control:hover {
  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 8px -1px;
}
.google-map >>> .search-control i {
  transition: all 0.3s ease !important;
  font-weight: 800;
}
.google-map >>> .search-control:hover i {
  color: #00a69c;
}
.google-map {
  width: 100%;
  min-height: 100%;
}
.google-map >>> * {
  outline: none;
}
</style>
