<template>
  <el-popover placement="bottom" width="100" v-model="visible" popper-class="roomtype-popover">
    <div class="poper-option" @click="confirmHandler(itemSrc.row)">
      
    </div>
    <el-divider></el-divider>
    <el-popconfirm
      title=""
      confirmButtonText=""
      cancelButtonText=""
      cancelButtonType="Text"
      @onConfirm="cancelHandler"
    >
      <div class="poper-option" slot="reference">
        
      </div>
    </el-popconfirm>
    <el-divider></el-divider>
    <el-popconfirm
      title=""
      confirmButtonText=""
      cancelButtonText=""
      cancelButtonType="Text"
      @onConfirm="deleteHandler"
    >
      <div class="poper-option" slot="reference">
        
      </div>
    </el-popconfirm>
    <el-button
      type="text"
      slot="reference"
      size="midium"
      :loading="updating"
      :icon="itemSrc.disable ? 'el-icon-loading' : 'el-icon-more'"
    >
    </el-button>
  </el-popover>
</template>

<script>
export default {
  props: {
    itemSrc: Object,
    title: String,
    updating: Boolean
  },
  data() {
    return {
      visible: false,
      disable: false
    };
  },
  methods: {
    cancelHandler() {
      this.visible = false;
      this.$emit('cancelItem', this.itemSrc.row);
    },
    confirmHandler(item) {
      this.visible = false;
      this.$emit('confirmItem', item);
    },
    deleteHandler() {
      this.visible = false;
      this.$emit('deleteItem', this.itemSrc.row);
    }
  }
};
</script>

<style scoped>
.poper-option {
  text-align: center;
  cursor: pointer;
  padding: 8px;
  font-size: 14px;
  transition: 0.25s ease;
  color: #767676 !important;
}
.poper-option:hover {
  background-color: #f5f5f5;
}
.el-divider {
  margin: 0;
  background: #f2f2f2;
}
</style>
